import React from 'react';
import { Link } from 'react-router-dom'
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Tooltip,
  IconButton,
  Grid,
  Typography,
  Container,
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ListIcon from '@material-ui/icons/List';
import AccountBalanceWalletIcon from '@material-ui/icons/AccountBalanceWallet';
import { getContractDashboard } from '../utils/api';
import { HasPerm, SimpleCard } from '../../components';

const styles = (theme) => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
    ul: {
      margin: 0,
      padding: 0,
    },
    li: {
      listStyle: 'none',
    },
  },
  flex: {
    flex: 1,
  },
  heroContent: {
    padding: theme.spacing(8, 0, 6),
  },
  footer: {
    borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('sm')]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
  },
});

class ContractDashboard extends React.Component {
  title = '契約管理'
  
  constructor(props) {
    super(props);

    this.state = {
      status: {
        member_count: 0,
        no_contract_count: 0,
        auto_updating_count: 0,
        auto_updated_count: 0,
        will_expire_count: 0,
        did_expired_count: 0,
      },
    };
    this.initialize();
  }

  initialize = () => {
    getContractDashboard().then(data => {
      this.setState({ status: data });
    });
  };

  componentDidMount = () => {
    document.title = this.title;
  };

  render () {
    const { classes } = this.props;
    const { status } = this.state;

    return (
      <div>
        <Container maxWidth="sm" component="main" className={classes.heroContent}>
          <Typography component="h2" variant="h4" align="center" color="textPrimary" gutterBottom>
            契約管理システム
          </Typography>
          <Typography variant="h5" align="center" color="textSecondary" component="p">
            正社員または契約社員の追加、情報変更、削除など、
            ＢＰと個人事業主の追加はここではできません。
          </Typography>
        </Container>
        <Container maxWidth="md" component="main">
          <Grid container spacing={5} alignItems="flex-end">
            <Grid item xs={12} sm={6} md={4}>
              <SimpleCard
                title='社員状態'
                subheader='正社員、契約社員、アルバイトと試用期間の社員一覧'
                data={{ count: status.member_count, message: '/人'}}
                secondaryList={[
                  {
                    count: (
                      <Link to={{pathname: '/contract/members/', search: 'has_no_contract=true'}}>
                        {status.no_contract_count}
                      </Link>
                    ),
                    message: '人の契約はありません。' 
                  },
                ]}
                actions={
                  <>
                    <HasPerm permission='contract.add_member'>
                      <Tooltip title="社員追加">
                        <IconButton
                          color="secondary" 
                          component={Link} 
                          aria-label="add" 
                          to='/contract/members/add'
                        >
                          <AddIcon />
                        </IconButton>
                      </Tooltip>
                    </HasPerm>
                    <div className={classes.flex}></div>
                    <HasPerm permission='contract.view_member'>
                      <Tooltip title="社員給料明細送信">
                        <IconButton
                          color="primary"
                          aria-label="list"
                          component={Link} 
                          to='/contract/members/salary/upload/'
                        >
                          <AccountBalanceWalletIcon />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="社員一覧表示">
                        <IconButton
                          color="primary"
                          aria-label="list"
                          component={Link} 
                          to='/contract/members?is_retired=false'
                        >
                          <ListIcon />
                        </IconButton>
                      </Tooltip>
                    </HasPerm>
                  </>
                }
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <SimpleCard
                title='自動更新状態'
                subheader='現在この一か月間で更新予定または更新済みの契約一覧'
                data={{ count: status.auto_updating_count, message: '/人'}}
                secondaryList={[
                  { count: status.auto_updated_count, message: '人契約更新済み。' },
                ]}
                actions={
                  <>
                    <div className={classes.flex}></div>
                    <HasPerm permission='contract.view_contract'>
                      <Tooltip title="自動更新一覧表示">
                        <IconButton
                          color="primary"
                          aria-label="list"
                          component={Link}
                          to='/contract/contracts/auto-update-list'
                        >
                          <ListIcon />
                        </IconButton>
                      </Tooltip>
                    </HasPerm>
                  </>
                }
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <SimpleCard
                title='契約終了状態'
                subheader='これから期限切れの契約一覧と今月期限切れの契約一覧。'
                data={{ count: status.will_expire_count, message: '/人'}}
                secondaryList={[
                  { count: status.did_expired_count, message: '人の契約は期限切れ。' },
                ]}
                actions={
                  <>
                    <div className={classes.flex}></div>
                    <HasPerm permission='contract.view_contract'>
                      <Tooltip title="契約終了一覧表示">
                        <IconButton
                          color="primary"
                          aria-label="list"
                          component={Link}
                          to='/contract/contracts/expire-list'
                        >
                          <ListIcon />
                        </IconButton>
                      </Tooltip>
                    </HasPerm>
                  </>
                }
              />
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default withStyles(styles)(ContractDashboard);
