import React from 'react';
import {
  withStyles,
  List,
  ListItem,
  Drawer,
  Divider,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Badge,
  Tooltip,
  Switch,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Toast from './Toast';
import { common, ajax, config, constant } from '../utils';

const styles = theme => ({
  title: {
    flexGrow: 1,
  },
  listRoot: {
    width: theme.spacing(45),
    '& table': {
      borderSpacing: 0,
      width: '100%',
    },
    '& table thead': {
      fontSize: 13,
    },
    '& table tbody': {
      fontSize: 11,
      lineHeight: '14px',
    },
    '& table tfoot td': {
      fontSize: 11,
      textAlign: 'right',
    },
  },
  badge: {
    cursor: 'pointer',
    '& span': {
      width: 35,
      fontSize: 11,
    },
  },
  noData: {
    margin: theme.spacing(2),
  },
});

class Notifications extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      read: false,  // 未読のみ表示
    };
  }

  handleOpen = () => {
    this.setState({open: true});
  };

  handleClose = () => {
    this.setState({open: false});
  };

  handleRead = () => {
    this.setState(state => ({ read: !state.read }));
  };

  toggleDrawer = () => {
    this.setState({
      open: !this.state.open
    });
  };

  toggleNotificationRead = (pk) => {
    const { changeNotification } = this.props;
    ajax.fetchPut(common.formatStr(config.api.account.notification_read, pk)).then((data) => {
      changeNotification(data);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { classes, notifications } = this.props;
    const { open, read } = this.state;
    const unread_count = notifications ? notifications.filter(i => i['is_read'] === false).length : 0;
    const results = notifications ? notifications.filter(i => i['is_read'] === read) : [];

    return (
      <Drawer anchor='right' open={open} onClose={this.handleClose}>
        <AppBar position="static" color="default">
          <Toolbar>
            <Typography variant="h6" className={classes.title}>
              {unread_count > 0 ? (
                <Badge badgeContent={unread_count} color="secondary">
                  通知一覧
                </Badge>
              ) : '通知一覧'}
            </Typography>
            <span style={{marginRight: 16}}>
              <Switch checked={!read} onChange={this.handleRead} />
            </span>
            <IconButton edge="start" color="inherit" onClick={this.handleClose}>
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <List className={classes.listRoot}>
          {results.length > 0 ? (
            <>
              {results.map((item, key) => (
                <React.Fragment key={key}>
                  <ListItem>
                    <table>
                      <thead>
                        <tr>
                          <th>{item.title}</th>
                          <td style={{width: 18}}>
                            {item.is_read ? (
                              <Tooltip title="未読にする" placement='bottom' enterDelay={300}>
                                <Badge
                                  badgeContent={'既読'}
                                  color="primary"
                                  className={classes.badge}
                                  onClick={() => this.toggleNotificationRead(item.id)}
                                />
                              </Tooltip>
                            ) : (
                              <Tooltip title="既読にする" placement='bottom' enterDelay={300}>
                                <Badge
                                  badgeContent={'未読'}
                                  color="secondary"
                                  className={classes.badge}
                                  onClick={() => this.toggleNotificationRead(item.id)}
                                />
                              </Tooltip>
                            )}
                          </td>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td colSpan={2}>{item.content}</td>
                        </tr>
                      </tbody>
                      <tfoot>
                        <tr>
                          <td colSpan={2}>{common.formatDate(item.created_dt, 'YYYY/MM/DD hh:mm')}</td>
                        </tr>
                      </tfoot>
                    </table>
                  </ListItem>
                  <Divider />
                </React.Fragment>
              ))}
            </>
          ) : (
            <Typography className={classes.noData}>{constant.INFO.NO_NOTIFICATIONS}</Typography>
          )}
        </List>
      </Drawer>
    )
  }
}

export default withStyles(styles)(Notifications);
