import React from 'react';
import ApexCharts from 'react-apexcharts'
import { withStyles } from '@material-ui/core';
import {
  getCompanyMonthlyTurnover,
  getClientsMonthlyTurnover,
} from '../utils/api';
import {
  company_monthly_turnover_option,
  clients_monthly_turnover_option,
} from './chart_options';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { Card, CardHeader, CardBody, withPermissions } from '../../components';

const styles = theme => ({
  charts: {
    '& .apexcharts-menu-item': {
      color: 'black',
    },
    '& .apexcharts-tooltip': {
      background: '#f3f3f3',
      color: 'black',
    }
  }
})

class TurnoverDashBoard extends React.Component {
  title = '売上管理'

  constructor(props){
    super(props);

    this.state = {
      company_turnover_monthly: null,
      clients_turnover_monthly: null,
    };
    this.initialize();
  }

  componentDidMount = () => {
    document.title = this.title;
  };

  initialize = () => {
    getCompanyMonthlyTurnover().then(data => {
      company_monthly_turnover_option.xaxis.categories = data.categories;
      this.setState({ company_turnover_monthly: data.series });
    });
    getClientsMonthlyTurnover().then(data => {
      clients_monthly_turnover_option.labels = data.categories;
      this.setState({ clients_turnover_monthly: data.series });
    });
  };

  render() {
    const { classes } = this.props;
    const { company_turnover_monthly, clients_turnover_monthly } = this.state;

    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={12} lg={6}>
            <Card chart>
              <CardHeader color="plain">
                {company_turnover_monthly ? (
                  <ApexCharts
                    className={classes.charts}
                    options={company_monthly_turnover_option}
                    series={company_turnover_monthly}
                    type="bar"
                    height={300}
                  />
                ) : null}
              </CardHeader>
              <CardBody>
                全社月別売上とコスト
              </CardBody>
            </Card>
          </GridItem>
          <GridItem xs={12} sm={12} md={12} lg={6}>
            <Card chart>
              <CardHeader color="plain">
                {clients_turnover_monthly ? (
                  <ApexCharts
                    className={classes.charts}
                    options={clients_monthly_turnover_option}
                    series={clients_turnover_monthly}
                    type="line"
                    height={300}
                  />
                ) : null}
              </CardHeader>
              <CardBody>
                取引先の月別売上（売上の一番多い５社）
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default withPermissions(withStyles(styles)(TurnoverDashBoard), 'turnover.view_turnover');
