import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import {
  TopBreadcrumbs,
  Card,
  CardHeader,
  CardBody,
  DataSource,
} from '../../components';
import { getOrganization } from '../utils/api';
import { config, common } from '../../utils';
import { org_member_list_schema } from './schema';

class OrgMemberList extends React.Component {
  title = 'メンバー一覧';

  constructor(props) {
    super(props);

    this.state = { 
      organization: {},
    };
    this.initialize(props);
　}

  initialize = (props) => {
    const { org_id } = props.match.params;
    getOrganization(org_id).then(organization => {
      this.setState({organization});
    });
  };

  componentDidUpdate = () => {
    const { organization } = this.state;
    document.title = `${this.title} | 部署：${organization.name}`;
  };

  render() {
    const { org_id } = this.props.match.params;
    const { organization } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/organization', name: '部署一覧' },
            { link: `/organization/${org_id}`, name: organization.name },
            { name: this.title },
          ]}
        />
        <Card>
          <CardHeader color='info'>{this.title}</CardHeader>
          <CardBody>
            <DataSource
              endpoint={common.formatStr(config.api.organization.members, org_id)}
              render={ (initData) => (
                <EnhancedTable
                  title='所属一覧'
                  storageKey='org.members'
                  tableHead={org_member_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default OrgMemberList;
