import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import { project_subcontractor_list_schema } from './schema';
import { config, common, ajax, constant } from '../../utils';
import { getProjectDetail } from '../utils/api';
import { withPermissions, Toast } from '../../components';

class SubcontractorList extends React.Component {
  title = '下請業者一覧'

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      reload: false,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id } = props.match.params;
    getProjectDetail(project_id).then(project => this.setState({project}));
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  onDeleteSubcontractor = (data) => {
    const { project_id } = this.props.match.params;
    return ajax.fetchDelete(common.formatStr(config.api.project.subcontractor_detail, project_id, data.id)).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      this.setState({reload: !this.state.reload});
    }).catch(errors => {
      Toast.error(errors.detail);
      Promise.reject(errors);
    });
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project, reload } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { link: `/project/projects/${project_id}`, name: project.name },
            { name: this.title },
          ]}
        />
        <Card>
          <CardHeader color="info">{this.title}</CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={common.formatStr(config.api.project.subcontractor_list, project_id)}
              render={(initData) => (
                <EnhancedTable
                  tableHead={project_subcontractor_list_schema}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '下請業者を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect(`/project/projects/${project_id}/subcontractors/add`)),
                      'visible': common.hasPerm('project.add_projectsubcontractor'),
                    },
                  ]}
                  deleteProps={{
                    handleDelete: this.onDeleteSubcontractor,
                    visible: common.hasPerm('project.delete_projectsubcontractor'),
                  }}
                  selectable='single'
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>

      </div>
    );
  }
}

export default withPermissions(SubcontractorList, 'project.view_projectsubcontractor');
