import { userConstants } from '../constants';
import { userService } from '../services';

export const getMe = () => {
  return dispatch => {
    dispatch(getMeRequest());

    userService.getMe()
      .then(
        data => dispatch(getMeSuccess(data)),
        error => dispatch(getMeFailure(error))
      );
  };
}

const getMeRequest = () => ({
  type: userConstants.GET_ME_REQUEST
});

const getMeSuccess = (data) => ({
  type: userConstants.GET_ME_SUCCESS,
  payload: data
});

const getMeFailure = (error) => ({
  type: userConstants.GET_ME_FAILURE,
  error
});

export const clearMe = () => ({
  type: userConstants.CLEAR_ME
});

/**
 * 通知を変更する。
 * @param {Object} notification 新しい通知
 */
export const addNotification = (notification) => ({
  type: userConstants.NOTIFICATION_ADD,
  notification,
});

/**
 * 通知を変更する。
 * @param {Object} notification 変更後の通知
 */
export const changeNotification = (notification) => ({
  type: userConstants.NOTIFICATION_CHANGE,
  notification,
});

/**
 * サーバーから取得した最新の通知一覧を表示
 * @param {Array} notifications 通知一覧
 */
export const reloadNotification = (notifications) => ({
  type: userConstants.NOTIFICATION_RELOAD,
  notifications,
});
