import React from 'react';
import { Card, CardHeader, CardContent, IconButton } from '@material-ui/core';
import PrintIcon from '@material-ui/icons/Print';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { common } from '../../utils/common';
import { getProjectDetail, getProjectRequestHtml } from '../utils/api';
import { withPermissions } from '../../components';

class ProjectRequest extends React.Component {
  title = '請求書';
  reportId = 'printProjectRequest';

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      project_request_html: null,
      status_code: null,
      error_message: null,
    };
    this.initialize(props.match.params);
  }

  initialize = (params) => {
    getProjectDetail(params.project_id).then(project => {this.setState({project});});
    getProjectRequestHtml(params.project_id, params.request_id).then(json => {
      this.setState({project_request_html: json.html});
    }).catch(errors => {
      if (errors.status_code === 403) {
        this.props.forbidden(errors.detail);
      }
    });
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name} | 案件一覧`;
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project, project_request_html } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/project/projects', name: '案件一覧'},
            {link: `/project/projects/${project_id}`, name: project.name },
            {name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <Card>
            <CardHeader
              title={this.title}
              action={
                <IconButton
                  aria-haspopup="true"
                  onClick={() => {common.printIframeContent(this.reportId)}}
                >
                  <PrintIcon />
                </IconButton>
              }
            />
            <CardContent>
              <iframe
                title={this.title}
                id={this.reportId}
                srcDoc={project_request_html}
                frameBorder='0'
                width='100%'
                height='1400px'
              >
              </iframe>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }
}

export default withPermissions(ProjectRequest, 'request.view_request');
