export const choice_years_from_prev = () => {
  const year = (new Date()).getFullYear();
  return [
    { "value": (year - 1) + '', "display_name": (year - 1) + '' },
    { "value": (year + 0) + '', "display_name": (year + 0) + '' },
    { "value": (year + 1) + '', "display_name": (year + 1) + '' },
    { "value": (year + 2) + '', "display_name": (year + 2) + '' },
  ];
};

