import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Typography } from '@material-ui/core';
import { constant } from '../utils/constants';

const useStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(2),
    paddingTop: 100,
    textAlign: 'center',
    color: theme.palette.text.secondary,
    minHeight: 200,
    marginBottom: theme.spacing(3),
  },
  statusCode: {
    textAlign: 'right',
    marginRight: theme.spacing(3),
  },
  errorMsg: {
    textAlign: 'left',
    marginLeft: theme.spacing(3),
  },
}));

export default function HttpStatus(props) {
  const classes = useStyles();

  const { statusCode, message } = props;

  return (
    <div className={classes.paper}>
      <Grid container>
        <Grid item xs={6} xm={6}>
          <Typography className={classes.statusCode} variant="h2" color="error">
            {statusCode}
          </Typography>
        </Grid>
        <Grid item xs={6} xm={6} style={{borderLeft: '1px solid lightgray'}}>
          <div className={classes.errorMsg}>
            <Typography variant="subtitle1">Sorry!</Typography>
            <Typography variant="body1">{message}</Typography>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export const HttpNotFound = () => (
  <HttpStatus
    statusCode={404}
    message={constant.ERROR.HTTP_NOT_FOUND}
  />
);
