import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  member_contract_form_schema,
  contract_calculate_hours_schema,
  contract_allowance_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
  default_contract_data,
} from './schema';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import {
  getPartnerCompany,
  getPartnerMember,
  getPartnerMemberContractTotal,
  getIndividualMember,
} from '../utils/api';
import { onChangeCalculateType, onChangeMinusAndPlus } from '../utils/common';
import { withPermissions } from '../../components';
import { choice_contract_type_individual, choice_contract_type_bp } from '../../utils';

class PartnerContractForm extends React.Component {
  title = 'ＢＰ契約変更';
  member_contract_form_schema = common.clone(member_contract_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      company: {},
      member: {},
      contract_total: default_contract_data,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id, member_id, contract_id } = props.match.params;
    if (company_id) {
      getPartnerCompany(company_id).then(company => {
        this.setState({company});
      });
      getPartnerMember(company_id, member_id).then(member => {
        this.setState({member});
      });
      this.detail_api = `/partner/companies/${company_id}/members/${member_id}`
    } else {
      getIndividualMember(member_id).then(member => {
        this.setState({member});
      });
      this.detail_api = `/partner/individual/${member_id}`
    }
    if (contract_id) {
      getPartnerMemberContractTotal(member_id, contract_id).then(
        contract_total => this.setState({contract_total})
      ).catch(errors => {
        if (errors.status_code === 403) {
          this.props.forbidden(errors.detail);
        }
      });
    }
  };

  componentDidUpdate = () => {
    const { contract_id } = this.props.match.params;
    const { company, member } = this.state;
    this.title = contract_id ? '契約変更' : '契約追加'
    document.title = `${this.title} | ${member.full_name || ''} | ${company.name || '個人事業主'}`;
  };

  render() {
    const { company_id, member_id, contract_id } = this.props.match.params;
    const { company, member, contract_total } = this.state;
    contract_total['company_content_type'] = member.company_content_type || member.content_type;
    contract_total['company_object_id'] = company_id || member_id;
    contract_total['content_type'] = member.content_type;
    contract_total['object_id'] = member_id;
    const breadcrumbs = [];
    if (company_id) {
      breadcrumbs.push({ link: '/partner/companies?is_retired=false', name: '協力会社一覧' });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/`, name: company.name });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members`, name: '社員一覧' });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members/${member_id}`, name: member.full_name });
      if (contract_id) {
        breadcrumbs.push({
          link: `/partner/companies/${company_id}/members/${member_id}/contracts/${contract_total.id}/detail`,
          name: contract_total.contract_no,
        });
      }
      common.getFromJsonList(this.member_contract_form_schema, 'name', 'contract_type')['choices'] = choice_contract_type_bp;
    } else {
      // 個人事業主の場合
      breadcrumbs.push({ link: '/partner/individual', name: '個人事業主一覧' });
      breadcrumbs.push({ link: `/partner/individual/${member_id}/`, name: member.full_name });
      if (contract_id) {
        breadcrumbs.push({
          link: `/partner/individual/${member_id}/contracts/${contract_total.id}/detail`,
          name: contract_total.contract_no,
        });
      }
      common.getFromJsonList(this.member_contract_form_schema, 'name', 'contract_type')['choices'] = choice_contract_type_individual;
    }
    if (contract_id) {
      breadcrumbs.push({ name: "変更" });
    } else {
      breadcrumbs.push({ name: "契約追加" });
    }

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={this.member_contract_form_schema}
                onChanges={[onChangeCalculateType, onChangeMinusAndPlus]}
                inlines={[
                  {
                    name: "calculate_hours",
                    title: "契約計算用時間",
                    schema: contract_calculate_hours_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: 'allowances',
                    title: '精算条件',
                    schema: contract_allowance_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: "contract_items",
                    title: "契約条件",
                    schema: bp_contract_item_schema,
                    layout: bp_contract_item_layout,
                    allowAdd: false,
                    allowDelete: false,
                  },
                ]}
                data={contract_total}
                methodName={'POST'}
                saveEndpoint={
                  contract_id ? common.formatStr(
                    config.api.partner.member_contract_save, member_id, contract_total.id
                  ) : common.formatStr(
                    config.api.partner.member_contract_add, member_id
                  )
                }
                saveCallback={() => common.redirect(this.detail_api)}
                deleteEndpoint={contract_id ? common.formatStr(config.api.partner.member_contract, member_id, contract_id) : null}
                deleteCallback={() => common.redirect(this.detail_api)}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerContractForm, 'partner.change_partnermember');
