export const member_list_search_layout = [
  ['name', 'code'],
  'company_name',
  ['is_working', 'is_on_sales'],
  ['salesperson_id', 'member_type'],
  'is_retired',
];

export const member_detail_schema = [
  {
    "name": "code",
    "type": "string",
    "label": "社員番号",
  },
  {
    "name": "company_name",
    "type": "choice",
    "label": "所属会社",
    "link": (data) => {
      if (data.member_type === '02') {
        // 他社技術者の場合
        return `/partner/companies/${data.company}/`;
      } else {
        return null;
      }
    },
  },
  {
    "name": "full_name",
    "type": "string",
    "label": "名前",
  },
  {
    "name": "full_kana",
    "type": "string",
    "label": "フリガナ",
  },
  {
    "name": "gender",
    "type": "choice",
    "label": "性別",
    "choices": [
      {
        "value": "1",
        "display_name": "男",
      },
      {
        "value": "2",
        "display_name": "女",
      }
    ],
  },
  {
    "name": "email",
    "type": "string",
    "label": "会社メールアドレス",
  },
  {
    "name": "post_code",
    "type": "string",
    "label": "郵便番号",
  },
  {
    "name": "address",
    "type": "string",
    "label": "住所",
  },
  {
    "name": "nearest_station",
    "type": "string",
    "label": "最寄駅",
  },
  {
    "name": "phone",
    "type": "string",
    "label": "携帯番号",
  },
  {
    "name": "marriage",
    "type": "choice",
    "label": "婚姻状況",
    "choices": [
      {
        "value": "0",
        "display_name": "未婚",
      },
      {
        "value": "1",
        "display_name": "既婚",
      }
    ],
  },
  {
    "name": "is_on_sales",
    "type": "boolean",
    "label": "営業対象",
  },
  {
    "name": "comment",
    "type": "text",
    "label": "備考",
  },
];

