export const list_salesperson_schema = [
  {
    "name": "full_name",
    "type": "string",
    "label": "名前",
    "sortable": true,
  },
  {
    "name": "member_count",
    "type": "integer",
    "label": "担当数",
    "link": "/member/members/?is_retired=false&salesperson_id=%(id)s"
  },
  {
    "name": "working_count",
    "type": "integer",
    "label": "稼働数",
    "link": "/member/members/?is_retired=false&salesperson_id=%(id)s&is_working=true"
  },
  {
    "name": "waiting_count",
    "type": "integer",
    "label": "待機数",
    "link": "/member/members/?is_retired=false&salesperson_id=%(id)s&is_working=false&is_on_sales=true"
  },
  {
    "name": "curr_release_count",
    "type": "integer",
    "label": "今月リリース数",
    "link": "/member/releases/%(curr_year)s/%(curr_month)s/?salesperson=%(id)s"
  },
  {
    "name": "next_release_count",
    "type": "integer",
    "label": "来月リリース数",
    "link": "/member/releases/%(next_year)s/%(next_month)s/?salesperson=%(id)s"
  },
  {
    "name": "next_2_release_count",
    "type": "integer",
    "label": "再来月リリース数",
    "link": "/member/releases/%(next_2_year)s/%(next_2_month)s/?salesperson=%(id)s"
  },
];

