import React from 'react';
import CommonMemberAdd from './common_member_add';
import { common, config } from '../../utils';
import { withPermissions } from '../../components';

class IndividualMemberAdd extends React.Component {
  title = '社員追加';

  componentDidMount = () => {
    document.title = `${this.title} | 個人事業主`;
  };

  render() {
    return (
      <div>
        <CommonMemberAdd
          breadcrumbs={[
            { 'link': '/partner/individual', 'name': '個人事業主一覧' },
            { 'name': this.title },
          ]}
          endpoint={config.api.partner.individual_member_add}
          callback={(data) => common.redirect(`/partner/individual/${data.id}/`)}
        />
      </div>
    );
  }
}

export default withPermissions(IndividualMemberAdd, 'partner.add_partnermember');