import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import withStyles from "@material-ui/core/styles/withStyles";
import AccessibilityIcon from "@material-ui/icons/Accessibility";
import PeopleIcon from "@material-ui/icons/People";
import WarningIcon from "@material-ui/icons/Warning";
import InfoIcon from "@material-ui/icons/Info";
import InfoOutlinedIcon from "@material-ui/icons/InfoOutlined";
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  Card,
  CardIcon,
  CardHeader,
  CardFooter,
  withPermissions,
} from "../../components";
import dashboardStyle from "../../assets/jss/views/dashboardStyle.jsx";
import Danger from "../../components/Typography/Danger";
import Warning from "../../components/Typography/Warning";
import { common } from '../../utils/common';
import { getMemberDashboard } from '../utils/api';
import Info from '../../components/Typography/Info';

class MemberDashboard extends Component {
  title = 'メンバー管理'

  constructor(props) {
    super(props);

    this.state = {
      dashboard: {},
    };
    this.initialize();
  }

  initialize = () => {
    getMemberDashboard().then(dashboard => this.setState({dashboard}));
  };

  componentDidMount = () => {
    document.title = this.title;
  };

  render() {
    const { classes } = this.props;
    const { dashboard } = this.state;

    return(
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={6} lg={4}>
            <Card>
              <CardHeader color="danger" stats icon>
                <CardIcon color="danger">
                  <PeopleIcon/>
                </CardIcon>
                <p className={classes.cardCategory}>現在待機中人数</p>
                <h3 className={classes.cardTitle}>
                  {!common.isEmpty(dashboard) ? (
                    <React.Fragment>
                      <Link to={{pathname: '/member/members/', search: 'is_working=false&is_retired=false&is_on_sales=true'}}>
                        {dashboard.total_status.waiting_count}
                      </Link> / 
                      <Link to={{pathname: '/member/members/', search: 'is_retired=false&is_on_sales=true'}}>
                        {dashboard.total_status.member_count}
                      </Link>&nbsp;
                      <small>人</small>
                    </React.Fragment>
                  ) : null}
                </h3>
              </CardHeader>
              <CardFooter stats>
                <div className={classes.stats}>
                  <Danger>
                    <WarningIcon/>
                  </Danger>
                  {!common.isEmpty(dashboard) ? (
                    <React.Fragment>
                      現在の待機率は&nbsp;{dashboard.total_status.waiting_rate}&nbsp;％
                    </React.Fragment>
                  ) : null}
                </div>
              </CardFooter>
            </Card>
          </GridItem>
          <GridItem xs={12} sm={12} md={6} lg={4}>
            <Card>
              <CardHeader color="warning" stats icon>
                <CardIcon color="warning">
                  <AccessibilityIcon/>
                </CardIcon>
                <p className={classes.cardCategory}>今月 / 来月 / 再来月</p>
                <h3 className={classes.cardTitle}>
                  {!common.isEmpty(dashboard) ? (
                    <React.Fragment>
                      <Link to={'/member/releases'}>
                        {dashboard.release_status.curr_month_cnt}
                      </Link> / 
                      <Link to={`/member/releases/${dashboard.next_year}/${dashboard.next_month}/`}>
                        {dashboard.release_status.next_month_cnt}
                      </Link> /
                      <Link to={`/member/releases/${dashboard.next_2_year}/${dashboard.next_2_month}/`}>
                        {dashboard.release_status.next_2_month_cnt}&nbsp;
                      </Link>
                      <small>人</small>
                    </React.Fragment>
                  ) : null}
                </h3>
              </CardHeader>
              <CardFooter stats>
                <div className={classes.stats}>
                  <Warning>
                    <WarningIcon/>
                  </Warning>
                  今月、来月と再来月のリリース人数
                </div>
              </CardFooter>
            </Card>
          </GridItem>
          <GridItem xs={12} sm={12} md={6} lg={4}>
            <Card>
              <CardHeader color="info" stats icon>
                <CardIcon color="info">
                  <InfoOutlinedIcon/>
                </CardIcon>
                <p className={classes.cardCategory}>社員 / ＢＰ / 個人事業主</p>
                <h3 className={classes.cardTitle}>
                  {!common.isEmpty(dashboard) ? (
                    <React.Fragment>
                      <Link to={{pathname: '/member/members/', search: 'is_retired=false&member_type=01'}}>
                        {dashboard.member_count_status.employee_count}
                      </Link> / 
                      <Link to={{pathname: '/member/members/', search: 'is_retired=false&member_type=02'}}>
                        {dashboard.member_count_status.bp_member_count}
                      </Link> / 
                      <Link to={{pathname: '/member/members/', search: 'is_retired=false&member_type=03'}}>
                        {dashboard.member_count_status.individual_count}
                      </Link>&nbsp;
                      <small>人</small>
                    </React.Fragment>
                  ) : null}
                </h3>
              </CardHeader>
              <CardFooter stats>
                <div className={classes.stats}>
                  <Info>
                    <InfoIcon/>
                  </Info>
                  {!common.isEmpty(dashboard) ? (
                    <React.Fragment>
                      ＢＰ割合&nbsp;{dashboard.member_count_status.bp_member_rate}&nbsp;％、
                      個人事業主割合&nbsp;{dashboard.member_count_status.individual_rate}&nbsp;％
                    </React.Fragment>
                  ) : null}
                </div>
              </CardFooter>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default withPermissions(
  withStyles(dashboardStyle)(MemberDashboard),
  'member.view_dashboard'
);
