import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { getClientCompany } from '../utils/api';
import { TableDetail, EnhancedTable } from '../../datatable';
import { company_form_schema, company_form_layout } from '../partner/schema';
import { config } from '../../utils/config';
import { ajax, common } from '../../utils/common';
import { constant } from '../../utils/constants';
import {
  withPermissions,
  DataSource,
  HasPerm,
  Toast,
} from '../../components';
import { Paper } from '@material-ui/core';
import { client_project_list_schema } from './schema';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ClientCompanyDetail extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      company: {},
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.client.detail,
      props.match.params.company_id
    );
  }

  initialize = (props) => {
    const { company_id } = props.match.params;
    getClientCompany(company_id).then(company => {
      this.setState({company});
    });
  };

  componentDidUpdate = () => {
    document.title = `${this.state.company.name || ''} | 取引先企業`;
  };

  onEditCompany = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(company => {
      this.setState({company});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteCompany = (data) => {
    ajax.fetchDelete(this.detail_api).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect('/client/companies')
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { company_id } = this.props.match.params;
    const { company } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/client/companies', name: '取引先企業一覧' },
            { name: company.name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={company}
                schema={company_form_schema}
                editProps={{
                  title: '取引先企業情報変更',
                  schema: company_form_schema,
                  layout: company_form_layout,
                  handleEdit: this.onEditCompany,
                  visible: common.hasPerm('client.change_clientcompany'),
                }}
                deleteProps={{
                  handleDelete: this.onDeleteCompany,
                  visible: common.hasPerm('client.delete_clientcompany'),
                }}
              />
            </GridItem>
          </GridContainer>
          <HasPerm permission='project.view_project'>
            <Paper style={paperSpacing}>
              <DataSource
                endpoint={ common.formatStr(config.api.client.project_list, company_id) }
                render={ (initData) => (
                  <EnhancedTable
                    title={`${company.name}の案件一覧`}
                    showTitle={true}
                    tableHead={client_project_list_schema}
                    tableData={initData}
                    rowsPerPage={config.rowsPerPage}
                    pushpinTop={common.getHeaderHeight()}
                  />
                ) } 
              />
            </Paper>
          </HasPerm>
        </div>
      </div>
    );
  }
}

export default withPermissions(ClientCompanyDetail, 'client.view_clientcompany');
