import React from 'react';
import { Paper } from '@material-ui/core';
import {
  untreated_waiting_members_schema,
  untreated_release_members_schema,
} from './schema';
import { EnhancedTable } from '../../datatable';
import { DataSource } from '../../components';
import { config, common } from '../../utils';
import { useStyles } from './style';

function WorkingStatusTab(props) {
  const { member_id } = props.match.params;
  const classes = useStyles();

  return (
    <div className={classes.tabWrapper}>
      <Paper className={classes.tabPaper}>
        <DataSource
          endpoint={ common.formatStr(config.api.member.untreated_waiting_members, member_id) }
          render={ (initData) => (
            <EnhancedTable
              title='待機メンバー一覧'
              tableHead={untreated_waiting_members_schema}
              tableData={initData.results}
              rowsPerPage={config.rowsPerPage}
              pushpinTop={common.getHeaderHeight()}
              allowCsv={true}
              showTitle={true}
            />
          ) }
        />
      </Paper>
      <Paper className={classes.tabPaper}>
        <DataSource
          endpoint={ common.formatStr(config.api.member.untreated_release_members, member_id) }
          render={ (initData) => (
            <EnhancedTable
              title='リリース予定一覧'
              tableHead={untreated_release_members_schema}
              tableData={initData.results}
              rowsPerPage={config.rowsPerPage}
              pushpinTop={common.getHeaderHeight()}
              allowCsv={true}
              showTitle={true}
            />
          ) }
        />
      </Paper>
    </div>
  );
};

export default WorkingStatusTab;
