import React, { Component } from "react";
import PropTypes from "prop-types";
import { ajax } from '../utils/common';
import HttpStatus from "./HttpStatus";
import Waiting from "./Waiting";

class DataSource extends Component {
  _isMounted = false;

  constructor(props) {
    super(props);

    this.state = {
      data: {},
      loaded: false,
      status_code: null,
      error_message: null,
      placeholder: "Loading...",
    };
  }

  UNSAFE_componentWillMount() {
    this._isMounted = true;
    if (this.props.endpoint) {
      ajax.fetchGet(this.props.endpoint)
        .then(data => {
          if (this._isMounted) {
            this.setState({
              data,
              loaded: true,
              status_code: 200,
              error_message: null,
            })
          }
        }).catch(errors => {
          this.setState({
            status_code: errors.status_code,
            error_message: errors.detail,
            loaded: false,
          });
        });
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.reload !== null && this.props.reload !== undefined) {
      if (this.props.reload !== nextProps.reload) {
        ajax.fetchGet(nextProps.endpoint).then(data => {
          this.setState({data, loaded: true, status_code: 200});
        }).catch(errors => {
          this.setState({
            status_code: errors.status_code,
            error_message: errors.detail,
            loaded: false,
          });
        });
      }
    }
  }

  render() {
    const { data, loaded, status_code, error_message, placeholder } = this.state;
    
    if (loaded) {
      return this.props.render(data);
    } else if (status_code !== null && status_code !== undefined && status_code !== 200) {
      return (
        <HttpStatus
          statusCode={status_code}
          message={error_message}
        />
      );
    } else {
      return (
        <Waiting
          placeholder={placeholder}
        />
      );
    }
  }
}

DataSource.propTypes = {
  endpoint: PropTypes.string.isRequired,
  render: PropTypes.func.isRequired,
  params: PropTypes.object,
  reload: PropTypes.bool,
};

DataSource.defaultProps = {
  params: {},
  reload: false,
}

export default DataSource;
