import React from 'react';
import { config, common } from '../../utils';
import DataSource from '../../components/DataSource';
import { EnhancedTable } from '../../datatable';
import { monthly_orders_list_schema } from './schema';
import { withPermissions } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class PartnerMonthlyOrders extends React.Component {
  title = 'BP註文書一覧';

  componentDidMount = () => {
    document.title = this.title;
  }
  
  render() {
    return (
      <Paper style={paperSpacing}>
        <DataSource
          endpoint={config.api.partner.monthly_orders}
          render={(initData) => (
            <EnhancedTable
              title={this.title}
              showTitle={true}
              tableHead={monthly_orders_list_schema}
              tableData={initData}
              rowsPerPage={config.rowsPerPage}
              pushpinTop={common.getHeaderHeight()}
              tableStyles={{ minWidth: 500 }}
            />
          )}
        />
      </Paper>
    );
  }
}

export default withPermissions(PartnerMonthlyOrders, 'partner.view_partnerorder');
