import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import DataSource from '../../components/DataSource';
import { expense_by_month_list_schema } from './schema';
import { common } from '../../utils';
import { config } from '../../utils/config';
import { withPermissions, TopBreadcrumbs } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ExpenseByMonthList extends React.Component {
  title = '立替金一覧';

  componentDidMount = () => {
    const { year, month } = this.props.match.params;
    document.title = `${year}年${month}月 | ${this.title}`;
  };

  render () {
    const { year, month } = this.props.match.params;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/member/expenses', name: '立替金一覧' },
            { name: `${year}年${month}月の${this.title}` },
          ]}
        />
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={ common.formatStr(config.api.attendance.expense_by_month, year, month) }
            render={ (initData) => (
              <EnhancedTable
                title={`${year}年${month}月の${this.title}`}
                showTitle={true}
                tableHead={expense_by_month_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                pushpinTop={common.getHeaderHeight()}
                allowCsv={true}
                urlReflect={false}
                tableStyles={{ minWidth: 500 }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(ExpenseByMonthList, 'attendance.view_expense');
