import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import DataSource from '../../components/DataSource';
import { project_blanket_contract_list_schema } from './schema';
import { config, common } from '../../utils';
import { getProjectDetail } from '../utils/api';
import { withPermissions } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ProjectContractList extends React.Component {
  title = '一括契約一覧'

  constructor(props) {
    super(props);

    this.state = {
      project: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    getProjectDetail(props.match.params.project_id).then(project => {
      this.setState({project});
    });
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { link: `/project/projects/${project_id}`, name: project.name },
            { name: this.title },
          ]}
        />
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={common.formatStr(config.api.project.blanket_contracts, project_id)}
            render={(initData) => (
              <EnhancedTable
                title={`${project.name}の${this.title}`}
                showTitle={true}
                tableHead={project_blanket_contract_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                tableActions={[
                  {
                    'tooltip': '一括契約を追加する',
                    'icon': <AddIcon color="secondary"/>,
                    'handleClick': () => (common.redirect(`/project/projects/${project_id}/contracts/add`)),
                  },
                ]}
                pushpinTop={common.getHeaderHeight()}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(ProjectContractList, 'project.view_projectcontract');
