import React from 'react';
import { Paper, Dialog, DialogTitle, DialogContent } from '@material-ui/core';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import FindInPageIcon from '@material-ui/icons/FindInPage';
import { EnhancedTable, FormDialog } from '../../datatable';
import { TopBreadcrumbs, Toast } from '../../components';
import {
  member_salary_preview_list_schema,
  import_salary_csv_form_schema,
} from './schema';
import { config, common, constant } from '../../utils';
import { getSalaryCSVPreview, previewSalaryStatement } from '../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class SalaryUpload extends React.Component {
  title = '社員給料明細送信';
  reportId = 'salary_statment';

  constructor(props) {
    super(props);
    this.state = {
      openPreview: false,
      salary_list: [],
      salary_statment_html: null,
    };
  }

  handleOpenPreview = () => {
    this.setState({ openPreview: true });
  }

  handleClosePreview = () => {
    this.setState({ openPreview: false });
  }

  showImportSalaryCSV = () => {
    if (this._showImportDialog) {
      this._showImportDialog();
    }
  };

  previewSalaryCSV = (data) => {
    return getSalaryCSVPreview(data).then((csv) => (
      this.setState({ salary_list: csv })
    )).catch(errors => (
      Toast.error(errors.detail)
    ));
  };

  handlePreviewSalaryStatement = (data) => {
    if (Array.isArray(data) && data.length > 1) {
      Toast.error(constant.ERROR.SELECT_SINGLE);
    } else {
      previewSalaryStatement(data).then(json => {
        this.setState({ salary_statment_html: json.html });
        this.handleOpenPreview();
      }).catch(errors => (
        Toast.error(errors.detail)
      ))
    }
  }

  render() {
    const { openPreview, salary_list, salary_statment_html } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/contract', name: '契約管理'},
            { name: this.title },
          ]}
        />
        <Paper style={paperSpacing}>
          <EnhancedTable
            title={this.title}
            showTitle={true}
            tableHead={member_salary_preview_list_schema}
            tableData={salary_list}
            rowsPerPage={config.rowsPerPage}
            tableActions={[
              {
                'tooltip': '社員給料明細をアップロード',
                'icon': <CloudUploadIcon />,
                'handleClick': this.showImportSalaryCSV,
              },
            ]}
            rowActions={[
              {
                'tooltip': '社員給料明細をプレビュー',
                'icon': <FindInPageIcon />,
                'handleClick': this.handlePreviewSalaryStatement,
              },
            ]}
            tableStyles={{minWidth: 2440}}
            pushpinTop={common.getHeaderHeight()}
            selectable='multiple'
          />
        </Paper>
        <FormDialog
          title='社員給料明細をアップロード'
          schema={import_salary_csv_form_schema}
          handleOk={this.previewSalaryCSV}
          innerRef={(dlg) => { this._showImportDialog = dlg && dlg.handleOpen }}
        />
        <Dialog
          open={openPreview}
          onClose={this.handleClosePreview}
          scroll='body'
          maxWidth={false}
        >
          <DialogTitle>給与明細書</DialogTitle>
          <DialogContent>
            <iframe
              title='給与明細書'
              id={this.reportId}
              srcDoc={salary_statment_html}
              frameBorder='0'
              width='960px'
              height='1400px'
            >
            </iframe>
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

export default SalaryUpload;
