import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import { project_member_list_schema } from './schema';
import { config, common } from '../../utils';
import { getProjectDetail } from '../utils/api';
import { withPermissions } from '../../components';

class ProjectMemberList extends React.Component {
  title = 'メンバー一覧'

  constructor(props) {
    super(props);

    this.state = {
      project: {},
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.project.detail,
      props.match.params.project_id,
    );
  }

  initialize = (props) => {
    getProjectDetail(props.match.params.project_id).then(project => {
      this.setState({project});
    });
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { link: `/project/projects/${project_id}`, name: project.name },
            { name: this.title },
          ]}
        />
        <Card>
          <CardHeader color="info">{this.title}</CardHeader>
          <CardBody>
            <DataSource
              endpoint={common.formatStr(config.api.project.member_portal_list, project_id)}
              render={(initData) => (
                <EnhancedTable
                  storageKey='project.members'
                  tableHead={project_member_list_schema}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '要員を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect(`/project/projects/${project_id}/members/add`)),
                    },
                  ]}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>

      </div>
    );
  }
}

export default withPermissions(ProjectMemberList, 'project.view_projectmember');
