import React from 'react';
import withStyles from "@material-ui/core/styles/withStyles";
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { organization_form_schema } from './schema';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { getOrganizationChoices, getOrganization } from '../utils/api';

const styles = (theme) => ({
  paper: {
    marginTop: theme.spacing(2),
  },
});

class OrganizationForm extends React.Component {

  constructor(props) {
    super(props);

    this.state = { 
      organization: null,
      organizations: [],
    };
    this.initialize(props.match.params);
　}

  initialize = (params) => {
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    if (params.org_id) {
      getOrganization(params.org_id).then(organization => {
        this.setState({organization});
      });
    }
  };

  render() {
    const { classes, match } = this.props;
    const { organizations, organization } = this.state;
    const title = organization ? '部署変更' : '部署追加';
    const methodName = organization ? 'PUT' : 'POST';

    // 所属部署の設定
    let colOrg = common.getFromJsonList(organization_form_schema, 'name', 'parent');
    colOrg['choices'] = organizations;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/organization', name: '部署一覧' },
            { name: title },
          ]}
        />
        <div className={classes.paper}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={title}
                schema={organization_form_schema}
                data={organization}
                methodName={methodName}
                saveEndpoint={
                  organization
                    ? common.formatStr(config.api.organization.detail, match.params.org_id)
                    : config.api.organization.add
                }
                saveCallback={(data) => common.redirect(`/organization/${data.id}/`)}
                deleteEndpoint={
                  organization && common.hasPerm('org.delete_organization')
                    ? common.formatStr(config.api.organization.detail, match.params.org_id)
                    : null
                }
                deleteCallback={() => common.redirect('/organization')}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
};

export default withStyles(styles)(OrganizationForm);
