import React from 'react';
import { Paper } from '@material-ui/core';
import {
  DataSource,
  withPermissions,
} from '../../components';
import { EnhancedTable } from '../../datatable';
import { config, common } from '../../utils';
import {
  paid_vacation_list_schema,
  paid_vacation_edit_schema,
  paid_vacation_edit_layout,
} from './schema';
import { handleEditObject } from '../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class PaidVacationList extends React.Component {
  title = '有給休暇一覧';

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
    };
  }

  render() {
    const { reload } = this.state;

    return (
      <div>
        <Paper style={paperSpacing}>
        <DataSource
            reload={reload}
            endpoint={ config.api.contract.paid_vacation_list }
            render={ (initData) => (
              <EnhancedTable
                title={this.title}
                showTitle={true}
                storageKey='member_commutation'
                tableHead={paid_vacation_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                allowCsv={true}
                pushpinTop={common.getHeaderHeight()}
                selectable='single'
                editProps={{
                  "schema": paid_vacation_edit_schema,
                  "layout": paid_vacation_edit_layout,
                  "handleOk": handleEditObject(
                    () => this.setState({reload: !reload}),
                    (data) => common.formatStr(config.api.contract.paid_vacation_detail, data.id),
                  ),
                  "visible": common.hasPerm('member.edit_paidvacation'),
                }}
              />
            )}
          />
        </Paper>
      </div>
    )
  }
}

export default withPermissions(PaidVacationList, 'member.view_paidvacation');
