import React from 'react';
import PropTypes from "prop-types";
import { StepForm, TopBreadcrumbs, withPermissions } from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import DuplicatedMemberDialog from '../common/duplicated_member_dialog';
import {
  member_form_basic_schema,
  member_form_basic_layout,
  member_contract_form_schema,
  contract_allowance_schema,
  contract_calculate_hours_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
  default_contract_data,
} from './schema';
import {
  member_add_org_schema,
  member_code_form_schema,
} from '../contract/schema';
import {
  getOrganizationChoices,
  getPartnerCompanyChoices,
  getPartnerMemberChoice,
  getNextPartnerMemberCode,
  getNextSelfEmployeeCode,
  checkMemberDuplications,
} from '../utils/api';
import { common } from '../../utils/common';
import {
  choice_contract_type_individual,
  checkDateContradict,
  choice_contract_type_bp,
} from '../../utils';
import {
  onChangeCalculateType,
  onBlurPostcode,
  onChangeMinusAndPlus,
} from '../utils';

class CommonMemberAdd extends React.Component {
  title = '社員追加';
  member_form_basic_schema = common.clone(member_form_basic_schema);
  member_contract_form_schema = common.clone(member_contract_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      organizations: [],
      partnerCompaies: [],
      members_choice: [],
      duplicated_member_data: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id } = props;
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    if (company_id) {
      getPartnerCompanyChoices().then(partnerCompaies => {
        this.setState({partnerCompaies});
      });
      getPartnerMemberChoice(company_id).then(members_choice => this.setState({members_choice}));
    }
  };

  onBlurMemberDuplicate = (name, data) => {
    if (['first_name', 'last_name'].indexOf(name) >= 0) {
      const { first_name, last_name } = data;
      if (first_name && last_name) {
        checkMemberDuplications(first_name, last_name).then(json => {
          if (this._openDuplicatedDlg && json.length > 0) {
            this._openDuplicatedDlg(`${last_name} ${first_name}`, json);
          }
        });
      }
    }
  };

  onDuplicatedMemberSelect = (data) => {
    this.setState({duplicated_member_data: data});
  };

  getSteps = () => {
    const { company_id } = this.props;
    const { organizations, partnerCompaies, members_choice, duplicated_member_data } = this.state;

    const colCompany = common.getFromJsonList(this.member_form_basic_schema, 'name', "company");
    if (company_id) {
      // 協力会社のを非活性
      colCompany['read_only'] = true;
      colCompany['choices'] = partnerCompaies;
      common.getFromJsonList(this.member_contract_form_schema, 'name', 'contract_type')['choices'] = choice_contract_type_bp;
      // 連絡窓口担当者
      common.getFromJsonList(this.member_form_basic_schema, 'name', "middleman")['choices'] = members_choice;
    } else {
      // 個人事業主の場合、協力会社を取り除く
      const idxCompany = this.member_form_basic_schema.indexOf(colCompany);
      if (idxCompany >= 0) {
        this.member_form_basic_schema.splice(idxCompany, 1);
      }
      common.getFromJsonList(this.member_contract_form_schema, 'name', 'contract_type')['choices'] = choice_contract_type_individual;
      common.getFromJsonList(this.member_form_basic_schema, 'name', 'email')['required'] = true;
    }
    // 社員コードを取り除く
    const colCode = common.getFromJsonList(this.member_form_basic_schema, 'name', 'code');
    if (!common.isEmpty(colCode)) {
      const idxCode = this.member_form_basic_schema.indexOf(colCode);
      this.member_form_basic_schema.splice(idxCode, 1);
    }
    // 所属部署の設定
    member_add_org_schema[0]['choices'] = organizations;

    return [
      {
        name: '基本情報',
        formProps: {
          name: 'basic_info',
          schema: this.member_form_basic_schema,
          layout: member_form_basic_layout,
          data: Object.assign({}, duplicated_member_data, {
            company: company_id && parseInt(company_id),
          }),
          onBlurs: [onBlurPostcode, this.onBlurMemberDuplicate],
        },
      },
      {
        name: '所属設定',
        formProps: {
          name: 'organization',
          schema: member_add_org_schema,
        },
      },
      {
        name: '社員番号設定',
        formProps: {
          name: 'basic_info',
          schema: member_code_form_schema,
        },
        actions: [
          {
            'tooltip': '自動採番',
            'title': '自動採番',
            'handleClick': (stepsData) => {
              if (company_id) {
                return getNextPartnerMemberCode(company_id, stepsData)
              } else {
                return getNextSelfEmployeeCode(stepsData)
              }
            },
          },
        ],
      },
      {
        name: "契約情報",
        formProps: {
          name: 'contract_info',
          schema: this.member_contract_form_schema,
          data: default_contract_data,
          checkList: [checkDateContradict],
          onChanges: [onChangeCalculateType, onChangeMinusAndPlus],
          inlines: [
            {
              name: "calculate_hours",
              title: "契約計算用時間",
              schema: contract_calculate_hours_schema,
              allowAdd: false,
              allowDelete: false,
            },
            {
              name: 'allowances',
              title: '精算条件',
              schema: contract_allowance_schema,
              allowAdd: false,
              allowDelete: false,
            },
            {
              name: "contract_items",
              title: "契約条件",
              schema: bp_contract_item_schema,
              layout: bp_contract_item_layout,
              allowAdd: false,
              allowDelete: false,
            },
          ],
        },
      }
    ];
  };

  render() {
    const { breadcrumbs, endpoint, callback } = this.props;
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <GridContainer>
          <GridItem xs={12} sm={12} md={9} lg={9}>
            <StepForm
              title={this.title}
              steps={steps}
              methodName='POST'
              saveEndpoint={endpoint}
              saveCallback={callback}
            />
          </GridItem>
        </GridContainer>
        <DuplicatedMemberDialog
          innerRef={dlg => this._openDuplicatedDlg = dlg && dlg.handleOpen}
          handleSelected={this.onDuplicatedMemberSelect}
        />
      </div>
    );
  }
}

CommonMemberAdd.propTypes = {
  company_id: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]),
  endpoint: PropTypes.string.isRequired,
  callback: PropTypes.func.isRequired,
  breadcrumbs: PropTypes.arrayOf(PropTypes.object),
};


export default withPermissions(CommonMemberAdd, 'partner.add_partnermember');
