import React from 'react';
import {
  Button,
  List,
  ListItem,
  ListItemText,
  withStyles,
  Typography,
} from '@material-ui/core';
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import AddRequestIcon from '@material-ui/icons/MonetizationOn';
import FileDownloadIcon from '@material-ui/icons/SaveAltRounded';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import DataSource from '../../components/DataSource';
import Card from '../../components/Card/Card';
import CardHeader from '../../components/Card/CardHeader';
import CardBody from '../../components/Card/CardBody';
import { TableDetail, EnhancedTable, FormDialog } from '../../datatable';
import {
  getProjectDetail,
  getProjectBusinessTypeChoice,
  getOrganizationChoices,
  getCompanyCategory,
  getContentObjects,
  getCascadeObjects,
  getBankAccountList,
  createProjectRequest,
  downloadAttachment,
  getSalespersonChoice,
  onDeleteProjectRequset,
  getProjectRequestProfile,
  changeProjectRequestProfile,
} from '../utils/api';
import {
  project_detial_schema,
  project_basic_info_schema,
  project_basic_info_layout,
  project_attendance_status_schema,
  project_request_status_schema,
  create_request_form_schema,
  project_request_profile_schema,
} from './schema';
import { common, config, ajax, constant, choice_attendance_type } from '../../utils';
import { withPermissions, HasPerm, Toast, CardFooter } from '../../components';

const styles = (theme) => ({
  listItem: {
    paddingTop: 0,
    paddingBottom: 0,
  },
  flex: {
    flex: 1,
  },
  button: {
    marginLeft: theme.spacing(1),
  }
});

class ProjectDetial extends React.Component {
  project_info_schema = JSON.parse(JSON.stringify(project_basic_info_schema));

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      project_request_profile: {},
      project_business_type_list: [],
      organizations: [],
      salesperson_choice: [],
      company_categories: [],
      bank_account_list: [],
      reload: false,
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.project.detail,
      props.match.params.project_id,
    );
  }

  initialize = (props) => {
    const { project_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
      getContentObjects(project.company_content_type).then(companyList => {
        this.project_info_schema[1]['choices'] = companyList;
      });
      getBankAccountList(project.my_company_content_type, project.my_company_id).then(bank_account_list => {
        this.setState({ bank_account_list });
      });
      getProjectRequestProfile(project_id).then(data => (
        this.setState({project_request_profile: data})
      ));
    });
    getProjectBusinessTypeChoice().then(data => {
      this.setState({project_business_type_list: data});
    });
    getOrganizationChoices().then(organizations => this.setState({organizations}));
    getCompanyCategory().then(company_categories => this.setState({company_categories}));
    getSalespersonChoice().then(salesperson_choice => this.setState({salesperson_choice}));
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${project.name || ''} | 案件一覧`;
  };

  onEditProject = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(project => {
      this.setState({project});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteProject = () => {
    ajax.fetchDelete(this.detail_api).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect('/project/projects')
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  showRequestConfirm = (data, requestCreatedCallback) => {
    if (this._showRequestConfirm) {
      this._showRequestConfirm(data);
      this._requestCreatedCallback = requestCreatedCallback;
    }
  };

  createProjectRequest = (data, handleClose) => {
    const { project_id } = this.props.match.params;
    return createProjectRequest(project_id, data).then(json => {
      handleClose();
      Toast.success(constant.SUCCESS.REQUEST_FILE_CREATED);
      if (this._requestCreatedCallback) {
        const newSelected = Object.assign(data, json);
        this._requestCreatedCallback(newSelected)
      }
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  handleRequestCreated = (data) => {
    this.setState({reload: !this.state.reload});
  };

  handleDeleteRequest = (request_id) => {
    if (request_id) {
      const { project_id } = this.props.match.params;
      return onDeleteProjectRequset(project_id, request_id).then(() => {
        Toast.success(constant.SUCCESS.DELETED);
        this.setState({reload: !this.state.reload});
      }).catch((errors) => {
        Toast.error(errors.detail);
      });
    } else {
      Toast.error(constant.ERROR.REQUEST_FILE_NOT_FOUND);
      return Promise.reject();
    }
  };

  showChangeProjectRequestProfile = () => {
    if (this._showChangeProjectRequestProfile) {
      const { project_request_profile } = this.state;
      this._showChangeProjectRequestProfile(project_request_profile);
    }
  };

  onChangeProjectRequestProfile = (data, handleClose) => {
    const { project_id } = this.props.match.params;
    return changeProjectRequestProfile(project_id, data).then(json => {
      handleClose();
      this.setState({project_request_profile: json});
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { project_id } = this.props.match.params;
    const { classes } = this.props;
    const {
      project,
      project_request_profile,
      project_business_type_list,
      organizations,
      company_categories,
      bank_account_list,
      reload,
      salesperson_choice,
    } = this.state;
    this.project_info_schema[0]['choices'] = company_categories;
    this.project_info_schema[4]['choices'] = project_business_type_list;
    this.project_info_schema[10]['choices'] = organizations;
    common.getFromJsonList(this.project_info_schema, 'name', 'salesperson')['choices'] = salesperson_choice;
    project_detial_schema[1]['choices'] = project_business_type_list;
    common.getFromJsonList(create_request_form_schema, 'name', 'bank_account')['choices'] = bank_account_list;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { name: project.name || '' },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={8} lg={9}>
              <TableDetail
                title={`案件：${project.name || ''}`}
                data={project}
                schema={project_detial_schema}
                editProps={{
                  title: '案件情報変更',
                  schema: this.project_info_schema,
                  layout: project_basic_info_layout,
                  handleEdit: this.onEditProject,
                  onChanges: [(name, data) => getCascadeObjects(
                    name,
                    'company_content_type',
                    data[name],
                    this.project_info_schema
                  )],
                  visible: common.hasPerm('project.change_project'),
                }}
                deleteProps={{
                  handleDelete: this.onDeleteProject,
                  visible: common.hasPerm('project.delete_project'),
                }}
                actions={[
                  (
                    <HasPerm permission='project.view_projectmember' key="list">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => common.redirect(`/project/projects/${project_id}/members`, 'is_retired=false')}
                      >要員一覧</Button>
                    </HasPerm>
                  ),
                  (
                    <HasPerm permission='project.add_projectmember' key="add">
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => common.redirect(`/project/projects/${project_id}/members/add`)}
                      >アサイン</Button>
                    </HasPerm>
                  ),
                ]}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={4} lg={3}>
              <Card>
                <CardBody>
                  <List disablePadding={true}>
                    <ListItem className={classes.listItem}>
                      <ListItemText
                        primary='既定出勤の計算区分'
                        secondary={project_request_profile.attendance_type ? common.getFromJsonList(
                          choice_attendance_type, 'value', project_request_profile.attendance_type
                        ).display_name : 'なし'}
                      />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                      <ListItemText
                        primary='既定基準時間'
                        secondary={common.isEmpty(project_request_profile.min_hours) ? 'なし' : project_request_profile.min_hours}
                      />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                      <ListItemText
                        primary='既定最大時間'
                        secondary={common.isEmpty(project_request_profile.max_hours) ? 'なし' : project_request_profile.max_hours}
                      />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                      <ListItemText
                        primary='時給'
                        secondary={project_request_profile.is_hourly_pay ? 'はい' : 'いいえ'}
                      />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                      <ListItemText
                        primary='一括'
                        secondary={project_request_profile.is_blanket_contract ? 'はい' : 'いいえ'}
                      />
                    </ListItem>
                  </List>
                </CardBody>
                <CardFooter>
                  <HasPerm permission='project.view_projectcontract'>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => common.redirect(`/project/projects/${project_id}/contracts`)}
                    >
                      一括契約
                    </Button>
                  </HasPerm>
                  <HasPerm permission='project.view_projectsubcontractor'>
                    <Button
                      className={classes.button}
                      variant="contained"
                      color="primary"
                      onClick={() => common.redirect(`/project/projects/${project_id}/subcontractors`)}
                    >
                      下請一覧
                    </Button>
                  </HasPerm>
                  <Typography className={classes.flex}></Typography>
                  <HasPerm permission='project.change_projectmember'>
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={this.showChangeProjectRequestProfile}
                    >
                      変更
                    </Button>
                  </HasPerm>
                </CardFooter>
              </Card>
            </GridItem>
            <HasPerm permission='attendance.view_attendance'>
              <GridItem xs={12} sm={12} md={12} lg={12}>
                <Card>
                  <CardHeader color="info">勤怠情報入力状態</CardHeader>
                  <CardBody>
                    <DataSource
                      endpoint={common.formatStr(config.api.project.project_attendance_status, project_id)}
                      render={(initData) => (
                        <EnhancedTable
                          tableHead={project_attendance_status_schema}
                          tableData={initData}
                          rowsPerPage={config.rowsPerPage}
                          toolbar={false}
                          tableStyles={{ minWidth: 300 }}
                        />
                      )}
                    />
                  </CardBody>
                </Card>
              </GridItem>
            </HasPerm>
            <HasPerm permission='request.view_request'>
              <GridItem xs={12} sm={12} md={12} lg={12}>
                <Card>
                  <CardHeader color="info">請求一覧</CardHeader>
                  <CardBody>
                    <DataSource
                      reload={reload}
                      endpoint={common.formatStr(config.api.project.project_request_status, project_id)}
                      render={(initData) => (
                        <EnhancedTable
                          tableHead={project_request_status_schema}
                          tableData={initData}
                          rowsPerPage={config.rowsPerPage}
                          selectable="single"
                          tableStyles={{ minWidth: 800 }}
                          tableActions={[
                            {
                              'tooltip': '注文書追加',
                              'icon': <NoteAddIcon />,
                              'handleClick': () => (common.redirect(`/project/projects/${project_id}/orders/add`)),
                              'visible': common.hasPerm('project.add_projectorder'),
                            },
                          ]}
                          rowActions={[
                            {
                              'tooltip': '請求書作成',
                              'icon': <AddRequestIcon />,
                              'handleClick': this.showRequestConfirm,
                              'visible': common.hasPerm('request.add_request'),
                            },
                            {
                              'tooltip': '請求書ダウンロード',
                              'icon': <FileDownloadIcon />,
                              'handleClick': (data) => {
                                if (data && data.request_file_id) {
                                  downloadAttachment(data.request_file_id);
                                } else {
                                  Toast.error(constant.ERROR.REQUEST_FILE_NOT_FOUND);
                                }
                              },
                              'visible': common.hasPerm('request.view_request'),
                            },
                          ]}
                          pushpinTop={common.getHeaderHeight()}
                          deleteProps={{
                            handleDelete: (data) => this.handleDeleteRequest(data.request_id),
                          }}
                        />
                      )}
                    />
                  </CardBody>
                </Card>
              </GridItem>
            </HasPerm>
          </GridContainer>
          <FormDialog
            innerRef={(dlg) => { this._showRequestConfirm = dlg && dlg.handleOpen }}
            title={'請求書作成'}
            schema={create_request_form_schema}
            handleOk={this.createProjectRequest}
            saveCallback={this.handleRequestCreated}
          />
          <FormDialog
            innerRef={(dlg) => { this._showChangeProjectRequestProfile = dlg && dlg.handleOpen }}
            title={'案件既定情報変更'}
            schema={project_request_profile_schema}
            handleOk={this.onChangeProjectRequestProfile}
          />
        </div>
      </div>
    );
  }
}

export default withPermissions(withStyles(styles)(ProjectDetial), 'project.view_project');
