import React from 'react';
import { withSnackbar } from 'notistack';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import {
  withPermissions,
  HasPerm,
  TopBreadcrumbs,
  Card,
  CardHeader,
  CardBody,
  DataSource,
  Timeline,
} from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { TableDetail, EnhancedTable } from '../../datatable';
import {
  getIndividualMember,
  handleEditObject,
  handleDeleteObject,
  getPartnerMemberProjectTimeline,
  handleAddObject,
  getOrganizationChoices,
} from '../utils/api';
import {
  member_detail_schema,
  member_form_basic_schema,
  member_form_basic_layout,
  bp_member_contract_list_schema,
} from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import PaymentNoticeStatus from './payment_notice_status';
import BankAccountList from '../common/bank_account_list';
import { onBlurPostcode } from '../utils';
import {
  member_education_form_schema,
  member_education_list_schema,
  list_member_organization_schema,
  member_organization_form_schema,
} from '../contract/schema';

class IndividualMember extends React.Component {
  member_contract_list_schema = common.clone(bp_member_contract_list_schema);
  member_organization_form_schema = common.clone(member_organization_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      member: {},
      member_projects: [],
      organizations: [],
      education_reload: false,
      org_reload: false,
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.partner.individual_member_detail,
      props.match.params.member_id
    );
  }

  initialize = (props) => {
    const { member_id } = props.match.params;
    getIndividualMember(member_id).then(member => {
      this.setState({member});
    });
    getPartnerMemberProjectTimeline(member_id).then(member_projects => {
      this.setState({ member_projects });
    });
    getOrganizationChoices().then(organizations => this.setState({ organizations }));
  };

  componentDidUpdate = () => {
    const { member } = this.state;
    document.title = `${member.full_name || ''} | 個人事業主`;
  };

  render() {
    const { member_id } = this.props.match.params;
    const { member, member_projects, organizations, education_reload, org_reload } = this.state;
    // 社員番号と所属会社は変更できないように
    const member_form_schema = common.clone(member_form_basic_schema);
    common.getFromJsonList(member_form_schema, 'name', "code")['read_only'] = true;
    common.getFromJsonList(member_form_schema, 'name', "company")['read_only'] = true;
    common.getFromJsonList(member_form_schema, 'name', "middleman")['read_only'] = true;
    common.getFromJsonList(this.member_contract_list_schema, 'name', 'contract_no').link
      = "/partner/individual/%(object_id)s/contracts/%(id)s/detail";
    // 所属部署の設定
    common.getFromJsonList(this.member_organization_form_schema, 'name', 'organization').choices = organizations;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/individual', name: '個人事業主一覧' },
            { name: member.full_name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={member}
                schema={member_detail_schema}
                editProps={{
                  title: '社員情報変更',
                  schema: member_form_schema,
                  layout: member_form_basic_layout,
                  handleEdit: handleEditObject(
                    (member) => this.setState({member}),
                    this.detail_api,
                  ),
                  visible: common.hasPerm('partner.change_partnermember'),
                  onBlurs: [onBlurPostcode],
                }}
                deleteProps={{
                  handleDelete: handleDeleteObject(
                    () => common.redirect('/partner/individual'),
                    this.detail_api,
                  ),
                  visible: common.hasPerm('partner.delete_partnermember'),
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={3} lg={3}>
              <Card profile>
                <CardHeader>アサインした案件</CardHeader>
                <CardBody>
                  { !common.isEmpty(member_projects) ? (
                    <Timeline items={member_projects} showTime={false} showEnd={true} />
                  ) : null }
                </CardBody>
              </Card>
            </GridItem>
          </GridContainer>
          {member.content_type ? (
            <HasPerm permission='org.view_organizationmember'>
              <Card>
                <CardHeader color='info'>所属一覧</CardHeader>
                <CardBody>
                  <DataSource
                    reload={org_reload}
                    endpoint={common.formatStr(config.api.member.member_organizations, member.content_type, member_id)}
                    render={(initData) => (
                      <EnhancedTable
                        title='所属一覧'
                        tableHead={list_member_organization_schema}
                        tableData={initData}
                        rowsPerPage={config.rowsPerPage}
                        selectable='single'
                        addProps={{
                          title: '所属情報追加',
                          schema: this.member_organization_form_schema,
                          handleOk: handleAddObject(
                            () => this.setState({ org_reload: !org_reload }),
                            config.api.organization.member_organization_add,
                          ),
                          data: {
                            content_type: member.content_type,
                            object_id: member_id,
                          },
                          visible: common.hasPerm('org.add_organizationmember'),
                        }}
                        editProps={{
                          title: '所属情報変更',
                          schema: this.member_organization_form_schema,
                          handleOk: handleEditObject(
                            () => this.setState({ org_reload: !org_reload }),
                            config.api.organization.member_organization_detail,
                          ),
                          visible: common.hasPerm('org.change_organizationmember'),
                        }}
                        deleteProps={{
                          handleDelete: handleDeleteObject(
                            () => this.setState({ org_reload: !org_reload }),
                            config.api.organization.member_organization_detail,
                          ),
                          visible: common.hasPerm('org.delete_organizationmember'),
                        }}
                        pushpinTop={common.getHeaderHeight()}
                      />
                    )}
                  />
                </CardBody>
              </Card>
            </HasPerm>
          ) : null}
          <Card>
            <CardHeader color="info">
              学歴一覧
            </CardHeader>
            <CardBody>
              <DataSource
                reload={education_reload}
                endpoint={ common.formatStr(config.api.member.education_list, member_id) }
                render={ (initData) => (
                  <EnhancedTable
                    title='学歴'
                    tableHead={member_education_list_schema}
                    tableData={initData.results}
                    rowsPerPage={config.rowsPerPage}
                    allowCsv={true}
                    selectable='single'
                    addProps={{
                      title: '学歴追加',
                      schema: member_education_form_schema,
                      handleOk: handleAddObject(
                        () => this.setState({education_reload: !education_reload}),
                        config.api.member.education_list,
                        member_id
                      ),
                      data: {
                        'content_type': member.content_type,
                        'object_id': member_id,
                      },
                    }}
                    editProps={{
                      title: '学歴変更',
                      schema: member_education_form_schema,
                      handleOk: handleEditObject(
                        () => this.setState({education_reload: !education_reload}),
                        config.api.member.education_detail,
                        member_id
                      ),
                    }}
                    deleteProps={{
                      handleDelete: handleDeleteObject(
                        () => this.setState({education_reload: !education_reload}),
                        config.api.member.education_detail,
                        member_id
                      ),
                    }}
                    pushpinTop={common.getHeaderHeight()}
                  />
                ) } 
              />
            </CardBody>
          </Card>
          <Card>
            <CardHeader color="info">契約一覧</CardHeader>
            <CardBody>
              <DataSource
                endpoint={common.formatStr(config.api.partner.individual_member_contracts, member_id)}
                render={(initData) => (
                  <EnhancedTable
                    tableHead={this.member_contract_list_schema}
                    tableData={initData}
                    rowsPerPage={config.rowsPerPage}
                    tableActions={[
                      {
                        'tooltip': 'ＢＰ契約を追加する',
                        'icon': <AddIcon color="secondary"/>,
                        'handleClick': () => (common.redirect(`/partner/individual/${member_id}/contracts/add`)),
                        'visible': common.hasPerm('partner.change_partnermember'),
                      },
                    ]}
                    rowActions={[
                      {
                        'tooltip': "ＢＰ契約を変更する",
                        "icon": <EditIcon />,
                        "handleClick": (data) => common.redirect(`/partner/individual/${member_id}/contracts/${data.id}/form`),
                        'visible': common.hasPerm('partner.change_partnermember'),
                      },
                    ]}
                    selectable='single'
                    pushpinTop={common.getHeaderHeight()}
                    tableStyles={{ minWidth: 800 }}
                  />
                )}
              />
            </CardBody>
          </Card>
          { member.content_type ? (
            <BankAccountList
              title={`${member.full_name} 口座一覧`}
              content_type={member.content_type}
              object_id={member_id}
            />
          ) : null}
          <HasPerm permission='partner.view_partnerrequest'>
            <Card>
              <CardHeader color="info">支払通知と請求一覧</CardHeader>
              <CardBody>
                {member.content_type ? (
                  <PaymentNoticeStatus
                    content_type={member.content_type}
                    object_id={member_id}
                    endpoint={common.formatStr(config.api.partner.individual_payment_notice_status, member_id)}
                  />
                ) : null }
              </CardBody>
            </Card>
          </HasPerm>
        </div>
      </div>
    );
  }
}

export default withPermissions(
  withSnackbar(IndividualMember),
  'partner.view_partnermember',
);
