import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { withPermissions, StepForm } from '../../components';
import { getProjectDetail, getPartnerCompanyChoices } from '../utils';
import { config, common } from '../../utils';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  project_subcontractor_add_schema,
} from './schema';
import {
  blanket_contract_form_schema,
  blanket_contract_form_layout,
  contract_allowance_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
  default_blanket_contract_data,
} from '../partner/schema';


class SubcontractorForm extends React.Component {
  title = '下請業者追加'
  
  constructor(props) {
    super(props);

    this.state = {
      project: {},
      contract_data: common.clone(default_blanket_contract_data),
      company_choice: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id } = props.match.params;
    getProjectDetail(project_id).then(project => this.setState({project}));
    getPartnerCompanyChoices().then(company_choice => this.setState({company_choice}));
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  handleSubcontractorCompanyChange = (name, data) => {
    const { company_choice } = this.state;
    const value = data[name];
    if (name === 'company') {
      if (value) {
        data['company_name'] = common.getFromJsonList(company_choice, 'value', value)['display_name'];
      } else {
        data['company_name'] = null;
      }
    }
  };

  getSteps = () => {
    const { project_id } = this.props.match.params;
    const { company_choice, contract_data } = this.state;
    const my_project_subcontractor_add_schema = common.clone(project_subcontractor_add_schema);
    my_project_subcontractor_add_schema[0]["choices"] = company_choice;
    if (common.isEmpty(company_choice)) {
      return null;
    } else {
      return [
        {
          name: '下請会社選択',
          formProps: {
            name: 'company_info',
            schema: my_project_subcontractor_add_schema,
            data: {
              'project': project_id,
            },
            onChanges: [this.handleSubcontractorCompanyChange],
          },
        },
        {
          name: "契約条件",
          formProps: {
            name: 'contract_info',
            schema: blanket_contract_form_schema,
            layout: blanket_contract_form_layout,
            inlines: [
              {
                name: 'allowances',
                title: '精算条件',
                schema: contract_allowance_schema,
                allowAdd: false,
                allowDelete: false,
              },
              {
                name: "contract_items",
                title: "契約条件",
                schema: bp_contract_item_schema,
                layout: bp_contract_item_layout,
                allowAdd: false,
                allowDelete: false,
              },
            ],
            data: contract_data,
          },
        }
      ];
    }
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project } = this.state;
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { link: `/project/projects/${project_id}`, name: project.name },
            { link: `/project/projects/${project_id}/subcontractors`, name: '下請業者一覧' },
            { name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              { steps ? (
                <StepForm
                  title={this.title}
                  steps={steps}
                  methodName='POST'
                  saveEndpoint={common.formatStr(config.api.project.subcontractor_save, project_id)}
                  saveCallback={() => common.redirect(`/project/projects/${project_id}/subcontractors`)}
                />
              ) : null}
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(SubcontractorForm, [
  'project.add_projectsubcontractor', 'project.change_projectsubcontractor'
]);
