import React from 'react';
import {
  Card,
  CardHeader,
  CardContent,
  IconButton,
  CardActions,
  Button,
} from '@material-ui/core';
import PrintIcon from '@material-ui/icons/Print';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { common, ajax } from '../../utils/common';
import {
  getPartnerCompany,
  getPartnerMember,
  getPartnerOrderHtml,
  getPartnerOrderRequestHtml,
  downloadAttachment,
} from '../utils/api';
import { ConfirmDialog } from '../../datatable';
import { config, constant } from '../../utils';
import { withPermissions, HasPerm, Toast } from '../../components';

class PartnerOrderDetail extends React.Component {
  title = 'BP注文書';
  reportId = 'printPartnerOrder';
  reportRequestId = 'printPartnerOrderRequest';

  constructor(props) {
    super(props);

    this.state = {
      company: {},
      member: {},
      partner_order_html: null,
      partner_order_uuid: null,
      partner_order_request_html: null,
      partner_order_request_uuid: null,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id, member_id, order_id } = props.match.params;
    getPartnerCompany(company_id).then(company => {
      this.setState({company});
    });
    if (member_id) {
      getPartnerMember(company_id, member_id).then(member => {
        this.setState({member});
      });
    }
    getPartnerOrderHtml(order_id).then(json => {
      this.setState({partner_order_html: json.html, partner_order_uuid: json.uuid});
    });
    getPartnerOrderRequestHtml(order_id).then(json => {
      this.setState({partner_order_request_html: json.html, partner_order_request_uuid: json.uuid});
    });
  };

  componentDidUpdate = () => {
    const { member, company } = this.state;
    if (!common.isEmpty(member)) {
      document.title = `${this.title} | ${member.full_name} | 協力社員`;
    } else {
      document.title = `${this.title} | ${company.name} | 協力会社`;
    }
  };

  showDeleteConfirm = () => {
    if (this.__confirm) {
      this.__confirm();
    }
  };

  handleDelete = () => {
    const { company_id, member_id, order_id } = this.props.match.params;
    ajax.fetchDelete(common.formatStr(config.api.partner.order_detail, order_id)).then(() => {
      if (member_id) {
        common.redirect(`/partner/companies/${company_id}/members/${member_id}`);
      } else {
        common.redirect(`/partner/companies/${company_id}`);
      }
      Toast.success(constant.SUCCESS.DELETED);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { company_id, member_id } = this.props.match.params;
    const {
      company,
      member,
      partner_order_html,
      partner_order_request_html,
      partner_order_uuid,
      partner_order_request_uuid,
    } = this.state;
    const breadcrumbs = [
      { link: '/partner/companies', name: '協力会社一覧' },
      { link: `/partner/companies/${company_id}/`, name: company.name },
    ];
    if (member_id) {
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members`, name: '社員一覧' });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members/${member_id}`, name: member.full_name });
      breadcrumbs.push({ name: this.title });
    } else {
      breadcrumbs.push({ name: this.title });
    }

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <div style={{marginTop: 16}}>
          <Card>
            <CardHeader
              title={this.title}
              action={
                <IconButton
                  aria-haspopup="true"
                  onClick={() => {common.printIframeContent(this.reportId)}}
                >
                  <PrintIcon />
                </IconButton>
              }
            />
            <CardContent>
              <iframe
                title={this.title}
                id={this.reportId}
                srcDoc={partner_order_html}
                frameBorder='0'
                width='100%'
                height='1300px'
              >
              </iframe>
            </CardContent>
            <CardActions>
              <HasPerm permission='partner.delete_partnerorder'>
                <Button
                  variant="contained"
                  color="secondary"
                  style={{margin: 8}}
                  onClick={this.showDeleteConfirm}
                >
                  削除
                </Button>
              </HasPerm>
              <Button
                variant="contained"
                color="primary"
                style={{margin: 8}}
                onClick={() => downloadAttachment(partner_order_uuid)}
              >
                ダウンロード
              </Button>
            </CardActions>
          </Card>
          <ConfirmDialog
            title='削除してもよろしいですか。'
            onOk={this.handleDelete}
            innerRef={dlg => { this.__confirm = dlg && dlg.handleOpen }}
          />
        </div>
        <div style={{marginTop: 16}}>
          <Card>
            <CardHeader
              title={this.title}
              action={
                <IconButton
                  aria-haspopup="true"
                  onClick={() => {common.printIframeContent(this.reportRequestId)}}
                >
                  <PrintIcon />
                </IconButton>
              }
            />
            <CardContent>
              <iframe
                title={this.title}
                id={this.reportRequestId}
                srcDoc={partner_order_request_html}
                frameBorder='0'
                width='100%'
                height='1300px'
              >
              </iframe>
            </CardContent>
            <CardActions>
              <Button
                variant="contained"
                color="primary"
                style={{margin: 8}}
                onClick={() => downloadAttachment(partner_order_request_uuid)}
              >
                ダウンロード
              </Button>
            </CardActions>
          </Card>
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerOrderDetail, 'partner.view_partnerorder');
