import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import {
  Drawer,
  IconButton,
  Divider,
  withWidth,
  isWidthDown,
} from '@material-ui/core';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TopBar from '../containers/TopBar';
import styles from '../assets/jss/views/mainStyle';

class MainFrame extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      showMessage: false,
    };
  }

  componentDidMount() {
    this.props.onMount();
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { classes, theme, appName, sideMenu, children, width } = this.props;
    const { open } = this.state;

    return (
      <div className={classes.root}>
        <TopBar 
          classes={classes} 
          open={open} 
          handleDrawerOpen={this.handleDrawerOpen}
          appName={appName}
        />
        <Drawer
          variant={isWidthDown('xs', width) ? 'temporary' : 'permanent'}
          className={classNames(classes.drawer, {
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          })}
          classes={{
            paper: classNames({
              [classes.drawerOpen]: open,
              [classes.drawerClose]: !open,
            }),
          }}
          open={open}
          PaperProps={{style: {overflowX: 'hidden'}}}
          onClick={this.handleDrawerClose}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={this.handleDrawerClose}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </div>
          <Divider/>
          {sideMenu}
        </Drawer>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          {children}
        </main>
      </div>
    );
  }
}

MainFrame.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  appName: PropTypes.string.isRequired,
};

export default withStyles(styles, { withTheme: true })(withWidth()(MainFrame));
