import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import Card from '../../components/Card/Card';
import CardHeader from '../../components/Card/CardHeader';
import CardBody from '../../components/Card/CardBody';
import OrderStatusList from './order_status_list';
import { common, config } from '../../utils';
import { withPermissions } from '../../components';

class PartnerOrderList extends React.Component {

  componentDidMount = () => {
    const { year, month } = this.props.match.params;
    document.title = `${year}年${month}月注文一覧 | BP注文`;
  };
  
  render() {
    const { year, month } = this.props.match.params;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/monthly-orders', name: 'BP注文一覧' },
            { name: `${year}年${month}月注文一覧` },
          ]}
        />
        <Card>
          <CardHeader color="info">{year}年{month}月注文一覧</CardHeader>
          <CardBody>
            <OrderStatusList
              endpoint={common.formatStr(config.api.partner.orders_status, year, month)}
              showMonth={false}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(PartnerOrderList, 'partner.view_partnerorder');
