import React from 'react';
import { Paper, withStyles } from '@material-ui/core';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import {
  TopBreadcrumbs,
  DataSource,
  Toast,
} from '../../components';
import {
  getOrganization,
  export_attendances_by_month,
  handleAddObject,
} from '../utils';
import { EnhancedTable, FormDialog } from '../../datatable';
import {
  org_attendance_by_month_schema,
  import_attendance_form_schema,
} from './schema';
import { common, config } from '../../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

const styles = (theme) => ({
  paper: {
    ...paperSpacing,
  }
})

class OrgAttendanceByMonth extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      organization: {},
      reload: false,
      year: props.match.params.year,
      month: props.match.params.month,
    };
    this.initialize(props);
　}

  initialize = (props) => {
    const { org_id } = props.match.params;
    getOrganization(org_id).then(organization => {
      this.setState({organization});
    });
  };

  componentDidUpdate = () => {
    const { organization, year, month } = this.state;
    document.title = `${year}年${month}月勤怠 | ${organization.name}`;
  };

  prevMonthAttendances = () => {
    const { org_id } = this.props.match.params;
    const { year, month } = this.state;
    const date = common.getPrevMonthDictByYm(year, month);
    common.redirect(`/organization/${org_id}/attendances/${date.year}/${date.month}/`);
    this.setState(state => ({
      year: date.year,
      month: date.month,
      reload: !state.reload,
    }));
  };

  nextMonthAttendances = () => {
    const { org_id } = this.props.match.params;
    const { year, month } = this.state;
    const date = common.getNextMonthDictByYm(year, month);
    common.redirect(`/organization/${org_id}/attendances/${date.year}/${date.month}/`);
    this.setState(state => ({
      year: date.year,
      month: date.month,
      reload: !state.reload,
    }));
  };

  exportAttendanceExcel = () => {
    const { org_id } = this.props.match.params;
    const { year, month } = this.state;
    return export_attendances_by_month(org_id, year, month).then(json => {
      const blob = common.toBlob(json.blob);
      common.downloadBlog(blob, json.name);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  showImportAttendanceExcel = () => {
    if (this._showImportDialog) {
      this._showImportDialog();
    }
  };

  render() {
    const { org_id } = this.props.match.params;
    const { classes } = this.props;
    const { organization, year, month, reload } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/organization', name: '部署一覧' },
            { link: `/organization/${org_id}`, name: organization.name },
            { name: `${year}年${month}月勤怠一覧` },
          ]}
        />
        <Paper className={classes.paper}>
          <DataSource
            reload={reload}
            endpoint={common.formatStr(config.api.organization.attendance_by_month, org_id, year, month)}
            render={ (initData) => (
              <EnhancedTable
                title={`${year}年${month}月勤怠一覧`}
                showTitle={true}
                tableHead={org_attendance_by_month_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                allowCsv={false}
                pushpinTop={common.getHeaderHeight()}
                tableProps={{size: 'small', style: {fontSize: 12}}}
                tableActions={[
                  {
                    "tooltip": "先月",
                    "icon": <NavigateBeforeIcon />,
                    "handleClick": this.prevMonthAttendances,
                  },
                  {
                    "tooltip": "次月",
                    "icon": <NavigateNextIcon />,
                    "handleClick": this.nextMonthAttendances,
                  },
                  {
                    "tooltip": "エクセルにエクスポート",
                    "icon": <CloudDownloadIcon />,
                    "handleClick": this.exportAttendanceExcel,
                    "showLoading": true,
                  },
                  {
                    "tooltip": "勤務時間をアップロード",
                    "icon": <CloudUploadIcon />,
                    "handleClick": this.showImportAttendanceExcel,
                  },
                ]}
              />
            )}
          />
        </Paper>
        <FormDialog
          title='勤務時間をアップロード'
          schema={import_attendance_form_schema}
          handleOk={handleAddObject(
            () => this.setState(state => ({ reload: !state.reload })),
            config.api.organization.import_attendance_excel,
            org_id, year, month
          )}
          innerRef={(dlg) => { this._showImportDialog = dlg && dlg.handleOpen }}
        />
      </div>
    );
  }
}

export default withStyles(styles)(OrgAttendanceByMonth);
