import React from 'react';
import ReactGA from 'react-ga';
import { Route, Switch, withRouter } from 'react-router-dom';
import Login from '../containers/Login';
import { PrivateRoute } from '../components';
import MainLayout from '../containers/MainLayout';
import messaging from '../utils/firebase';
import { ajax, config, firebaseDeviceInfo, isGoogleAnalytics } from '../utils';
import PasswordForget from '../pages/account/password_forget';
import PasswordReset from '../pages/account/password_reset';

class App extends React.Component {

  async componentDidUpdate() {
    const self = this;
    if (messaging) {
      messaging.requestPermission().then(async function() {
        const token = await messaging.getToken();
        console.log('token:', token);
        self.sendTokenToServer(token);
      }).catch(function(error) {
        console.log("Unable to get permission to notify.", error);
      });
      // Callback fired if Instance ID token is updated.
      messaging.onTokenRefresh(() => {
        messaging.getToken().then((refreshedToken) => {
          console.log('Token refreshed:', refreshedToken);
          self.sendTokenToServer(refreshedToken);
        }).catch((err) => {
          console.log('Unable to retrieve refreshed token ', err);
        });
      });
      navigator.serviceWorker.addEventListener("message", (messageEvent) => {
        // const data = messageEvent.data.firebaseMessaging.payload.data;
        console.log('App.jsx', messageEvent);
        ajax.fetchGet(config.api.account.notification_list_by_me).then(data => (
          setTimeout(() => this.props.reloadNotification(data), 1000)
        ))
      });
    }
  }

  componentDidMount = () => {
    const { pathname } = this.props.location;
    if (isGoogleAnalytics) {
      ReactGA.set({ page: pathname });
      ReactGA.pageview(pathname);
    }
  };

  sendTokenToServer = (token) => {
    const { user } = this.props;

    if (user.devices.indexOf(token) >= 0) {
      console.log('デバイス既に登録済み！');
      return;
    }
    ajax.fetchPost(config.api.master.register_firebase_device, {
      token,
      description: firebaseDeviceInfo,
      user: user.id,
    }).then(
      () => console.log('デバイス登録成功！')
    ).catch(
      () => console.log('デバイス登録失敗！')
    );
  };
  
  render() {
    return (
      <Switch>
        <Route exact path="/login" component={Login} />
        <Route exact path="/password/forget" component={PasswordForget} />
        <Route path="/password/reset/:user:signature" component={PasswordReset} />
        <PrivateRoute path="/" component={MainLayout} />
      </Switch>
    );
  }
}

export default withRouter(App);
