import React from 'react';
import { Switch, Route } from 'react-router-dom';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import {
  List,
  Divider,
} from '@material-ui/core';
import {
  HomeListItems,
  MemberListItems,
  ProjectListItems,
  TurnoverListItems,
  PartnerListItems,
  ContractListItems,
  SettingListItems,
} from './SideMenu';
import MainFrame from '../containers/MainFrame';
import {
  HttpNotFound, HasPerm,
} from '../components';
import {
  MemberDashboard,
  MemberList,
  MemberDetail,
  SalespersonList,
  ReleaseList,
  ExpenseMonths,
  ExpenseByMonthList,
  ExpenseByObjectList,
} from '../pages/member';
import {
  ClientCompanyList,
  ClientCompanyForm,
  ClientCompanyDetail,
} from '../pages/client';
import {
  ContractDashboard,
  ContractMemberAdd,
  ContractMemberList,
  ContractMemberDetail,
  ContractAdd,
  ContractDetail,
  ContractEmployment,
  ContractIncumbency,
  ContractIncome,
  ContractAutoUpdateList,
  ContractExpireList,
  CommutationList,
  SalaryUpload,
  PaidVacationList,
} from '../pages/contract';
import {
  OrganizationList,
  OrganizationForm,
  OrganizationDetail,
  OrgMemberList,
  OrgPositionList,
  OrgAttendanceByMonth,
} from '../pages/organization';
import {
  PartnerCompanyList,
  PartnerCompanyForm,
  PartnerCompanyDetail,
  PartnerMemberAdd,
  PartnerMemberList,
  PartnerMemberDetail,
  PartnerContractForm,
  PartnerContractDetail,
  PartnerOrderDetail,
  PartnerOrderList,
  PartnerMonthlyOrders,
  PartnerCompanyBankAccounts,
  IndividualMembers,
  IndividualMember,
  IndividualMemberAdd,
  PartnerCompanyPaymentNotice,
  IndividualPaymentNotice,
  PartnerMonthlyRequests,
  PartnerRequestList,
} from '../pages/partner';
import {
  ProjectList,
  ProjectAdd,
  ProjectDetial,
  ProjectMemberAdd,
  ProjectMemberDetail,
  ProjectMemberList,
  ProjectMemberContractForm,
  ProjectOrderAdd,
  ProjectOrderDetail,
  ProjectAttendance,
  ProjectRequest,
  SubcontractorList,
  SubcontractorForm,
  SubcontractorContractForm,
  ProjectMemberDuplications,
  ProjectContractList,
  ProjectContractForm,
} from '../pages/project';
import {
  MyPage,
} from '../containers';
import styles from '../assets/jss/views/mainStyle';
import { config } from '../utils';
import {
  TurnoverDashBoard,
  MembersMonthlyTurnover,
  MembersTurnoverByMonth,
  MembersTurnoverByFiscalYear,
} from '../pages/turnover';
import Home from '../pages/home';

class MainLayout extends React.Component {
  render() {
    const { user } = this.props;

    return (
      <MainFrame
        sideMenu={
          <React.Fragment>
            <List><HomeListItems /></List>
            <HasPerm permission={['member.view_dashboard', 'member.view_member', 'member.view_salesperson', 'org.view_organization']}>
              <Divider />
              <List><MemberListItems /></List>
            </HasPerm>
            <HasPerm permission={['partner.view_partnercompany', 'partner.view_partnermember', 'partner.view_partnerorder']}>
              <Divider />
              <List><PartnerListItems /></List>
            </HasPerm>
            <HasPerm permission={['project.view_project', 'client.view_clientcompany']}>
              <Divider />
              <List><ProjectListItems /></List>
            </HasPerm>
            <HasPerm permission={['contract.view_contract', 'contract.view_member']}>
              <Divider />
              <List><ContractListItems /></List>
            </HasPerm>
            <HasPerm permission={['turnover.view_turnover']}>
              <Divider />
              <List><TurnoverListItems /></List>
            </HasPerm>
            {user.is_staff ? (
              <>
                <Divider />
                <List><SettingListItems /></List>
              </>
            ) : null}
          </React.Fragment>
        }
        appName={config.systemName}
      >
        <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/account' component={AccountRouter} />
          <Route path='/contract' component={ContractRouter} />
          <Route path='/member' component={MemberRouter} />
          <Route path='/partner' component={PartnerRouter} />
          <Route path='/organization' component={OrganizationRouter} />
          <Route path='/client' component={ClientRouter} />
          <Route path='/project' component={ProjectRouter} />
          <Route path='/turnover' component={TurnoverRouter} />
          <Route component={HttpNotFound} />
        </Switch>
      </MainFrame>
    );
  };
}

const AccountRouter = () => (
  <Switch>
    <Route path='/account/mypage' component={MyPage} />
    <Route component={HttpNotFound} />
  </Switch>
);

const MemberRouter = () => (
  <Switch>
    <Route exact path='/member' component={MemberDashboard} />
    <Route exact path='/member/members/' component={MemberList} />
    <Route exact path='/member/members/:member_id/ct/:content_type_id' component={MemberDetail} />
    <Route exact path='/member/salesperson/' component={SalespersonList} />
    <Route exact path='/member/releases/' component={ReleaseList} />
    <Route exact path='/member/releases/:year/:month/' component={ReleaseList} />
    <Route exact path='/member/expenses' component={ExpenseMonths} />
    <Route exact path='/member/expenses/:year/:month/' component={ExpenseByMonthList} />
    <Route exact path='/member/expenses/:year/:month/ct/:content_type_id/pk/:member_id' component={ExpenseByObjectList} />
    <Route component={HttpNotFound} />
  </Switch>
);

const PartnerRouter = () => (
  <Switch>
    <Route exact path='/partner/companies/' component={PartnerCompanyList} />
    <Route exact path='/partner/companies/add' component={PartnerCompanyForm} />
    <Route exact path='/partner/companies/:company_id/' component={PartnerCompanyDetail} />
    <Route exact path='/partner/companies/:company_id/members' component={PartnerMemberList} />
    <Route exact path='/partner/companies/:company_id/members/add' component={PartnerMemberAdd} />
    <Route exact path='/partner/companies/:company_id/members/:member_id' component={PartnerMemberDetail} />
    <Route exact path='/partner/companies/:company_id/members/:member_id/contracts/add' component={PartnerContractForm} />
    <Route exact path='/partner/companies/:company_id/members/:member_id/contracts/:contract_id/form' component={PartnerContractForm} />
    <Route exact path='/partner/companies/:company_id/members/:member_id/contracts/:contract_id/detail' component={PartnerContractDetail} />
    <Route exact path='/partner/companies/:company_id/members/:member_id/orders/:order_id' component={PartnerOrderDetail} />
    <Route exact path='/partner/companies/:company_id/orders/:order_id' component={PartnerOrderDetail} />
    <Route exact path='/partner/companies/:company_id/bank-accounts/' component={PartnerCompanyBankAccounts} />
    <Route exact path='/partner/companies/:company_id/payment-notice/:year/:month/divisions/:org_id' component={PartnerCompanyPaymentNotice} />
    <Route exact path='/partner/individual/' component={IndividualMembers} />
    <Route exact path='/partner/individual/add' component={IndividualMemberAdd} />
    <Route exact path='/partner/individual/:member_id/contracts/add' component={PartnerContractForm} />
    <Route exact path='/partner/individual/:member_id/contracts/:contract_id/detail' component={PartnerContractDetail} />
    <Route exact path='/partner/individual/:member_id/contracts/:contract_id/form' component={PartnerContractForm} />
    <Route exact path='/partner/individual/:member_id' component={IndividualMember} />
    <Route exact path='/partner/individual/:member_id/payment-notice/:year/:month/divisions/:org_id' component={IndividualPaymentNotice} />
    <Route exact path='/partner/monthly-orders' component={PartnerMonthlyOrders} />
    <Route exact path='/partner/partner-requests' component={PartnerMonthlyRequests} />
    <Route exact path='/partner/orders-status/year/:year/month/:month' component={PartnerOrderList} />
    <Route exact path='/partner/requests/year/:year/month/:month' component={PartnerRequestList} />
    <Route component={HttpNotFound} />
  </Switch>
);

const ContractRouter = () => (
  <Switch>
    <Route exact path='/contract' component={ContractDashboard} />
    <Route exact path='/contract/members/add' component={ContractMemberAdd} />
    <Route exact path='/contract/members' component={ContractMemberList} />
    <Route exact path='/contract/members/:member_id/' component={ContractMemberDetail} />
    <Route exact path='/contract/members/:member_id/contracts/add' component={ContractAdd} />
    <Route exact path='/contract/members/:member_id/contracts/:contract_id' component={ContractDetail} />
    <Route exact path='/contract/members/:member_id/contracts/:contract_id/employment' component={ContractEmployment} />
    <Route exact path='/contract/members/:member_id/contracts/:contract_id/incumbency' component={ContractIncumbency} />
    <Route exact path='/contract/members/:member_id/contracts/:contract_id/income' component={ContractIncome} />
    <Route exact path='/contract/contracts/auto-update-list' component={ContractAutoUpdateList} />
    <Route exact path='/contract/contracts/expire-list' component={ContractExpireList} />
    <Route exact path='/contract/commutations' component={CommutationList} />
    <Route exact path='/contract/commutations/year/:year/month/:month' component={CommutationList} />
    <Route exact path='/contract/members/salary/upload' component={SalaryUpload} />
    <Route exact path='/contract/paid-vacations' component={PaidVacationList} />
    <Route component={HttpNotFound} />
  </Switch>
);

const OrganizationRouter = () => (
  <Switch>
    <Route exact path='/organization' component={OrganizationList} />
    <Route exact path='/organization/add' component={OrganizationForm} />
    <Route exact path='/organization/:org_id/' component={OrganizationDetail} />
    <Route exact path='/organization/:org_id/edit/' component={OrganizationForm} />
    <Route exact path='/organization/:org_id/members/' component={OrgMemberList} />
    <Route exact path='/organization/:org_id/positions/' component={OrgPositionList} />
    <Route exact path='/organization/:org_id/attendances/:year/:month/' component={OrgAttendanceByMonth} />
    <Route component={HttpNotFound} />
  </Switch>
);

const ClientRouter = () => (
  <Switch>
    <Route exact path='/client/companies' component={ClientCompanyList} />
    <Route exact path='/client/companies/add' component={ClientCompanyForm} />
    <Route exact path='/client/companies/:company_id' component={ClientCompanyDetail} />
    <Route component={HttpNotFound} />
  </Switch>
);

const ProjectRouter = () => (
  <Switch>
    <Route exact path='/project/projects' component={ProjectList} />
    <Route exact path='/project/projects/add' component={ProjectAdd} />
    <Route exact path='/project/projects/:project_id' component={ProjectDetial} />
    <Route exact path='/project/projects/:project_id/members' component={ProjectMemberList} />
    <Route exact path='/project/projects/:project_id/members/add' component={ProjectMemberAdd} />
    <Route exact path='/project/projects/:project_id/members/:member_id' component={ProjectMemberDetail} />
    <Route exact path='/project/projects/:project_id/members/:member_id/contracts/add' component={ProjectMemberContractForm} />
    <Route exact path='/project/projects/:project_id/members/:member_id/contracts/:contract_id/form' component={ProjectMemberContractForm} />
    <Route exact path='/project/projects/:project_id/members/:member_id/duplications' component={ProjectMemberDuplications} />
    <Route exact path='/project/projects/:project_id/attendance/:year/:month' component={ProjectAttendance} />
    <Route exact path='/project/projects/:project_id/orders/add' component={ProjectOrderAdd} />
    <Route exact path='/project/projects/:project_id/orders/:order_id' component={ProjectOrderDetail} />
    <Route exact path='/project/projects/:project_id/requests/:request_id/' component={ProjectRequest} />
    <Route exact path='/project/projects/:project_id/subcontractors' component={SubcontractorList} />
    <Route exact path='/project/projects/:project_id/subcontractors/add' component={SubcontractorForm} />
    <Route exact path='/project/projects/:project_id/subcontractors/:subcontractor_id/contracts/:contract_id' component={SubcontractorContractForm} />
    <Route exact path='/project/projects/:project_id/contracts' component={ProjectContractList} />
    <Route exact path='/project/projects/:project_id/contracts/add' component={ProjectContractForm} />
    <Route exact path='/project/projects/:project_id/contracts/:contract_id/change' component={ProjectContractForm} />
    <Route component={HttpNotFound} />
  </Switch>
);

const TurnoverRouter = () => (
  <Switch>
    <Route exact path='/turnover' component={TurnoverDashBoard} />
    <Route exact path='/turnover/members/monthly' component={MembersMonthlyTurnover} />
    <Route exact path='/turnover/members/year/:year/month/:month' component={MembersTurnoverByMonth} />
    <Route exact path='/turnover/members/fiscal-year/:year/' component={MembersTurnoverByFiscalYear} />
    <Route component={HttpNotFound} />
  </Switch>
)

MainLayout.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(MainLayout);
