import React from 'react';
import { Link } from 'react-router-dom'
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Button, 
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions, 
  List, 
  ListItem, 
  ListItemText, 
  Divider,
  MenuItem,
  CircularProgress, 
} from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { EnhancedTable } from '../../datatable/index';
import {
  TopBreadcrumbs,
  Card,
  CardAvatar,
  CardHeader,
  CardBody,
  DataSource,
  Toast,
} from '../../components';
import {
  getMemberInfo,
  getContractTimeline,
  handleAddObject,
  handleEditObject,
  handleDeleteObject,
  getOrganizationChoices,
  downloadWorkerRoster,
} from '../utils/api';
import { TableDetail } from '../../datatable';
import {
  member_detail_schema,
  member_form_basic_schema,
  member_edit_layout,
  member_emergency_contact_list_schema,
  member_emergency_contact_form_schema,
  member_family_list_schema,
  member_family_form_schema,
  member_family_form_layout,
  member_residence_list_schema,
  member_residence_form_schema,
  member_residence_form_layout,
  list_member_organization_schema,
  member_organization_form_schema,
  member_education_list_schema,
  member_education_form_schema,
} from './schema';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { ajax, common } from '../../utils/common';
import { config } from '../../utils/config';
import { constant } from '../../utils/constants';
import AvatarEditor from '../../components/ImageEditor';
import BankAccountList from '../common/bank_account_list';

import defaultAvatar from "../../assets/img/avatar.png";
import { HasPerm } from '../../components';
import { onBlurPostcode } from '../utils';

const styles = (theme) => ({
  cardProfile: {
    marginTop: 60,
  },
  cardAvatar: {
    width: 110,
    height: 110,
    position: 'relative',
  },
  cardAvatarSpan: {
    display: 'inline-block',
    height: '100%',
    verticalAlign: 'middle',
  },
  cardImage: {
    maxHeight: 110,
    maxWidth: 110,
    verticalAlign: 'middle'
  },
  button: {
    marginRight: theme.spacing(1),
  },
  changeAvatar: {
    position: 'absolute',
    bottom: 0,
    width: 110,
    backgroundColor: 'rgba(255,255,255, 0.5)',
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
  fullScreen: {
    [theme.breakpoints.down('xs')]: {
      width: '100%',
      height: '100%',
      margin: 0,
      maxWidth: '100%',
      maxHeight: 'none',
      borderRadius: 0,
    },
  },
});

class ContractMemberDetail extends React.Component {
  member_form_basic = common.clone(member_form_basic_schema);
  member_organization_form_schema = common.clone(member_organization_form_schema);

  constructor(props) {
    super(props);

    this.onSaveAvatar = this.onSaveAvatar.bind(this);
    this.state = {
      member: {},
      open: false,
      avatar_url: null,
      timelines: [],
      emergency_reload: false,
      family_reload: false,
      residence_reload: false,
      education_reload: false,
      organizations: [],
      org_reload: false,
      loading: false,
    };
    this.initialize(props.match.params);
    this.detail_api = common.formatStr(
      config.api.member.detail,
      props.match.params.member_id
    );
  }

  initialize = (params) => {
    getMemberInfo(params.member_id).then(member => {
      this.setState({member});
    });
    getContractTimeline(params.member_id).then(data => {
      this.setState({timelines: data.results});
    });
    getOrganizationChoices().then(organizations => this.setState({ organizations }));
  };

  componentDidUpdate = () => {
    document.title = `${this.state.member.full_name || ''} | 契約管理`
  };

  onEditMember = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(member => {
      this.setState({member});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteMember = () => {
    ajax.fetchDelete(this.detail_api).then(data => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect('/contract/members')
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  handleCloseAvatar = () => {
    this.setState({open: false});
  };

  handleSaveAvatar = () => {
    if (this.__getAvatarImage) {
      const self = this;
      const canvas = this.__getAvatarImage();
      if (canvas) {
        canvas.canvas.toBlob(function(blob) {
          self.onSaveAvatar(blob, canvas.type);
        }, canvas.type);
      }
    }
  }

  onSaveAvatar = (blob, content_type) => {
    const { params } = this.props.match;
    ajax.fetchPut(
      common.formatStr(config.api.member.avatar, params.member_id),
      {blob, type: content_type},
    ).then(json => {
      Toast.success(constant.SUCCESS.UPLOADED);
      this.handleCloseAvatar();
      this.setState({avatar_url: json.url});
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  handleDownloadWorkerRoster = () => {
    const { member_id } = this.props.match.params;
    this.setState({loading: true});
    return downloadWorkerRoster(member_id).then(json => {
      const blob = common.toBlob(json.blob);
      common.downloadBlog(blob, json.name);
      if (this._closeMenu) {
        this._closeMenu();
      }
    }).catch(errors => {
      Toast.error(errors.detail);
    }).finally(() => {
      this.setState({loading: false});
    });
  };

  render() {
    const { classes } = this.props;
    const { member_id } = this.props.match.params;
    const {
      open, member, avatar_url, timelines,
      emergency_reload,
      family_reload,
      residence_reload,
      education_reload,
      organizations,
      org_reload,
      loading,
    } = this.state;
    common.getFromJsonList(this.member_form_basic, 'name', 'code').read_only = true;
    const avatar = avatar_url || member.avatar || defaultAvatar;

    // 所属部署の設定
    common.getFromJsonList(this.member_organization_form_schema, 'name', 'organization').choices = organizations;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/contract', name: '契約管理' },
            { link: '/contract/members', name: '社員一覧' },
            { name: member.full_name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={8} lg={8}>
              <TableDetail
                data={member}
                schema={member_detail_schema}
                editProps={{
                  title: '社員情報変更',
                  schema: this.member_form_basic,
                  layout: member_edit_layout,
                  handleEdit: this.onEditMember,
                  visible: common.hasPerm('contract.change_member'),
                  onBlurs: [onBlurPostcode],
                }}
                deleteProps={{
                  handleDelete: this.onDeleteMember,
                  visible: common.hasPerm('contract.delete_member'),
                }}
                cardMenuItems={[
                  <MenuItem
                    key='1'
                    disabled={loading}
                    onClick={this.handleDownloadWorkerRoster}
                  >
                    労働者名簿
                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                  </MenuItem>,
                ]}
                innerRef={(component => this._closeMenu = component && component.handleCloseMenu)}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={4}>
              <Card profile className={classes.cardProfile}>
                <CardAvatar profile className={classes.cardAvatar}>
                  <span className={classes.cardAvatarSpan}></span>
                  <a href='#img' onClick={e => e.preventDefault()}>
                    <img src={avatar} alt="..." className={classes.cardImage} />
                  </a>
                  <HasPerm permission='contract.change_member'>
                    <div className={classes.changeAvatar}>
                      <Button
                        fullWidth
                        style={{padding: '0px 8px', fontSize: 12}}
                        onClick={() => this.setState({open: true})}
                      >
                        変更
                      </Button>
                    </div>
                  </HasPerm>
                </CardAvatar>
                <CardBody>
                  <List>
                    <ListItem>
                      {timelines.length > 0 ? (
                        <ListItemText>
                          契約情報
                        </ListItemText>
                      ) : (
                        <HasPerm permission='contract.add_contract'>
                          <ListItemText
                            primary='契約情報なし'
                            secondary='「契約追加」ボタンをクリックし、契約を追加してください。'
                          />
                        </HasPerm>
                      )}
                    </ListItem>
                    <HasPerm permission='contract.view_contract'>
                      {timelines.map((c, key) => (
                        <React.Fragment key={key}>
                          <Divider variant="middle" component="li" />
                          <ListItem
                            button
                            component={Link}
                            to={`/contract/members/${member_id}/contracts/${c.id}`}
                          >
                            <ListItemText
                              primary={c.contract_no}
                              secondary={`${c.start_date}〜${c.end_date} （月給:${common.toNumComma(c.amount)}）`}
                            />
                          </ListItem>
                        </React.Fragment>
                      ))}
                    </HasPerm>
                    <HasPerm permission='contract.add_contract'>
                      <ListItem>
                        <Link to={`/contract/members/${member_id}/contracts/add`}>
                          <Button variant="contained" color="primary">契約追加</Button>
                        </Link>
                      </ListItem>
                    </HasPerm>
                  </List>
                </CardBody>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
        {member.content_type ? (
          <HasPerm permission='org.view_organizationmember'>
            <Card>
              <CardHeader color='info'>所属一覧</CardHeader>
              <CardBody>
                <DataSource
                  reload={org_reload}
                  endpoint={common.formatStr(config.api.member.member_organizations, member.content_type, member_id)}
                  render={(initData) => (
                    <EnhancedTable
                      title='所属一覧'
                      tableHead={list_member_organization_schema}
                      tableData={initData}
                      rowsPerPage={config.rowsPerPage}
                      selectable='single'
                      addProps={{
                        title: '所属情報追加',
                        schema: this.member_organization_form_schema,
                        handleOk: handleAddObject(
                          () => this.setState({ org_reload: !org_reload }),
                          config.api.organization.member_organization_add,
                        ),
                        data: {
                          content_type: member.content_type,
                          object_id: member_id,
                        },
                        visible: common.hasPerm('org.add_organizationmember'),
                      }}
                      editProps={{
                        title: '所属情報変更',
                        schema: this.member_organization_form_schema,
                        handleOk: handleEditObject(
                          () => this.setState({ org_reload: !org_reload }),
                          config.api.organization.member_organization_detail,
                        ),
                        visible: common.hasPerm('org.change_organizationmember'),
                      }}
                      deleteProps={{
                        handleDelete: handleDeleteObject(
                          () => this.setState({ org_reload: !org_reload }),
                          config.api.organization.member_organization_detail,
                        ),
                        visible: common.hasPerm('org.delete_organizationmember'),
                      }}
                      pushpinTop={common.getHeaderHeight()}
                    />
                  )}
                />
              </CardBody>
            </Card>
          </HasPerm>
        ) : null}
        <Card>
          <CardHeader color="info">
            緊急連絡先
          </CardHeader>
          <CardBody>
            <DataSource
              reload={emergency_reload}
              endpoint={ common.formatStr(config.api.contract.member_emergency_contact_list, member_id) }
              render={ (initData) => (
                <EnhancedTable
                  title='緊急連絡先'
                  tableHead={member_emergency_contact_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  selectable='single'
                  addProps={{
                    title: '緊急連絡先追加',
                    schema: member_emergency_contact_form_schema,
                    handleOk: handleAddObject(
                      () => this.setState({emergency_reload: !emergency_reload}),
                      config.api.contract.member_emergency_contact_list,
                      member_id
                    ),
                    data: {
                      'content_type': member.content_type,
                      'object_id': member_id,
                    },
                  }}
                  editProps={{
                    title: '緊急連絡先変更',
                    schema: member_emergency_contact_form_schema,
                    handleOk: handleEditObject(
                      () => this.setState({emergency_reload: !emergency_reload}),
                      config.api.contract.member_emergency_contact_detail,
                      member_id
                    ),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({emergency_reload: !emergency_reload}),
                      config.api.contract.member_emergency_contact_detail,
                      member_id
                    ),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 800 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
        <Card>
          <CardHeader color="info">
            家族情報
          </CardHeader>
          <CardBody>
            <DataSource
              reload={family_reload}
              endpoint={ common.formatStr(config.api.contract.member_family_list, member_id) }
              render={ (initData) => (
                <EnhancedTable
                  title='家族情報'
                  tableHead={member_family_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  selectable='single'
                  addProps={{
                    title: '家族情報追加',
                    schema: member_family_form_schema,
                    layout: member_family_form_layout,
                    handleOk: handleAddObject(
                      () => this.setState({family_reload: !family_reload}),
                      config.api.contract.member_family_list,
                      member_id
                    ),
                    data: {
                      'content_type': member.content_type,
                      'object_id': member_id,
                    },
                  }}
                  editProps={{
                    title: '家族情報変更',
                    schema: member_family_form_schema,
                    layout: member_family_form_layout,
                    handleOk: handleEditObject(
                      () => this.setState({family_reload: !family_reload}),
                      config.api.contract.member_family_detail,
                      member_id
                    ),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({family_reload: !family_reload}),
                      config.api.contract.member_family_detail,
                      member_id
                    ),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 700 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
        <Card>
          <CardHeader color="info">
            在留資格
          </CardHeader>
          <CardBody>
            <DataSource
              reload={residence_reload}
              endpoint={ common.formatStr(config.api.contract.member_residence_list, member_id) }
              render={ (initData) => (
                <EnhancedTable
                  title='在留資格'
                  tableHead={member_residence_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  selectable='single'
                  addProps={{
                    title: '在留資格追加',
                    schema: member_residence_form_schema,
                    layout: member_residence_form_layout,
                    handleOk: handleAddObject(
                      () => this.setState({residence_reload: !residence_reload}),
                      config.api.contract.member_residence_list,
                      member_id
                    ),
                    data: {
                      'content_type': member.content_type,
                      'object_id': member_id,
                    },
                  }}
                  editProps={{
                    title: '在留資格変更',
                    schema: member_residence_form_schema,
                    layout: member_residence_form_layout,
                    handleOk: handleEditObject(
                      () => this.setState({residence_reload: !residence_reload}),
                      config.api.contract.member_residence_detail,
                      member_id
                    ),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({residence_reload: !residence_reload}),
                      config.api.contract.member_residence_detail,
                      member_id
                    ),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 750 }}
                />
              ) }
            />
          </CardBody>
        </Card>
        { member.content_type ? (
          <BankAccountList
            title={`${member.full_name} 口座一覧`}
            content_type={member.content_type}
            object_id={member_id}
          />
        ) : null}
        <Card>
          <CardHeader color="info">
            学歴一覧
          </CardHeader>
          <CardBody>
            <DataSource
              reload={education_reload}
              endpoint={ common.formatStr(config.api.member.education_list, member_id) }
              render={ (initData) => (
                <EnhancedTable
                  title='学歴'
                  tableHead={member_education_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  selectable='single'
                  addProps={{
                    title: '学歴追加',
                    schema: member_education_form_schema,
                    handleOk: handleAddObject(
                      () => this.setState({education_reload: !education_reload}),
                      config.api.member.education_list,
                      member_id
                    ),
                    data: {
                      'content_type': member.content_type,
                      'object_id': member_id,
                    },
                  }}
                  editProps={{
                    title: '学歴変更',
                    schema: member_education_form_schema,
                    handleOk: handleEditObject(
                      () => this.setState({education_reload: !education_reload}),
                      config.api.member.education_detail,
                      member_id
                    ),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({education_reload: !education_reload}),
                      config.api.member.education_detail,
                      member_id
                    ),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                />
              ) } 
            />
          </CardBody>
        </Card>
        <Dialog
          open={open}
          onClose={this.handleCloseAvatar}
          PaperProps={{className: classes.fullScreen}}
        >
          <DialogTitle>
            写真変更する
          </DialogTitle>
          <DialogContent>
            <AvatarEditor
              ref={editor => { this.__getAvatarImage = editor && editor.getImage }}
            />
          </DialogContent>
          <DialogActions>
            <Button color="secondary" onClick={this.handleCloseAvatar}>取消</Button>
            <Button color="primary" onClick={this.handleSaveAvatar}>確定</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default withStyles(styles)(ContractMemberDetail);
