import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import { EnhancedTable } from '../../datatable/index';
import {
  TopBreadcrumbs,
  DataSource,
  Card,
  CardHeader,
  CardBody,
  withPermissions,
  Toast,
} from '../../components';
import { config } from '../../utils/config';
import {
  list_member_schema,
  member_list_filter_layout,
} from './schema';
import { common } from '../../utils';
import {
  getOrganizationChoices,
  downloadWorkerRosterList,
} from '../utils';

class ContractMemberList extends React.Component {
  title = '社員一覧'
  member_list_schema = list_member_schema;

  constructor(props) {
    super(props);

    this.state = {
      org_choices: [],
    };
    this.initialize();
  }

  initialize = () => {
    getOrganizationChoices().then(org_choices => {
      this.setState({org_choices});
    });
  };

  componentDidMount = () => {
    document.title = `${this.title} | 契約管理`;
  };

  handleDownloadWorkerRoster = (data) => {
    if (Array.isArray(data) && data.length > 0) {
      const member_id_list = [];
      data.map(item => member_id_list.push(item.id));
      return downloadWorkerRosterList(member_id_list).then(json => {
        const blob = common.toBlob(json.blob);
        common.downloadBlog(blob, json.name);
      }).catch(errors => {
        Toast.error(errors.detail);
      });
    }
  };

  render() {
    const { org_choices } = this.state;
    common.getFromJsonList(this.member_list_schema, 'name', 'organization_id')['choices'] = org_choices;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/contract', name: '契約管理'},
            { name: this.title },
          ]}
        />
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={ config.api.contract.member_list }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  storageKey='contract_members'
                  tableHead={this.member_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  urlReflect={false}
                  tableActions={[
                    {
                      'tooltip': '社員追加',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect('/contract/members/add')),
                      'visible': common.hasPerm('contract.add_member'),
                    },
                    // {
                    //   'tooltip': '口座情報をダウンロード',
                    //   'icon': <AssignmentIndIcon />,
                    //   'handleClick': this.handleDownloadWorkerRoster,
                    //   "showLoading": true,
                    // },
                    {
                      'tooltip': '労働者名簿をダウンロード',
                      'icon': <AssignmentIndIcon />,
                      'handleClick': this.handleDownloadWorkerRoster,
                      "showLoading": true,
                    },
                  ]}
                  pushpinTop={common.getHeaderHeight()}
                  filterLayout={member_list_filter_layout}
                  tableStyles={{ minWidth: 1000 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(ContractMemberList, 'contract.view_member');
