import React from 'react';
import CommonMemberAdd from './common_member_add';
import { withPermissions } from '../../components';
import {
  getPartnerCompany,
} from '../utils/api';
import { common } from '../../utils/common';
import { config } from '../../utils/config';

class PartnerMemberAdd extends React.Component {
  title = '社員追加';

  constructor(props) {
    super(props);

    this.state = {
      company: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id } = props.match.params;
    getPartnerCompany(company_id).then(company => {
      this.setState({company});
    });
  };

  componentDidUpdate = () => {
    const { company } = this.state;
    document.title = `${this.title} | ${company.name} | 協力会社`;
  };

  render() {
    const { company_id } = this.props.match.params;
    const { company } = this.state;

    return (
      <div>
        <CommonMemberAdd
          company_id={company_id}
          breadcrumbs={[
            { link: `/partner/companies?is_retired=false`, name: '協力会社一覧' },
            { link: `/partner/companies/${company_id}/`, name: company.name },
            { link: `/partner/companies/${company_id}/members`, name: '社員一覧' },
            { name: this.title },
          ]}
          endpoint={common.formatStr(config.api.partner.add_from_partner, company_id)}
          callback={(data) => common.redirect(`/partner/companies/${company_id}/members/${data.id}`)}
        />
      </div>
    );
  }
}

export default withPermissions(PartnerMemberAdd, 'partner.add_partnermember');
