import React from 'react';
import PropTypes from 'prop-types';
import {
  withStyles,
  Card,
  CardContent,
  CardHeader,
  CardActions,
  Button,
} from '@material-ui/core';
import GridContainer from './Grid/GridContainer';
import GridItem from './Grid/GridItem';
import { Form, ConfirmDialog } from '../datatable';
import { ajax } from '../utils/common';
import { constant } from '../utils/constants';
import { Toast } from '.';

const styles = (theme) => ({
  paper: {
  },
  buttons: {
    textAlign: 'right',
    flexGrow: 1,
  },
  button: {
    margin: theme.spacing(1),
  },
  cardActions: {
    padding: theme.spacing(2),
  }
});

class ActionForm extends React.Component {

  constructor(props) {
    super(props);

    this.handleOk = this.handleOk.bind(this);
    this.state = {
      data: props.data,
      errors: props.errors || {},
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (JSON.stringify(this.props.data) !== JSON.stringify(nextProps.data)) {
      this.setState({data: nextProps.data});
    }
  }

  handleOk = () => {
    if (this.__clean) {
      const formData = this.__clean();
      if (formData) {
        const { methodName, saveEndpoint, saveCallback } = this.props;
        ajax.getMethod(methodName)(saveEndpoint, formData).then(json => {
          Toast.success(constant.SUCCESS.SAVED);
          if (saveCallback) {
            saveCallback(json);
          }
        }).catch(errors => {
          this.setState({errors});
          if ('detail' in errors) {
            Toast.error(errors.detail);
          } else {
            Toast.error(constant.ERROR.FORM_CHECK_ERROR);
          }
        });
      } else {
        Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      }
    }
  };

  handleDelete = () => {
    const { deleteEndpoint, deleteCallback } = this.props;
    ajax.fetchDelete(deleteEndpoint).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      if (deleteCallback) {
        deleteCallback();
      }
    }).catch(errors => {
      this.setState({errors});
      Toast.error(errors.detail || constant.ERROR.DELETED_FAILURE);
    });
  };

  render() {
    const { classes, title, deleteEndpoint, ...formProps } = this.props;
    const { data, errors } = this.state;

    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            {title ? (
              <CardHeader
                title={title}
              />
            ) : null}
            <CardContent>
              <Form
                ref={(form) => {
                  this.__clean = form && form.clean;
                  this.__toastError = form && form.toastError;
                }}
                {...formProps}
                data={data}
                errors={errors}
              />
            </CardContent>
            <CardActions className={classes.cardActions}>
              <div className={classes.buttons}>
                {deleteEndpoint ? (
                  <Button
                    className={classes.button}
                    onClick={() => this.__confirm ? this.__confirm() : null} 
                    variant="contained" 
                    color="secondary"
                  >
                    削除
                  </Button>
                ) : null}
                <Button
                  onClick={this.handleOk} 
                  variant="contained" 
                  color="primary"
                  autoFocus={true}
                >
                  保存
                </Button>
              </div>
            </CardActions>
          </Card>
        </GridItem>
        <ConfirmDialog
          title='削除してもよろしいですか。'
          innerRef={dlg => { this.__confirm = dlg && dlg.handleOpen }}
          onOk={this.handleDelete}
        />
      </GridContainer>
    );
  }
}

ActionForm.propTypes = {
  classes: PropTypes.object.isRequired,
  title: PropTypes.string,
  data: PropTypes.object,
  methodName: PropTypes.oneOf(['POST', 'PUT', 'DELETE']).isRequired,
  saveEndpoint: PropTypes.string.isRequired,
  saveCallback: PropTypes.func,
  deleteEndpoint: PropTypes.string,
  deleteCallback: PropTypes.func,
}

export default withStyles(styles)(ActionForm);