import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  Badge,
  ClickAwayListener,
  Avatar,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import NotificationsIcon from '@material-ui/icons/Notifications';
import AccountCircle from '@material-ui/icons/AccountCircle';
import AccountInfo from '../containers/AccountInfo';
import { Notifications } from '../containers';

class TopBar extends React.Component {

  constructor(props) {
    super(props);

    this.handleMenuClick = this.handleMenuClick.bind(this);
  }

  componentDidMount = () => {
    window.appName = this.props.appName;
    document.title = this.props.appName;
  };

  handleMenuClick = () => {
    this.props.handleDrawerOpen();
  };

  handleShowNotifications = (event) => {
    if (this._openNotifications) {
      this._openNotifications(this.toobarEl);
    }
  };

  handleShowAccountInfo = (event) => {
    if (this._openAccountInfo) {
      this._openAccountInfo(this.toobarEl);
    }
  };

  handleAwayFromAccountInfo = () => {
    if (this._closeAccountInfo) {
      this._closeAccountInfo();
    }
  };

  render() {
    const { classes, loggedIn, user, appName, open } = this.props;

    return (
      <AppBar
        position="fixed"
        className={classNames(classes.appBar, open && classes.appBarShift)}
      >
        <Toolbar disableGutters={!open} style={{paddingRight: 24}} ref={bar => (this.toobarEl = bar)}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={this.handleMenuClick}
            className={classNames(classes.menuButton, open && classes.hide)}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" color="inherit" noWrap className={classes.grow}>
            {appName}
          </Typography>
          { loggedIn ? (
            <div style={{ whiteSpace: 'nowrap' }}>
              <span style={{float: 'left'}}>
                <IconButton color="inherit" onClick={this.handleShowNotifications}>
                  {user.notifications && user.notifications.filter(i => i['is_read'] === false).length > 0 ? (
                    <Badge badgeContent={user.notifications.filter(i => i['is_read'] === false).length} color="secondary">
                      <NotificationsIcon />
                    </Badge>
                  ) : (
                    <NotificationsIcon />
                  )}
                </IconButton>
                <Notifications
                  notifications={user.notifications}
                  innerRef={popper => {
                    this._openNotifications = popper && popper.handleOpen;
                  }}
                />
              </span>
              <ClickAwayListener onClickAway={this.handleAwayFromAccountInfo}>
                <span>
                  {user.me.avatar ? (
                    <IconButton color="inherit" style={{padding: 9}} onClick={this.handleShowAccountInfo}>
                      <Avatar aria-label="Recipe" className={classes.avatar} style={{width: 30, height: 30}}>
                        <img src={user.me.avatar} alt='Avatar' width={30} />
                      </Avatar>
                    </IconButton>
                  ) : (
                    <IconButton color="inherit" onClick={this.handleShowAccountInfo}>
                      <AccountCircle />
                    </IconButton>
                  )}
                  <AccountInfo
                    innerRef={popper => {
                      this._openAccountInfo = popper && popper.handleOpen;
                      this._closeAccountInfo = popper && popper.handleClose;
                    }}
                  />
                </span>
              </ClickAwayListener>
            </div>
          ) : (
            <Button component={Link} color="inherit" to="/login">Login</Button>
          )}
        </Toolbar>
      </AppBar>
    );
  }
}

TopBar.propTypes = {
  classes: PropTypes.object.isRequired,
  appName: PropTypes.string.isRequired,
};

export default TopBar;
