import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import {
  Card,
  CardHeader,
  CardBody,
  withPermissions,
  DataSource,
} from "../../components";
import {
  list_company_schema,
  company_list_search_layout,
} from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';

class PartnerCompanyList extends React.Component {
  title = '協力会社一覧';

  componentDidMount = () => {
    document.title = this.title;
  }

  render () {
    const filters = {};

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={ config.api.partner.list }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  storageKey='partner.companies'
                  tableHead={list_company_schema}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '協力会社を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect('/partner/companies/add')),
                      'visible': common.hasPerm('partner.add_partnercompany'),
                    }
                  ]}
                  filters={filters}
                  filterLayout={company_list_search_layout}
                  allowCsv={true}
                  urlReflect={false}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 1000 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(PartnerCompanyList, 'partner.view_partnercompany');
