import React from 'react';
import { Button, Tooltip } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import {
  TopBreadcrumbs,
  Card,
  CardHeader,
  CardBody,
  DataSource,
  withPermissions,
  HasPerm,
  Toast,
} from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  getProjectDetail,
  getMemberCategory,
  getProjectMemberDetial,
  onDeleteObject,
  getDuplicatedProjectMember,
} from '../utils/api';
import { config } from '../../utils/config';
import { common, ajax, constant } from '../../utils';
import { TableDetail, EnhancedTable } from '../../datatable';
import {
  project_member_detail_schema,
  proejct_member_form_schema,
  project_member_contract_list_schema,
} from './schema';
import ExpensesList from '../common/expense_list';

class ProjectMemberDetail extends React.Component {
  project_member_schema = JSON.parse(JSON.stringify(proejct_member_form_schema));

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      member: {},
      member_categories: [],
      is_duplicated: false,
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.project.member_detail,
      props.match.params.project_id,
      props.match.params.member_id,
    );
  }

  initialize = (props) => {
    const { project_id, member_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
    });
    getProjectMemberDetial(project_id, member_id).then(member => {
      this.setState({member});
    });
    getMemberCategory().then(member_categories => {
      this.setState({member_categories});
    });
    getDuplicatedProjectMember(project_id, member_id).then(duplications => {
      this.setState({is_duplicated: duplications.length > 0});
    });
  };

  componentDidUpdate = () => {
    const { project, member } = this.state;
    document.title = `${member.full_name || ''} | ${project.name || ''} | 案件一覧`;
  };

  onEditProjectMember = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(member => {
      this.setState({member});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  getDetailActions = () => {
    const { project_id, member_id } = this.props.match.params;
    const { member, is_duplicated } = this.state;
    const actions = [];
    if (member.member_type === '03') {
      actions.push(
        <HasPerm permission='partner.view_partnermember' key="member_detail">
          <Button
            variant="contained"
            color="primary"
            onClick={() => common.redirect(`/partner/individual/${member.member_object_id}`)}
          >個人事業主</Button>
        </HasPerm>
      );
    }
    if (is_duplicated === true) {
      actions.push(
        <HasPerm permission='partner.view_partnermember' key="member_detail">
          <Tooltip title='同じメンバーが同じ期間中複数の案件にアサインした場合に各案件のコスト割合を指定できる' placement='bottom' enterDelay={300}>
            <Button
              variant="contained"
              color="primary"
              onClick={() => common.redirect(`/project/projects/${project_id}/members/${member_id}/duplications`)}
            >
              コスト分担
            </Button>
          </Tooltip>
        </HasPerm>
      );
    }
    return actions;
  };

  render() {
    const { project_id, member_id } = this.props.match.params;
    const { project, member, member_categories } = this.state;
    let col_member_ct = common.getFromJsonList(
      this.project_member_schema, 'name', 'member_content_type'
    )
    col_member_ct.choices = member_categories;
    col_member_ct.read_only = true;
    let col_member_id = common.getFromJsonList(
      this.project_member_schema, 'name', 'member_object_id'
    )
    col_member_id.choices = [{"value": member.member_object_id, "display_name": member.full_name}];
    col_member_id.read_only = true;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: `/project/projects`, name: '案件一覧' },
            { link: `/project/projects/${project_id}/`, name: project.name },
            { link: `/project/projects/${project_id}/members`, name: 'メンバー一覧' },
            { name: member.full_name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={12} lg={12}>
              <TableDetail
                title={member.full_name || ''}
                data={member}
                schema={project_member_detail_schema}
                editProps={{
                  title: '案件メンバー情報変更',
                  schema: this.project_member_schema,
                  handleEdit: this.onEditProjectMember,
                  visible: common.hasPerm('project.change_projectmember'),
                }}
                deleteProps={{
                  handleDelete: () => (onDeleteObject(
                    this.detail_api,
                    `/project/projects/${project_id}/members`,
                  )),
                  visible: common.hasPerm('project.delete_projectmember'),
                }}
                actions={this.getDetailActions()}
              />
            </GridItem>
          </GridContainer>
          <Card>
            <CardHeader color="info">精算条件</CardHeader>
            <CardBody>
              <DataSource
                endpoint={common.formatStr(config.api.project.member_contracts, project_id, member_id)}
                render={(initData) => (
                  <EnhancedTable
                    tableHead={project_member_contract_list_schema}
                    tableData={initData}
                    rowsPerPage={config.rowsPerPage}
                    tableActions={[
                      {
                        'tooltip': '要員を追加する',
                        'icon': <AddIcon color="secondary"/>,
                        'handleClick': () => (common.redirect(`/project/projects/${project_id}/members/${member_id}/contracts/add`)),
                      },
                    ]}
                    pushpinTop={common.getHeaderHeight()}
                  />
                )}
              />
            </CardBody>
          </Card>
          {member.member_content_type && member.member_object_id ? (
            <ExpensesList
              title='精算リスト'
              content_type={member.member_content_type}
              object_id={member.member_object_id}
              project={{
                project_id: project_id,
                project_name: project.name,
              }}
            />
          ) : null }
        </div>
      </div>
    );
  }
}

export default withPermissions(ProjectMemberDetail, 'project.view_projectmember');
