import React from 'react';
import { Paper } from '@material-ui/core';
import { DataSource } from '../../components';
import { EnhancedTable } from '../../datatable';
import { config, common } from '../../utils';
import {
  member_monthly_turnover_list_schema,
  member_fiscal_year_turnover_list_schema,
} from './schema';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class MembersMonthlyTurnover extends React.Component {
  title = '要員売上'

  componentDidMount = () => {
    document.title = this.title;
  };

  render() {
    return (
      <div>
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={ config.api.turnover.members_monthly }
            render={ (initData) => (
              <EnhancedTable
                title='要員月別売上'
                showTitle={true}
                tableHead={member_monthly_turnover_list_schema}
                tableData={initData}
                showAggregate={true}
                pushpinTop={common.getHeaderHeight()}
                allowCsv={true}
                tableStyles={{ minWidth: 800 }}
              />
            )}
          />
        </Paper>
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={ config.api.turnover.members_fiscal_year }
            render={ (initData) => (
              <EnhancedTable
                title='要員年度別売上'
                showTitle={true}
                tableHead={member_fiscal_year_turnover_list_schema}
                tableData={initData}
                showAggregate={true}
                pushpinTop={common.getHeaderHeight()}
                allowCsv={true}
                tableStyles={{ minWidth: 700 }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default MembersMonthlyTurnover;
