import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import PaymentNoticeStatus from './payment_notice_status';
import { common, config } from '../../utils';
import { withPermissions } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class PartnerRequestList extends React.Component {

  componentDidMount = () => {
    const { year, month } = this.props.match.params;
    document.title = `${year}年${month}月請求一覧`;
  };
  
  render() {
    const { year, month } = this.props.match.params;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/partner-requests', name: '請求一覧' },
            { name: `${year}年${month}月請求一覧` },
          ]}
        />
        <Paper style={paperSpacing}>
          <PaymentNoticeStatus
            title={`${year}年${month}月請求一覧`}
            endpoint={common.formatStr(config.api.partner.request_list_by_month, year, month)}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(PartnerRequestList, 'partner.view_partnerrequest');
