import React from 'react';
import PropTypes from "prop-types";
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import SendIcon from '@material-ui/icons/Send';
import DoneIcon from '@material-ui/icons/Done';
import DataSource from '../../components/DataSource';
import { EnhancedTable, FormDialog, ConfirmDialog } from '../../datatable';
import {
  partner_payment_notice_status_list_schema,
  create_partner_payment_notice_form_schema,
  create_partner_payment_notice_form_layout,
  partner_payment_notice_status_filter_layout,
} from './schema';
import { config, common, constant } from '../../utils';
import {
  getPartnerPaymentNoticeEmailData,
  sendContentObjectEmail,
  send_email_form_schema,
  send_email_form_layout,
  createPartnerPaymentNotice,
  getBankAccountList,
  receivePartnerRequest,
  onDeletePartnerRequset,
} from '../utils';
import { HasPerm, Toast } from '../../components';

class PaymentNoticeStatus extends React.Component {
  partner_payment_notice_form = common.clone(create_partner_payment_notice_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      email_schema: send_email_form_schema,
      received_data: null,
    };
  }

  showPaymentNoticeConfirm = (data, paymentNoticeCreatedCallback) => {
    if (this._showPaymentNoticeConfirm) {
      let { content_type, object_id } = this.props;
      if (!content_type || !object_id) {
        content_type = data.content_type_id;
        object_id = data.object_id;
      }
      getBankAccountList(content_type, object_id).then(bank_account_list => {
        common.getFromJsonList(
          this.partner_payment_notice_form, 'name', 'bank_account_id'
        )['choices'] = bank_account_list;
        this._showPaymentNoticeConfirm(data);
        this._paymentNoticeCreatedCallback = paymentNoticeCreatedCallback;
      });
    }
  };

  createPartnerPaymentNotice = (data, handleClose) => {
    return createPartnerPaymentNotice(data).then(json => {
      handleClose();
      Toast.success(constant.SUCCESS.PAYMENT_NOTICE_CREATED);
      if (this._paymentNoticeCreatedCallback) {
        const newSelected = Object.assign(data, json);
        this._paymentNoticeCreatedCallback(newSelected)
      }
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  handlePaymentNoticeReload = (data) => {
    this.setState({reload: !this.state.reload});
  };

  showSendEmail = (data, sendEmailCallback) => {
    if (this._showSendEmail) {
      if (data.request_id) {
        getPartnerPaymentNoticeEmailData(data.request_id).then(mail_data => {
          // 添付ファイルの選択肢を設定する
          const email_schema = common.clone(send_email_form_schema);
          common.getFromJsonList(email_schema, 'name', 'attachments')['choices'] = mail_data.attachment_choices;
          this.setState({email_schema});
          this._showSendEmail(mail_data);
          this._sendEmailCallback = sendEmailCallback;
        }).catch(errors => {
          Toast.error(errors.detail);
        });
      } else {
        Toast.error(constant.ERROR.PAYMENT_NOTICE_NOT_FOUND);
      }
    }
  };

  sendPartnerPaymentNotice = (data, handleClose) => {
    return sendContentObjectEmail(data.content_type_id, data.object_id, data).then(() => {
      handleClose();
      Toast.success(constant.SUCCESS.EMAIL_SENT);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  showReceivePartnerRequest = (data) => {
    if(this._showReceivePartnerRequest) {
      this.setState({received_data: data});
      this._showReceivePartnerRequest(
        data.is_received
         ? constant.CONFIRM.RECEIVE_PARTNER_REQUEST_UNDO
         : constant.CONFIRM.RECEIVE_PARTNER_REQUEST
      );
    }
  };

  handleReceivePartnerRequest = () => {
    const { received_data } = this.state;
    if (received_data.request_id) {
      receivePartnerRequest(received_data.request_id, !received_data.is_received).then(() => {
        Toast.success(constant.SUCCESS.SAVED);
        this.handlePaymentNoticeReload(received_data);
        this._closeReceivePartnerRequest && this._closeReceivePartnerRequest();
      }).catch(errors => (
        Toast.error(errors.detail)
      ));
    } else {
      Toast.error(constant.ERROR.REQUEST_FILE_NOT_FOUND);
    }
  };

  handleDeleteRequest = (request_id) => {
    if (request_id) {
      return onDeletePartnerRequset(request_id).then(() => {
        Toast.success(constant.SUCCESS.DELETED);
        this.setState({reload: !this.state.reload});
      }).catch((errors) => {
        Toast.error(errors.detail);
      });
    } else {
      Toast.error(constant.ERROR.REQUEST_FILE_NOT_FOUND);
      return Promise.reject();
    }
  };

  render() {
    const { title, endpoint, filters } = this.props;
    const { reload, email_schema } = this.state;

    
    return (
      <div>
        <DataSource
          reload={reload}
          endpoint={endpoint}
          render={(initData) => (
            <EnhancedTable
              title={title}
              showTitle={title ? true : false}
              storageKey='payment.notice.status'
              tableHead={partner_payment_notice_status_list_schema}
              tableData={initData}
              rowsPerPage={config.rowsPerPage}
              tableStyles={{ minWidth: 1100 }}
              rowActions={[
                {
                  'tooltip': '支払通知書／ＢＰ請求書を作成',
                  'icon': <NoteAddIcon />,
                  'handleClick': this.showPaymentNoticeConfirm,
                  'visible': common.hasPerm('partner.add_partnerrequest'),
                },
                {
                  'tooltip': '支払通知書／ＢＰ請求書を送付',
                  'icon': <SendIcon />,
                  'handleClick': this.showSendEmail,
                  'visible': common.hasPerm('partner.send_partnerrequest'),
                },
                {
                  'tooltip': '請求書を回収',
                  'icon': <DoneIcon />,
                  'handleClick': this.showReceivePartnerRequest,
                  'visible': common.hasPerm('partner.change_partnerrequest'),
                },
              ]}
              selectable="single"
              filters={filters}
              pushpinTop={common.getHeaderHeight()}
              filterLayout={partner_payment_notice_status_filter_layout}
              deleteProps={{
                handleDelete: (data) => this.handleDeleteRequest(data.request_id),
                'visible': common.hasPerm('partner.delete_partnerrequest'),
              }}
            />
          )}
        />
        <HasPerm permission='partner.add_partnerrequest'>
          <FormDialog
            innerRef={(dlg) => { this._showPaymentNoticeConfirm = dlg && dlg.handleOpen }}
            title={'支払通知書／ＢＰ請求書を作成'}
            schema={this.partner_payment_notice_form}
            layout={create_partner_payment_notice_form_layout}
            handleOk={this.createPartnerPaymentNotice}
            saveCallback={this.handlePaymentNoticeReload}
          />
        </HasPerm>
        <HasPerm permission='partner.send_partnerrequest'>
          <FormDialog
            innerRef={(dlg) => { this._showSendEmail = dlg && dlg.handleOpen }}
            title='支払通知書／ＢＰ請求書を送付'
            schema={email_schema}
            layout={send_email_form_layout}
            handleOk={this.sendPartnerPaymentNotice}
            saveCallback={this.handlePaymentNoticeReload}
          />
        </HasPerm>
        <ConfirmDialog
          title={'注文請書の回収'}
          innerRef={(dlg) => {
            this._showReceivePartnerRequest = dlg && dlg.handleOpen;
            this._closeReceivePartnerRequest = dlg && dlg.handleCancel;
          }}
          onOk={this.handleReceivePartnerRequest}
        />
      </div>
    )
  }
}

PaymentNoticeStatus.propTypes = {
  title: PropTypes.string,
  content_type: PropTypes.oneOfType([
    PropTypes.string.isRequired,
    PropTypes.number.isRequired
  ]),
  object_id: PropTypes.oneOfType([
    PropTypes.string.isRequired,
    PropTypes.number.isRequired
  ]),
  endpoint: PropTypes.string.isRequired,
  filters: PropTypes.object,
};

export default PaymentNoticeStatus;
