import React from 'react';
import withStyles from "@material-ui/core/styles/withStyles";
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { common, config, ajax, constant } from '../../utils';
import { TableDetail } from '../../datatable';
import {
  member_detail_schema,
  member_form_basic_schema,
  member_form_basic_layout,
} from './schema';
import {
  withPermissions,
  HasPerm,
  Card,
  CardBody,
  CardAvatar,
  Timeline,
  CardFooter,
  CardHeader,
  Toast,
} from '../../components';
import {
  getMemberProjectTimeline, getPartnerMemberContractTimeline,
} from '../utils/api';
import defaultAvatar from "../../assets/img/avatar.png";
import MemberRetire from '../common/member_retire';

const styles = (theme) => ({
  cardProfile: {
    marginTop: 60,
  },
  cardAvatar: {
    width: 110,
    height: 110,
    position: 'relative',
  },
  cardAvatarSpan: {
    display: 'inline-block',
    height: '100%',
    verticalAlign: 'middle',
  },
  cardImage: {
    maxHeight: 110,
    maxWidth: 110,
    verticalAlign: 'middle'
  },
  button: {
    marginRight: theme.spacing(1),
  },
});

class MemberDetail extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      member: {},
      org_reload: false,
      member_projects: [],
      member_contracts: [],
    };
    this.portal_detail_api = common.formatStr(
      config.api.member.portal_detail,
      props.match.params.content_type_id,
      props.match.params.member_id,
    );
    this.initialize(props);
  }

  initialize = (props) => {
    const { content_type_id, member_id } = props.match.params;
    ajax.fetchGet(this.portal_detail_api).then(member => {
      this.setState({ member });
      if (member.company) {
        // 個人事業主の場合memmber.companyは有効、そうでない場合は空白となる。
        getPartnerMemberContractTimeline(member.company, member_id).then(data => (
          this.setState({ member_contracts: data })
        ));
      }
    });
    getMemberProjectTimeline(content_type_id, member_id).then(member_projects => {
      this.setState({ member_projects });
    });
  };

  componentDidUpdate = () => {
    const { member } = this.state;
    document.title = `${member.full_name || ''} | メンバー一覧`;
  };

  onEditMember = (data, handleClose) => {
    return ajax.fetchPut(this.portal_detail_api, data).then(member => {
      this.setState({ member });
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  handleRetired = () => {
    const { org_reload, member } = this.state;
    member.is_on_sales = false;
    this.setState({org_reload: !org_reload, member});
  }

  render() {
    const { member_id } = this.props.match.params;
    const { classes } = this.props;
    const { member, member_projects, member_contracts } = this.state;
    const avatar = member.avatar || defaultAvatar;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/member/members?is_retired=false&is_on_sales=true', name: 'メンバー一覧' },
            { name: member.full_name },
          ]}
        />
        <div style={{ marginTop: 16 }}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={member}
                schema={member_detail_schema}
                editProps={{
                  title: '社員情報変更',
                  schema: member_form_basic_schema,
                  layout: member_form_basic_layout,
                  handleEdit: this.onEditMember,
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={3} lg={3}>
              <Card profile className={classes.cardProfile}>
                <CardAvatar profile className={classes.cardAvatar}>
                  <span className={classes.cardAvatarSpan}></span>
                  <a href='#img' onClick={e => e.preventDefault()}>
                    <img src={avatar} alt="..." className={classes.cardImage} />
                  </a>
                </CardAvatar>
                <CardBody>
                  { !common.isEmpty(member_projects) ? (
                    <Timeline items={member_projects} showTime={false} showEnd={true} />
                  ) : null }
                </CardBody>
                <CardFooter>
                  {member.member_type === '02' ? (
                    <HasPerm permission='partner.change_partnermember'>
                      <MemberRetire
                        member_id={member_id}
                        full_name={member.full_name}
                        callback={this.handleRetired}
                      />
                    </HasPerm>
                  ) : null }
                </CardFooter>
              </Card>
              {member.member_type === '02' ? (
                <Card>
                <CardHeader>
                  契約履歴
                </CardHeader>
                <CardBody>
                  { !common.isEmpty(member_contracts) ? (
                    <Timeline items={member_contracts} showTime={false} showEnd={true} />
                  ) : null }
                </CardBody>
              </Card>
            ) : null}
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(withStyles(styles)(MemberDetail), 'member.view_member');
