import { common } from "./common";
import { constant } from "./constants";

/**
 * データ期間は間違っているかどうかのチェック
 * @param {JSON} data フォームデータ
 * @param {String} start_name 期間の開始項目
 * @param {String} end_name 期間の終了項目
 */
export const checkDateContradict = (data, start_name='start_date', end_name='end_date') => {
  const start = data[start_name];
  const end = data[end_name];
  if (start > end) {
    return [{
      name: end_name,
      message: common.formatStr(constant.ERROR.DATE_CONTRADICT, start, end),
    }];
  } else {
    return true;
  }
};

/**
 * データリストの一意チェック
 * @param {Array} dataList フォームデータ
 */
export const checkUnique = (fields) => (dataList) => {
  if (fields && Array.isArray(dataList) && dataList.length > 0) {
    let vals = [];
    let results = [];
    dataList.map((data, index) => {
      let item = {};
      Array.from(fields).map(arg => (item[arg] = data[arg]));
      if (vals.indexOf(JSON.stringify(item)) >= 0) {
        Array.from(fields).map(arg => (
          results.push({
            name: arg,
            message: constant.ERROR.UNIQUE,
            index: index,
          })
        ));
      } else {
        vals.push(JSON.stringify(item));
      }
      return true;
    });
    return common.isEmpty(results) ? true : results;
  } else {
    return true;
  }
};

/**
 * 終了年月と開始年月をチェックする
 * @param {JSON} data フォームデータ
 * @param {String} start_name 期間の開始項目
 * @param {String} end_name 期間の終了項目
 */
export const checkEndYearAndMonth = (data, start_year='year', start_month='month', end_year="end_year", end_month="end_month") => {
  const start_ym = data[start_year] + data[start_month];
  const end_ym = data[end_year] + data[end_month];
  if (start_ym > end_ym) {
    return [
      {
        name: end_year,
        message: common.formatStr(constant.ERROR.DATE_CONTRADICT, start_ym, end_ym),
      },
      {
        name: end_month,
        message: common.formatStr(constant.ERROR.DATE_CONTRADICT, start_ym, end_ym),
      }
    ];
  } else {
    return true;
  }
};

export const checkAllowanceAmount = (data) => {
  if (common.getFromJsonList(data, 'code', '0001').amount === 0) {
    return [
      {
        index: 0,
        name: "amount",
        message: "基本給の金額が必須です。",
      },
    ];
  } else {
    return true;
  }
};
