import React from 'react';
import { config, common } from '../../utils';
import DataSource from '../../components/DataSource';
import { EnhancedTable } from '../../datatable';
import { monthly_requests_list_schema } from './schema';
import { withPermissions } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class PartnerMonthlyRequests extends React.Component {
  title = '協力会社と個人事業主請求一覧';

  componentDidMount = () => {
    document.title = this.title;
  }
  
  render() {
    return (
      <div>
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={config.api.partner.monthly_requests}
            render={(initData) => (
              <EnhancedTable
                title={this.title}
                showTitle={true}
                tableHead={monthly_requests_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                pushpinTop={common.getHeaderHeight()}
                tableStyles={{ minWidth: 320 }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(PartnerMonthlyRequests, 'partner.view_partnerrequest');
