import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Paper } from '@material-ui/core';
import { EnhancedTable } from '../../datatable/index';
import {
  TopBreadcrumbs,
  DataSource,
  withPermissions,
} from '../../components';
import { config } from '../../utils/config';
import { contract_auto_update_list_schema } from './schema';
import { common } from '../../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

const styles = theme => ({
  card: { ...paperSpacing },
})

class AutoUpdateList extends React.Component {
  title = '自動更新一覧';

  componentDidMount = () => {
    document.title = `${this.title} | 契約管理`
  };

  render() {
    const { classes } = this.props;
    const filters = {};

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/contract', name: '契約管理'},
            { name: this.title },
          ]}
        />
        <Paper className={classes.card}>
          <DataSource
            endpoint={ config.api.contract.auto_update_list }
            render={ (initData) => (
              <EnhancedTable
                title={this.title}
                showTitle={true}
                tableHead={contract_auto_update_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                filters={filters}
                allowCsv={true}
                urlReflect={false}
                pushpinTop={common.getHeaderHeight()}
                tableStyles={{ minWidth: 500 }}
              />
            ) } 
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(withStyles(styles)(AutoUpdateList), 'contract.view_contract');
