import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  contract_allowance_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
} from '../partner/schema';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import {
  getProjectDetail,
  getProjectContractTotal,
} from '../utils/api';
import { withPermissions } from '../../components';
import {
  default_blanket_contract_data,
  blanket_contract_form_schema,
} from './schema';
import { onChangeProjectBlanketContract } from '../utils';

class ProjectContractForm extends React.Component {
  title = '精算条件変更';

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      contract_total: default_blanket_contract_data,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id, contract_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
    });
    if (contract_id) {
      getProjectContractTotal(project_id, contract_id).then(
        contract_total => this.setState({contract_total})
      );
    }
  };

  componentDidUpdate = () => {
    const { contract_id } = this.props.match.params;
    this.title = contract_id ? '精算条件変更' : '精算条件追加'
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  render() {
    const { project_id, contract_id } = this.props.match.params;
    const { project, contract_total } = this.state;
    const breadcrumbs = [
      { link: '/project/projects', name: '案件一覧' },
      { link: `/project/projects/${project_id}/`, name: project.name },
      { link: `/project/projects/${project_id}/contracts/`, name: '一括契約一覧' },
    ];
    if (contract_id) {
      breadcrumbs.push({ name: "精算条件変更" });
    } else {
      breadcrumbs.push({ name: "精算条件追加" });
    }

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={blanket_contract_form_schema}
                onChanges={[onChangeProjectBlanketContract]}
                inlines={[
                  {
                    name: 'allowances',
                    title: '精算条件',
                    schema: contract_allowance_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: "contract_items",
                    title: "契約条件",
                    schema: bp_contract_item_schema,
                    layout: bp_contract_item_layout,
                    allowAdd: false,
                    allowDelete: false,
                  },
                ]}
                data={contract_total}
                methodName={'POST'}
                saveEndpoint={
                  contract_id ? common.formatStr(
                    config.api.project.blanket_contract_save, project_id, contract_id
                  ) : common.formatStr(
                    config.api.project.blanket_contract_add, project_id
                  )
                }
                saveCallback={() => common.redirect(`/project/projects/${project_id}/contracts`)}
                deleteEndpoint={contract_id ? common.formatStr(config.api.project.blanket_contract_detail, project_id, contract_id) : null}
                deleteCallback={() => common.redirect(`/project/projects/${project_id}/contracts`)}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(ProjectContractForm, 'project.change_projectcontract');
