import React from 'react';
import { StepForm, TopBreadcrumbs, withPermissions } from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  getMemberInfo,
  getContractItems,
  getContractAllownaces,
  getContract,
  getOrganizationChoices,
} from '../utils/api';
import {
  config,
  common,
  checkDateContradict,
  checkUnique,
  checkAllowanceAmount,
  choice_contract_comment,
} from '../../utils';
import {
  onChangeContractBasicInfo,
  onChangeContractAllowance,
} from '../utils/common';
import { 
  contract_form_basic_schema, 
  contract_form_basic_layout, 
  contract_item_schema,
  contract_item_layout,
  contract_allowance_schema,
} from './schema';

class ContractAdd extends React.Component {
  basic_info_edit_schema = common.clone(contract_form_basic_schema);
  new_contract_item_schema = common.clone(contract_item_schema);

  constructor(props) {
    super(props);

    this.state = {
      member: {},
      contract: {},
      default_contract_items: [],
      default_contract_allowances: [],
      organizations: [],
    };
    this.initialize(props.match.params);
  }

  initialize = (params) => {
    let contract_id = null;
    if (this.props.location.search) {
      const search = common.parseQueryString(this.props.location.search);
      contract_id = search && search.contract_id;
      getContract(contract_id).then(contract => {
        this.setState({contract});
      });
    }
    getMemberInfo(params.member_id).then(member => {
      this.setState({member});
    });
    getContractItems(contract_id).then(data => {
      this.setState({default_contract_items: data.results});
    });
    getContractAllownaces(contract_id).then(data => {
      this.setState({default_contract_allowances: data.results});
    });
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
  };

  componentDidUpdate = () => {
    document.title = `契約追加 | ${this.state.member.full_name || ''} | 契約管理`
  };

  getSteps = () => {
    const { contract, default_contract_items, default_contract_allowances, organizations } = this.state;
    common.getFromJsonList(this.basic_info_edit_schema, 'name', 'organization').choices = organizations;
    let contract_item_choices = [];
    common.getFromJsonList(this.new_contract_item_schema, "name", "name").get_props = (data, inlines) => {
      const exists = [];
      inlines.map(d => !d.is_new && exists.push(d.code));
      contract_item_choices = [];
      const choices = [];
      choice_contract_comment.map(item => {
        if (exists.indexOf(item.value) < 0) {
          contract_item_choices.push(item);
          choices.push({
            "value": item.display_name,
            "display_name": item.display_name,
          });
        }
        return true;
      });
      return {
        "type": "choice",
        "read_only": false,
        "choices": choices,
      }
    }

    if (common.isEmpty(default_contract_items) || common.isEmpty(default_contract_allowances)) {
      return null;
    } else {
      const default_contract_allowances_empty_comment = common.clone(default_contract_allowances)
      default_contract_allowances_empty_comment.map(item => item.comment = null);
      return [
        {
          name: '契約基本情報',
          formProps: {
            name: 'basic_info',
            schema: this.basic_info_edit_schema,
            layout: contract_form_basic_layout,
            data: contract,
            checkList: [checkDateContradict],
            onChanges: [
              onChangeContractBasicInfo,
            ],
          },
        },
        {
          name: '契約条件',
          formProps: {
            name: 'contract_items',
            inline: true,
            schema: contract_item_schema,
            new_line_schema: this.new_contract_item_schema,
            layout: contract_item_layout,
            data: default_contract_items,
            onChanges: [
              (name, data, steps, prefix, inlineIndex) => {
                if (name === 'name') {
                  const choice = common.getFromJsonList(contract_item_choices, "display_name", data[inlineIndex]["name"])
                  data[inlineIndex]["code"] = choice ? choice.value : null;
                }
              },
            ],
            checkList: [
              checkUnique(['code']),
              checkUnique(['name']),
            ],
          },
        },
        {
          name: '基本給・諸手当',
          formProps: {
            name: 'allowances',
            inline: true,
            schema: contract_allowance_schema,
            data: default_contract_allowances_empty_comment,
            checkList: [
              checkUnique(['code']),
              checkUnique(['name']),
              checkAllowanceAmount,
            ],
            onChanges: [(n, d, s, p, i) => onChangeContractAllowance(n, d, s, p, i, default_contract_allowances)],
            allowAdd: false,
            allowDelete: false,
          },
        },
      ];
    }
  };

  render() {
    const { params } = this.props.match;
    const { member } = this.state;
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/contract', name: '契約管理' },
            { link: '/contract/members', name: '社員一覧' },
            { link: `/contract/members/${params.member_id}/`, name: member.full_name },
            { name: '契約追加' },
          ]}
        />
        <GridContainer>
          <GridItem xs={12} sm={12} md={9} lg={9}>
            { steps ? (
              <StepForm
                title='契約追加'
                steps={steps}
                methodName='POST'
                saveEndpoint={common.formatStr(config.api.contract.add_member_contract, params.member_id)}
                saveCallback={() => common.redirect(`/contract/members/${params.member_id}`)}
              />
            ) : null}
          </GridItem>
        </GridContainer>
      </div>
    );
  }
};

export default withPermissions(ContractAdd, 'contract.add_contract');
