import React from 'react';
import {
  TopBreadcrumbs,
  withPermissions,
} from '../../components';
import BankAccountList from '../common/bank_account_list';
import { getPartnerCompany } from '../utils/api';

class PartnerCompanyBankAccounts extends React.Component {
  title = '銀行口座一覧';

  constructor(props) {
    super(props);

    this.state = {
      company: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { params } = props.match;
    getPartnerCompany(params.company_id).then(company => {
      this.setState({company});
    });
  };

  render() {
    const { company_id } = this.props.match.params;
    const { company } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { link: `/partner/companies/${company_id}/`, name: company.name },
            { name: this.title },
          ]}
        />
        <div>
          { company.content_type ? (
            <BankAccountList
              title={`${company.name} 口座一覧`}
              content_type={company.content_type}
              object_id={company_id}
            />
          ) : null}
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerCompanyBankAccounts, 'master.view_bankaccount');