import React from 'react';
import { StepForm, TopBreadcrumbs, withPermissions } from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import DuplicatedMemberDialog from '../common/duplicated_member_dialog';
import {
  member_form_basic_schema,
  member_form_basic_layout,
  member_add_org_schema,
  member_code_form_schema,
} from './schema';
import {
  getOrganizationChoices,
  getNextMemberCode,
  checkMemberDuplications,
} from '../utils/api';
import { common } from '../../utils/common';
import { config } from '../../utils/config';
import { onBlurPostcode } from '../utils/common';

class MemberAdd extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      organizations: [],
      duplicated_member_data: {},
    };
    this.initialize();
  }

  initialize = () => {
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
  };

  componentDidMount = () => {
    document.title = '社員追加 | 契約管理'
  };

  onBlurMemberDuplicate = (name, data) => {
    if (['first_name', 'last_name'].indexOf(name) >= 0) {
      const { first_name, last_name } = data;
      if (first_name && last_name) {
        checkMemberDuplications(first_name, last_name).then(json => {
          if (this._openDuplicatedDlg && json.length > 0) {
            this._openDuplicatedDlg(`${last_name} ${first_name}`, json);
          }
        });
      }
    }
  };

  onDuplicatedMemberSelect = (data) => {
    this.setState({duplicated_member_data: data});
  };

  getSteps = () => {
    const { organizations, duplicated_member_data } = this.state;

    // 所属部署の設定
    let colOrg = common.getFromJsonList(member_add_org_schema, 'name', 'organization');
    colOrg['choices'] = organizations;

    return [
      {
        name: '基本情報',
        formProps: {
          name: 'basic_info',
          schema: member_form_basic_schema,
          layout: member_form_basic_layout,
          onBlurs: [onBlurPostcode, this.onBlurMemberDuplicate],
          data: duplicated_member_data,
        }
      },
      {
        name: '所属設定',
        formProps: {
          name: 'organization',
          schema: member_add_org_schema,
        }
      },
      {
        name: '社員番号設定',
        formProps: {
          name: 'basic_info',
          schema: member_code_form_schema,
        },
        actions: [
          {
            'tooltip': '自動採番',
            'title': '自動採番',
            'handleClick': (stepsData) => {return getNextMemberCode(stepsData)},
          },
        ]
      },
    ];
  };

  render() {
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/contract', name: '契約管理'},
            { link: '/contract/members', name: '社員一覧'},
            { name: '社員追加' },
          ]}
        />
        <GridContainer>
          <GridItem xs={12} sm={12} md={9} lg={9}>
            <StepForm
              title='社員追加'
              steps={steps}
              methodName='POST'
              saveEndpoint={config.api.member.add_from_contract}
              saveCallback={(data) => common.redirect(`/contract/members/${data.id}/`)}
            />
          </GridItem>
        </GridContainer>
        <DuplicatedMemberDialog
          innerRef={dlg => this._openDuplicatedDlg = dlg && dlg.handleOpen}
          handleSelected={this.onDuplicatedMemberSelect}
        />
      </div>
    );
  }
}

export default withPermissions(MemberAdd, 'contract.add_member');
