import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import { release_list_schema } from './schema';
import { config } from '../../utils/config';
import { withPermissions } from '../../components';
import { getSalespersonChoice } from '../utils/api';
import { common } from '../../utils';

class ReleaseList extends React.Component {
  title = 'リリース一覧';

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      salesperson_choice: [],
      ...this.getYm(),
    };
    
    this.initialize(props);
  }

  initialize = (props) => {
    getSalespersonChoice().then(salesperson_choice => this.setState({salesperson_choice}));
  };

  componentDidUpdate = () => {
    const { year, month } = this.state;
    document.title = `${year}年${month}月 | ${this.title}`;
  };

  prevMonthRelease = () => {
    const { year, month } = this.state;
    const date = common.getPrevMonthDictByYm(year, month)
    common.redirect(`/member/releases/${date.year}/${date.month}/`);
    this.setState(state => ({ ...date, reload: !state.reload }));
  };

  nextMonthRelease = () => {
    const { year, month } = this.state;
    const date = common.getNextMonthDictByYm(year, month)
    common.redirect(`/member/releases/${date.year}/${date.month}/`);
    this.setState(state => ({ ...date, reload: !state.reload }));
  };

  getYm = () => {
    let { year, month } = this.props.match.params;
    if (!year || !month) {
      year = common.getCurrentYear();
      month = common.getCurrentMonth();
    }
    return { year, month };
  };

  render() {
    const { salesperson_choice, year, month, reload } = this.state;
    // 営業員選択肢の設定
    const my_release_list_schema = common.clone(release_list_schema);
    common.getFromJsonList(my_release_list_schema, 'name', 'salesperson')['choices'] = salesperson_choice;
    console.log(common.formatStr(config.api.member.release_list, year, month))

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {`${year}年${month}月リリース予定一覧`}
          </CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={ common.formatStr(config.api.member.release_list, year, month) }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  storageKey='member.release.list'
                  tableHead={my_release_list_schema}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  pushpinTop={common.getHeaderHeight()}
                  allowCsv={true}
                  urlReflect={false}
                  tableActions={[
                    {
                      "tooltip": "先月",
                      "icon": <NavigateBeforeIcon />,
                      "handleClick": this.prevMonthRelease,
                    },
                    {
                      "tooltip": "次月",
                      "icon": <NavigateNextIcon />,
                      "handleClick": this.nextMonthRelease,
                    },
                  ]}
                />
              ) }
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(ReleaseList, 'member.view_member');