import queryString from 'query-string';
import cloneDeep from 'lodash/cloneDeep';
import { push } from 'connected-react-router';
import { vsprintf } from 'sprintf-js';
import { authHeader } from './authHeader';
import { store } from '../app/store';
import { logoutAndRedirect, clearMe } from '../features/actions/index';
import { constant } from './constants';

export const common = {

  /**
   * 数字をカンマ区切りで表示
   * @param {Integer} num 数字
   */
  toNumComma: function(num) {
    if (num === null || num === undefined) {
      return '';
    } else {
      const int_comma = (num + "").replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
      return int_comma;
    }
  },

  /**
   * オブジェクトは空白なのか
   * @param {Object} obj 
   */
  isEmpty: function(obj) {
    if (obj === null || obj === undefined || obj === '') {
      return true;
    } else if (Array.isArray(obj)) {
      return obj.length === 0;
    } else if (typeof obj === 'number') {
      return false;
    }
    for(var key in obj) {
      if(obj.hasOwnProperty(key))
        return false;
    }
    return true;
  },

  /**
   * 文字列の両端に特定の文字を消す
   * @param {String} s 文字列
   * @param {String} c 置換する文字列
   */
  trim: function(s, c) {
    if (c === "]") c = "\\]";
    if (c === "\\") c = "\\\\";
    return s.replace(new RegExp(
      "^[" + c + "]+|[" + c + "]+$", "g"
    ), "");
  },

  /**
   * 数字の先頭に0をつける
   * @param {Integer} num 数字
   * @param {Integer} size 桁数
   */
  pad: function(num, size) {
    var s = "000000000" + num;
    return s.substr(s.length-size);
  },

  /**
   * ＵＲＬにパラメーターを追加する
   * @param {String} url ＵＲＬ
   * @param {Object} data JSON型のパラメーター
   */
  addUrlParameter: function(url, data) {
    url = this.trim(this.trim(url, '?'), '&');
    const params = queryString.stringify(data);
    if (url.indexOf('?') < 0) {
      url += '?';
    }
    return url + '&' + params;
  },

  /**
   * URLのパラメーターからJSONを取得
   * @param {String} qs URL
   */
  parseQueryString: function(qs) {
    return queryString.parse(qs);
  },

  /**
   * 現在ログイン中のユーザーに指定の権限があるかどうか
   * @param {String} perm 権限名称
   */
  hasPerm: function(perm) {
    if (!store.getState()) {
      return false;
    }
    const user = store.getState().user;
    if (user.me.is_superuser) {
      return true;
    }
    if (this.isEmpty(user.perms)) {
      return false;
    }
    return user.perms.indexOf(perm) >= 0;
  },

    /**
   * 画面を遷移する
   * @param {String} url ＵＲＬ
   */
  redirect: function(url, search='', state={}) {
    store.dispatch(push({
      pathname: url,
      search: search,
      state: state,
    }));
  },

  /**
   * JSON項目のリストから項目を取得
   * @param {Array} json_list 
   * @param {String} key 
   * @param {String} value 
   */
  getFromJsonList: function(json_list, key, value) {
    if (!json_list) {
      return {};
    } else if (json_list.length === 0) {
      return {};
    } else if (typeof value === 'undefined') {
      return {};
    } else {
      if (typeof value === 'string') {
        value = value.split('__')[0];
      }
      let cols = json_list.filter(col => col[key] === value);
      return cols.length > 0 ? cols[0] : {};
    }
  },

  /**
   * JSONかどうかを判定する関数
   * @param {Object} arg 
   */
  isJSON: function(arg) {
    return typeof arg === 'object';
  },

  /**
   * 文字列をフォーマットする
   * @param {String} format 
   *
   * 使用方法：utils.format('This is argument: %s', arg1);
   */
  formatStr: function(format) {
    if (!format) {
      return null;
    } else if (arguments && arguments.length === 2 && this.isJSON(arguments[1])) {
      return vsprintf(format, arguments[1]);
    } else {
      var i = 0,
        j = 0,
        r = "",
        next = function(args){
          j += 1; i += 1;
          return args[j] !== void 0 ? args[j] : "";
        };

      for(i=0; i<format.length; i++){
        if(format.charCodeAt(i) === 37){
          switch(format.charCodeAt(i+1)){
            case 115: r += next(arguments); break;
            case 100: r += Number(next(arguments)); break;
            default: r += format[i]; break;
          }
        } else {
          r += format[i];
        }
      }
      return r;
    }
  },

  /**
   * 日付をフォーマットする
   * @param {Date} date 
   * @param {String} format 
   */
  formatDate: function (date, format) {
    if (!format) {
        format = 'YYYY-MM-DD hh:mm:ss.SSS';
    }
    if (!date) {
      return null;
    } else if (typeof date === "string") {
        date = new Date(date);
    }
    format = format.replace(/YYYY/g, date.getFullYear());
    format = format.replace(/MM/g, ('0' + (date.getMonth() + 1)).slice(-2));
    format = format.replace(/DD/g, ('0' + date.getDate()).slice(-2));
    format = format.replace(/hh/g, ('0' + date.getHours()).slice(-2));
    format = format.replace(/mm/g, ('0' + date.getMinutes()).slice(-2));
    format = format.replace(/ss/g, ('0' + date.getSeconds()).slice(-2));
    if (format.match(/S/g)) {
        var milliSeconds = ('00' + date.getMilliseconds()).slice(-3);
        var length = format.match(/S/g).length;
        for (var i = 0; i < length; i++) format = format.replace(/S/, milliSeconds.substring(i, i + 1));
    }
    return format;
  },

  /**
   * フォーム保存時エラー発生する場合のエラーメッセージを取得
   * @param {JSON} response Ajaxの応答
   */
  getFormErrorMessage: function(response) {
    if (response.detail) {
      return response.detail;
    } else {
      return constant.ERROR.FORM_CHECK_ERROR;
    }
  },

  /**
   * iframeの内容を印刷する
   * @param {String} idFrame iframeのID
   */
  printIframeContent: function(idFrame) {
    var frm = document.getElementById(idFrame).contentWindow;
    frm.focus();
    frm.print();
    return false;
  },

  /**
   * 
   * @param {Array} results 結果
   * @param {String} src_field 項目
   */
  cascade_reflect: function(results, schema, src_field) {
    const column = common.getFromJsonList(schema, 'name', src_field);
    if (column && column.cascade_to) {
      const cascade_to = common.getFromJsonList(schema, 'name', column.cascade_to);
      if (cascade_to && cascade_to.cascade_reflect) {
        cascade_to.cascade_reflect(results);
      }
    }
  },

  /**
   * Base64をBlobに変換する
   * @param {Base64} base64 Base64
   * @param {String} mime_ctype 
   */
  toBlob: function(base64, mime_ctype='application/octet-stream') {
    var bin = atob(base64.replace(/^.*,/, ''));
    var buffer = new Uint8Array(bin.length);
    for (var i = 0; i < bin.length; i++) {
        buffer[i] = bin.charCodeAt(i);
    }
    // Blobを作成
    try{
        var blob = new Blob([buffer.buffer], {
            type: mime_ctype
        });
    }catch (e){
        return false;
    }
    return blob;
  },

  /**
   * Blobファイルをダウンロード
   * @param {Blob} blob 
   * @param {String} filename ファイル名
   */
  downloadBlog: function(blob, filename) {
    var csvURL = window.URL.createObjectURL(blob);
    var tempLink = document.createElement('a');
    tempLink.href = csvURL;
    tempLink.setAttribute('download', filename);
    tempLink.click();
  },

  /**
   * JSONをコピーする
   * @param {JSON} data コピーしようとするJSONデータ
   */
  clone: function(data) {
    return cloneDeep(data);
  },

  /**
   * 現在日付の'YYYY-MM-DD'形式を取得
   */
  getCurrentDateStr: function() {
    return this.formatDate(new Date(), 'YYYY-MM-DD');
  },

  /**
   * 現在の年を取得
   */
  getCurrentYear: function() {
    const d = new Date();
    return d.getFullYear() + '';
  },

  /**
   * 現在の月を取得
   */
  getCurrentMonth: function() {
    const d = new Date();
    // return (d.getMonth() + 1) + '';
    return this.pad(d.getMonth() + 1, 2);
  },

  /**
   * 先月を取得
   * データ形式：{year: YYYY, month: MM}
   * @param {Date}} date 指定日付
   */
  getPrevMonthDict: function(date) {
    if (!(date instanceof Date)) {
      date = new Date();
    }
    const d = new Date(date.getFullYear(), date.getMonth() - 1, 1);
    return {
      'year': d.getFullYear() + '',
      'month': this.pad(d.getMonth() + 1, 2),
    }
  },

  /**
   * 次月を取得
   * データ形式：{year: YYYY, month: MM}
   * @param {Date}} date 指定日付
   */
  getNextMonthDict: function(date) {
    if (!(date instanceof Date)) {
      date = new Date();
    }
    const d = new Date(date.getFullYear(), date.getMonth() + 1, 1);
    return {
      'year': d.getFullYear() + '',
      'month': this.pad(d.getMonth() + 1, 2),
    }
  },

  /**
   * 先月を取得
   * データ形式：{year: YYYY, month: MM}
   * @param {String} year 対象年
   * @param {String} month 対象月
   */
  getPrevMonthDictByYm: function(year, month) {
    const date = new Date(year, month - 1, 1);
    return this.getPrevMonthDict(date);
  },

  /**
   * 次月を取得
   * データ形式：{year: YYYY, month: MM}
   * @param {String} year 対象年
   * @param {String} month 対象月
   */
  getNextMonthDictByYm: function(year, month) {
    const date = new Date(year, month - 1, 1);
    return this.getNextMonthDict(date);
  },

  /**
   * 2つの期間中の月数を取得する
   * @param {Date} d1 開始日
   * @param {Date} d2 終了日
   */
  getMonthDiff: function(d1, d2) {
    if (typeof d1 === 'string') {
      d1 = new Date(d1);
    }
    if (typeof d2 === 'string') {
      d2 = new Date(d2);
    }
    if (!d1 || !d2) {
      return null;
    }
    if (isNaN(d1) || isNaN(d2)) {
      return null;
    }
    let months = (d2.getFullYear() - d1.getFullYear()) * 12;
    months -= d1.getMonth();
    months += d2.getMonth();
    return months <= 0 ? 0 : months;
  },

  getHeaderHeight: function() {
    if (document.getElementsByTagName('header').length > 0) {
      return document.getElementsByTagName('header')[0].offsetHeight;
    } else {
      return 64;
    }
  },

  /**
   * 日付に月数を足す
   * @param {Date} date 日付
   * @param {Integer} months 月数
   */
  addMonths: function(date, months) {
    if (typeof date === 'string') {
      date = new Date(date);
    }
    if (isNaN(date)) {
      return null;
    }
    const resultDate = new Date(date.getTime());
    resultDate.setMonth(date.getMonth() + months);
    return resultDate;
  },

  /**
   * 開始日以降の一年後
   * 1年間の契約を作成時、一年後の契約終了日を取得する。
   * @param {Date} date 開始日
   */
  getOneYearEndDate: function (date) {
    const date_after_12_month = this.addMonths(date, 12);
    if (date_after_12_month) {
      return new Date(date_after_12_month.getFullYear(), date_after_12_month.getMonth(), 0);
    } else {
      return null;
    }
  }
};

export const ajax = {

  getMethod: function(name) {
    switch (name) {
      case 'GET':
        return this.fetchGet;
      case 'POST':
        return this.fetchPost;
      case 'DELETE':
        return this.fetchDelete;
      case 'PUT':
        return this.fetchPut;
      case 'OPTIONS':
        return this.fetchOptions;
      default:
        throw Error('UNKNOWN METHOD NAME!');
    }
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchGet: function(url, params) {
    return ajax.fetchCommon(url, 'GET', params);
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchPost: function(url, params) {
    return ajax.fetchCommon(url, 'POST', params);
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchPatch: function(url, params) {
    return ajax.fetchCommon(url, 'PATCH', params);
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchDelete: function(url, params) {
    return ajax.fetchCommon(url, 'DELETE', params);
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchPut: function(url, params) {
    return ajax.fetchCommon(url, 'PUT', params);
  },

  /**
   * 
   * @param {String} url ＵＲＬ
   * @param {Object} params パラメーター
   */
  fetchOptions: function(url) {
    return ajax.fetchCommon(url, 'OPTIONS');
  },

  fetchBinary: function(url) {
    const requestOptions = {
      method: 'GET',
      headers: authHeader(),
      // credentials: 'same-origin',
    };
    return fetch(url, requestOptions)
      .then(ajax.handleStatus)
      .then(function(res) {
        return res.blob();
      });
  },

  /**
   * ＡＰＩを呼び出す
   * @param {String} url ＵＲＬ
   * @param {String} method GET|POST|PUT|DELETE
   * @param {Object} params パラメーター
   */
  fetchCommon: function(url, method, params) {
    const requestOptions = {
      method: method,
      headers: authHeader(),
      // credentials: 'same-origin',
    };
    if (params && !common.isEmpty(params)) {
      if (method === 'GET') {
        url = common.addUrlParameter(url, params);
      } else if (['POST', 'PUT'].indexOf(method) >= 0 && 'blob' in params) {
        requestOptions['body'] = params['blob'];
        requestOptions.headers['Content-Type'] = params.type || 'multipart/form-data';
      } else {
        requestOptions['body'] = JSON.stringify(params)
      }
    }

    url = common.addUrlParameter(url, {'format': 'json'});
    return fetch(url, requestOptions)
      .then(ajax.handleStatus)
      .then(ajax.handleResponse);
  },

  handleStatus: function(response) {
    if (!response.ok) {
      // store.dispatch(changeStatusCode(response.status));
      if (response.status === 401) {
        // auto logout if 401 response returned from api
        store.dispatch(logoutAndRedirect());
        store.dispatch(clearMe());
        //location.reload(true);
      }
      return ajax.handleResponse(response).then(data => {
        return Promise.reject(data);
      });
    } else {
      return response;
    }
  },

  handleResponse: function(response) {
    return response.text().then(text => {
      let data = text;
      try {
        data = JSON.parse(text);
      } catch (e) { }

      return data;
    });
  },
};
