import React from 'react';
import { Paper } from '@material-ui/core';
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import { EnhancedTable } from '../../datatable/index';
import DataSource from '../../components/DataSource';
import { expense_by_object_list_schema } from './schema';
import { common, ajax } from '../../utils';
import { config } from '../../utils/config';
import { withPermissions, TopBreadcrumbs, Toast } from '../../components';
import {
  getExpenseCategoryChoices,
  downloadMemberExpense,
  handleEditObject,
  expense_form_schema,
  expense_form_layout,
  handleAddObject,
  handleDeleteObject,
  getProjectChoices,
  getOrganizationChoices,
} from '../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ExpenseByObjectList extends React.Component {
  title = '立替金一覧';
  expense_by_object_list_schema = common.clone(expense_by_object_list_schema);
  expense_form_schema = common.clone(expense_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      member: {},
      expense_category_list: [],
      project_choices: [],
      org_choices: [],
    };
    this.initialize(props);
  }
  
  initialize = (props) => {
    const { content_type_id, member_id } = props.match.params;
    ajax.fetchGet(common.formatStr(
      config.api.member.portal_detail, content_type_id, member_id
    )).then(member => {
      this.setState({ member });
    });
    getExpenseCategoryChoices().then(data => (
      this.setState({expense_category_list: data})
    ));
    getProjectChoices().then(data => this.setState({ project_choices: data }));
    getOrganizationChoices().then(data => this.setState({ org_choices: data }));
  };

  componentDidUpdate = () => {
    const { year, month } = this.props.match.params;
    const { member } = this.state;
    document.title = `${member.full_name} | ${year}年${month}月 | ${this.title}`;
  };

  handleDownloadMemberExpenses = (data) => {
    if (Array.isArray(data) && data.length > 0) {
      const { year, month, content_type_id, member_id } = this.props.match.params;
      return downloadMemberExpense(year, month, content_type_id, member_id).then(json => {
        const blob = common.toBlob(json.blob);
        common.downloadBlog(blob, json.name);
      }).catch(errors => {
        Toast.error(errors.detail);
      });
    }
  };

  render () {
    const { year, month, content_type_id, member_id } = this.props.match.params;
    const { reload, member, expense_category_list, project_choices, org_choices } = this.state;
    common.getFromJsonList(this.expense_by_object_list_schema, 'name', 'category_id').choices = expense_category_list;
    common.getFromJsonList(this.expense_form_schema, 'name', 'category').choices = expense_category_list;
    common.getFromJsonList(this.expense_form_schema, 'name', 'project').choices = project_choices;
    common.getFromJsonList(this.expense_form_schema, 'name', 'organization').choices = org_choices;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/member/expenses', name: '立替金一覧' },
            { link: `/member/expenses/${year}/${month}`, name: `${year}年${month}月の${this.title}` },
            { name: member.full_name },
          ]}
        />
        <Paper style={paperSpacing}>
          <DataSource
            reload={reload}
            endpoint={ common.formatStr(config.api.attendance.expense_by_object, year, month, content_type_id, member_id) }
            render={ (initData) => (
              <EnhancedTable
                title={`${member.full_name}: ${year}年${month}月の${this.title}`}
                showTitle={true}
                tableHead={this.expense_by_object_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                pushpinTop={common.getHeaderHeight()}
                urlReflect={false}
                tableStyles={{ minWidth: 900 }}
                selectable='single'
                tableActions={[
                  {
                    'tooltip': '経費精算書ダウンロード',
                    'icon': <CloudDownloadIcon />,
                    'handleClick': this.handleDownloadMemberExpenses,
                    'showLoading': true,
                  },
                ]}
                addProps={{
                  "title": `${member.full_name}の経費追加`,
                  "schema": this.expense_form_schema,
                  "layout": expense_form_layout,
                  "data": {
                    'content_type': content_type_id,
                    'object_id': member_id,
                    'year': year,
                    'month': month,
                  },
                  "handleOk": handleAddObject(
                    () => this.setState({reload: !reload}),
                    config.api.attendance.expense_add,
                  ),
                  "visible": common.hasPerm('attendance.add_expense'),
                }}
                editProps={{
                  "title": `${member.full_name}の経費変更`,
                  "schema": this.expense_form_schema,
                  "layout": expense_form_layout,
                  "handleOk": handleEditObject(
                    () => this.setState({reload: !reload}),
                    config.api.attendance.expense_detail,
                  ),
                  "visible": common.hasPerm('attendance.edit_expense'),
                }}
                deleteProps={{
                  "handleDelete": handleDeleteObject(
                    () => this.setState({reload: !reload}),
                    config.api.attendance.expense_detail,
                  ),
                  "visible": common.hasPerm('attendance.delete_expense'),
                }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(ExpenseByObjectList, 'attendance.view_expense');
