import React from 'react';
import { StepForm, TopBreadcrumbs, withPermissions } from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { config, common } from '../../utils';
import {
  project_basic_info_schema,
  project_basic_info_layout,
  project_request_profile_schema,
  project_request_profile_layout,
  blanket_contract_form_schema,
  default_blanket_contract_data,
} from './schema';
import {
  getProjectBusinessTypeChoice,
  getOrganizationChoices,
  getCompanyCategory,
  getCascadeObjects,
  getSalespersonChoice,
} from '../utils/api';
import { contract_allowance_schema, bp_contract_item_schema, bp_contract_item_layout } from '../partner/schema';
import { onChangeProjectBlanketContractInStep } from '../utils';

class ProjectAdd extends React.Component {
  title = '案件追加';
  project_basic_info_schema = JSON.parse(JSON.stringify(project_basic_info_schema));

  constructor(props) {
    super(props);

    this.state = {
      project_business_type_list: [],
      organizations: [],
      company_categories: [],
      salesperson_choice: [],
      is_blanket_contract: false,
      project_basic_info: {},
    };
    this.initialize();
  }

  initialize = () => {
    getProjectBusinessTypeChoice().then(data => {
      this.setState({project_business_type_list: data});
    });
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    getCompanyCategory().then(company_categories => {
      this.setState({company_categories});
    });
    getSalespersonChoice().then(salesperson_choice => this.setState({salesperson_choice}));
  };

  getSteps = () => {
    const {
      project_business_type_list, organizations, company_categories, salesperson_choice,
      is_blanket_contract, project_basic_info,
    } = this.state;
    this.project_basic_info_schema[0]['choices'] = company_categories;
    this.project_basic_info_schema[4]['choices'] = project_business_type_list;
    this.project_basic_info_schema[10]['choices'] = organizations;
    common.getFromJsonList(this.project_basic_info_schema, "name", "salesperson")["choices"] = salesperson_choice;

    let steps = [
      {
        name: '案件基本情報',
        formProps: {
          name: 'basic_info',
          schema: this.project_basic_info_schema,
          layout: project_basic_info_layout,
          data: {
            'status': '10',
          },
          onChanges: [(name, data) => getCascadeObjects(
            name,
            'company_content_type',
            data[name],
            this.project_basic_info_schema
          )],
        },
      },
      {
        name: '請求条件設定',
        description: "メンバーアサイン時のデフォールト設定として使われます。",
        formProps: {
          name: 'request_profile',
          schema: project_request_profile_schema,
          layout: project_request_profile_layout,
          onChanges: [(name, data, steps) => {
            if (name === 'is_blanket_contract') {
              this.setState({
                is_blanket_contract: data[name],
                project_basic_info: steps['basic_info'],
              });
            }
          }],
        },
      },
    ];
    if (is_blanket_contract) {
      steps.push({
        name: '一括契約',
        formProps: {
          name: 'blanket_contract',
          schema: blanket_contract_form_schema,
          onChanges: [onChangeProjectBlanketContractInStep],
          inlines: [
            {
              name: 'allowances',
              title: '精算条件',
              schema: contract_allowance_schema,
              allowAdd: false,
              allowDelete: false,
            },
            {
              name: "contract_items",
              title: "契約条件",
              schema: bp_contract_item_schema,
              layout: bp_contract_item_layout,
              allowAdd: false,
              allowDelete: false,
            },
          ],
          data: Object.assign({
            'contract_type': '0104',
            'contract_date': project_basic_info.start_date,
            'start_date': project_basic_info.start_date,
            'end_date': project_basic_info.end_date,
          }, default_blanket_contract_data),
        }
      })
    }
    return steps;
  };

  render() {
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { name: this.title },
          ]}
        />
        <GridContainer>
          <GridItem xs={12} sm={12} md={9} lg={9}>
            { steps ? (
              <StepForm
                title={this.title}
                steps={steps}
                methodName='POST'
                saveEndpoint={config.api.project.add_project}
                saveCallback={(data) => common.redirect(`/project/projects/${data.id}`)}
              />
            ) : null}
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default withPermissions(ProjectAdd, 'project.add_project');
