import React from 'react';
import { InlineForm } from '../../datatable';
import { 
  withPermissions,
  TopBreadcrumbs,
  Card,
  CardHeader,
  CardBody,
  Waiting,
  CardFooter,
  Toast,
} from '../../components';
import {
  getProjectDetail,
  getProjectMemberDetial,
  getDuplicatedProjectMember,
  saveProjectMemberCostPercentage,
} from '../utils/api';
import { common, constant } from '../../utils';
import { cost_percentage_form_schema } from './schema';
import { Button, Typography, withStyles } from '@material-ui/core';

const styles = theme => ({
  button: {
    margin: theme.spacing(1),
  }
});

class ProjectMemberDuplications extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      member: {},
      duplications: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id, member_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
    });
    getProjectMemberDetial(project_id, member_id).then(member => {
      this.setState({member});
    });
    getDuplicatedProjectMember(project_id, member_id).then(duplications => {
      this.setState({duplications});
    });
  };

  componentDidUpdate = () => {
    const { project, member } = this.state;
    document.title = `${member.full_name || ''} | ${project.name || ''} | 案件一覧`;
  };

  saveCostPercentage = () => {
    if (this.__clean) {
      const { project_id, member_id } = this.props.match.params;
      const data = this.__clean();
      saveProjectMemberCostPercentage(project_id, member_id, data).then(() => {
        common.redirect(`/project/projects/${project_id}/members/${member_id}`);
        Toast.success(constant.SUCCESS.SAVED);
      }).catch(errors => {
        Toast.error(errors.detail);
      });
    }
  };

  render() {
    const { project_id, member_id } = this.props.match.params;
    const { classes } = this.props;
    const { project, member, duplications } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: `/project/projects`, name: '案件一覧' },
            { link: `/project/projects/${project_id}/`, name: project.name },
            { link: `/project/projects/${project_id}/members`, name: 'メンバー一覧' },
            { link: `/project/projects/${project_id}/members/${member_id}`, name: member.full_name },
            { name: 'コスト割合' },
          ]}
        />
        <Card>
          <CardHeader>
            {`${member.full_name}のコスト分担一覧`}
          </CardHeader>
          <CardBody>
            {duplications && duplications.length > 0 ? (
              <InlineForm
                schema={cost_percentage_form_schema}
                allowAdd={false}
                allowDelete={false}
                data={duplications}
                innerRef={frm => this.__clean = frm && frm.clean}
              />
            ) : <Waiting/>}
          </CardBody>
          <CardFooter>
            <Typography style={{flex: 1}} />
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={this.saveCostPercentage}
            >
              保存
            </Button>
          </CardFooter>
        </Card>
      </div>
    );
  }
}

export default withPermissions(
  withStyles(styles)(ProjectMemberDuplications),
  'project.view_projectmember',
);
