import React from 'react';
import { Paper } from '@material-ui/core';
import {
  withPermissions,
  TopBreadcrumbs,
} from '../../components';
import PaymentNoticeByMonth from './payment_notice_by_month';
import {
  getPartnerCompany,
  getOrganization,
} from '../utils';
import { common, config } from '../../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class CompanyPaymentNotice extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      company: {},
      organization: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id, org_id } = props.match.params;
    getPartnerCompany(company_id).then(company => {
      this.setState({company});
    });
    getOrganization(org_id).then(organization => {
      this.setState({organization});
    });
  };

  componentDidUpdate = () => {
    const { company } = this.state;
    document.title = `支払通知書・ＢＰ請求書 | ${company.name || ''} | 協力会社`;
  };

  render() {
    const { company_id, year, month, org_id } = this.props.match.params;
    const { company, organization } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { link: `/partner/companies/${company_id}`, name: company.name },
            { name: `${year}年${month}月 ${organization.name}の支払通知書・ＢＰ請求書` },
          ]}
        />
        <Paper style={paperSpacing}>
          {company.content_type ? (
            <PaymentNoticeByMonth
              title={`${year}年${month}月 ${organization.name}の支払通知書・ＢＰ請求書`}
              content_type={company.content_type}
              object_id={company_id}
              company_id={company_id}
              org_id={org_id}
              year={year}
              month={month}
              endpoint={common.formatStr(config.api.partner.company_payment_notice_by_month, company_id, year, month, org_id)}
            />
          ) : null}
        </Paper>
      </div>
    );
  }
}

export default withPermissions(CompanyPaymentNotice, 'partner.view_partnerrequest');
