import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  member_contract_form_schema,
  contract_calculate_hours_schema,
  contract_allowance_schema,
  default_contract_data,
  bp_contract_item_schema,
  bp_contract_item_layout,
} from '../partner/schema';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import {
  getProjectDetail,
  getProjectMemberDetial,
  getProjectMemberContractTotal,
} from '../utils/api';
import {
  onChangeCalculateType,
  onChangeMinusAndPlus,
} from '../utils/common';
import { withPermissions } from '../../components';

class ProjectMemberContractForm extends React.Component {
  title = '精算条件変更';

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      member: {},
      contract_total: default_contract_data,
      error_message: null,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id, member_id, contract_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
    });
    getProjectMemberDetial(project_id, member_id).then(member => {
      this.setState({member});
    });
    if (contract_id) {
      getProjectMemberContractTotal(project_id, member_id, contract_id).then(
        contract_total => this.setState({contract_total})
      ).catch(errors => {
        if (errors.status_code === 403) {
          this.props.forbidden(errors.detail);
        }
      });
    }
  };

  componentDidUpdate = () => {
    const { contract_id } = this.props.match.params;
    this.title = contract_id ? '精算条件変更' : '精算条件追加'
    const { project, member } = this.state;
    document.title = `${this.title} | ${member.full_name || ''} | ${project.name || ''} | 案件一覧`;
  };

  render() {
    const { project_id, member_id, contract_id } = this.props.match.params;
    const { project, member, contract_total } = this.state;
    if (!contract_id) {
      contract_total['company_content_type'] = project.company_content_type;
      contract_total['company_object_id'] = project.company_object_id;
      contract_total['content_type'] = member.project_member_content_type;
      contract_total['object_id'] = member_id;
    }
    const breadcrumbs = [
      { link: '/project/projects', name: '案件一覧' },
      { link: `/project/projects/${project_id}/`, name: project.name },
      { link: `/project/projects/${project_id}/members`, name: '社員一覧' },
      { link: `/project/projects/${project_id}/members/${member_id}`, name: member.full_name },
    ];
    if (contract_id) {
      breadcrumbs.push({ name: "精算条件変更" });
    } else {
      breadcrumbs.push({ name: "精算条件追加" });
    }

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={member_contract_form_schema}
                onChanges={[onChangeCalculateType, onChangeMinusAndPlus]}
                inlines={[
                  {
                    name: "calculate_hours",
                    title: "契約計算用時間",
                    schema: contract_calculate_hours_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: 'allowances',
                    title: '精算条件',
                    schema: contract_allowance_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: "contract_items",
                    title: "契約条件",
                    schema: bp_contract_item_schema,
                    layout: bp_contract_item_layout,
                    allowAdd: false,
                    allowDelete: false,
                  },
                ]}
                data={contract_total}
                methodName={'POST'}
                saveEndpoint={
                  contract_id ? common.formatStr(
                    config.api.project.member_contract_save, project_id, member_id, contract_total.id
                  ) : common.formatStr(
                    config.api.project.member_contract_add, project_id, member_id
                  )
                }
                saveCallback={() => common.redirect(`/project/projects/${project_id}/members/${member_id}`)}
                deleteEndpoint={contract_id ? common.formatStr(config.api.project.member_contract, project_id, member_id, contract_id) : null}
                deleteCallback={() => common.redirect(`/project/projects/${project_id}/members/${member_id}`)}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(ProjectMemberContractForm, 'project.change_projectmember');
