import React from 'react';
import PropTypes from "prop-types";
import { withStyles } from '@material-ui/core/styles';
import {
  Paper,
  Stepper,
  Step,
  StepLabel,
  Button,
  Typography,
} from '@material-ui/core';
import { Form, InlineForm } from '../datatable';
import { common, ajax } from '../utils/common';
import { constant } from '../utils/constants';
import { Toast } from '.';

const styles = (theme) => ({
  paper: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  stepDescription: {
    color: 'gray',
    padding: '0 20px 5px',
  },
  errorStepLabel: {
    color: 'red',
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
});

class StepForm extends React.Component {

  constructor(props) {
    super(props);

    this.handleNext = this.handleNext.bind(this);
    this.handleBack = this.handleBack.bind(this);
    this.state = {
      activeStep: 0,
      maxStep: 0,
      data: {},
      errors: {},
    };
  }

  getCurrentForm = (index) => {
    const { steps } = this.props;
    const { data, errors } = this.state;
    if (index < steps.length) {
      const { name, inline, checkList, onChanges, ...props } = steps[index].formProps;
      const error = common.isEmpty(errors) ? {} : errors[name];
      if (data[name]) {
        props['data'] = data[name];
      }
      let newCheckList = [];
      if (checkList) {
        // 全ステップのデータを渡す。
        newCheckList = checkList.map(method => (d) => method(d, data));
      }
      let newOnChangeList = [];
      if (onChanges) {
        // 全ステップのデータを渡す。
        newOnChangeList = onChanges.map(method => (n, d, s, p, i) => method(n, d, data, p, i));
      }
      const form = React.createElement(
        inline === true ? InlineForm : Form,
        {
          ...props,
          key: index,
          checkList: newCheckList,
          onChanges: newOnChangeList,
          errors: error || {},
          ref: (frm) => {
            if (frm && frm.clean) {
              this['__clean__' + name] = frm.clean;
            }
            if (frm && frm.toastError) {
              this['__toastError__' + name] = frm && frm.toastError;
            }
          }
        }
      )
      return { name, form };
    } else {
      return null;
    }
  };

  hasStepError = (step) => {
    const { errors } = this.state;
    const name = step.formProps.name;
    if (errors[name]) {
      return true;
    } else {
      return false;
    }
  };

  handleNext = (name) => () => {
    const formData = this.clean(name);
    if (formData) {
      this.setState(state => {
        let data = state.data;
        data[name] = formData;
        return {
          data,
          activeStep: state.activeStep + 1,
          maxStep: state.maxStep <= state.activeStep ? state.activeStep + 1 : state.maxStep,
        };
      });
    } else if (!this.hasForm(name)) {
      this.setState(state => {
        return {
          activeStep: state.activeStep + 1,
          maxStep: state.maxStep <= state.activeStep ? state.activeStep + 1 : state.maxStep,
        };
      });
    } else {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
    }
  };

  hasForm = (name) => {
    const forms = this.props.steps.filter(step => {
      if (step.formProps && step.formProps.name === name) {
        return true;
      } else {
        return false;
      }
    });
    return forms.length > 0;
  };

  handleBack = () => {
    this.setState(state => {
      return {activeStep: state.activeStep - 1};
    });
  };

  clean = (name) => {
    if (name && this['__clean__' + name]) {
      return this['__clean__' + name]();
    } else {
      return null;
    }
  };

  handleOk = (name) => () => {
    const formData = this.clean(name);
    if (formData) {
      let { data } = this.state;
      data[name] = formData;
      const { methodName, saveEndpoint, saveCallback } = this.props;
      ajax.getMethod(methodName)(saveEndpoint, data).then(json => {
        Toast.success(constant.SUCCESS.SAVED);
        if (saveCallback) {
          saveCallback(json);
        }
      }).catch(errors => {
        this.setState({errors});
        Toast.error(common.getFormErrorMessage(errors));
      });
    } else {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
    }
  };

  getStepActions = (stepIndex) => {
    const { steps } = this.props;
    if (stepIndex <= steps.length) {
      return steps[stepIndex].actions;
    } else {
      return null;
    }
  }

  handleActionClick = (formName, stepsData, method) => {
    method(stepsData).then(json => {
      stepsData[formName] = Object.assign({}, stepsData[formName], json);
      this.setState({data: stepsData});
    });
  }

  render() {
    const { classes, title, steps } = this.props;
    const { activeStep, data } = this.state;
    const currentForm = this.getCurrentForm(activeStep);
    const stepActions = this.getStepActions(activeStep);

    if (common.isEmpty(steps)) {
      return null;
    } else {
      return (
        <div>
          <Paper className={classes.paper}>
            <Typography component="h1" variant="h4" align="center">
              {title}
            </Typography>
            <Stepper activeStep={activeStep} className={classes.stepper}>
              {steps.map((step, index) => (
                <Step key={index}>
                  <StepLabel>
                    <span className={this.hasStepError(step) ? classes.errorStepLabel : null}>
                      {step.name}
                    </span>
                  </StepLabel>
                </Step>
              ))}
            </Stepper>
            {activeStep < steps.length && steps[activeStep].description ? (
              <Typography className={classes.stepDescription} variant='body2' gutterBottom>
                {steps[activeStep].description}
              </Typography>
            ) : null}
            <div>
              {activeStep === steps.length ? (
                <React.Fragment>
                  <Typography variant="h5" gutterBottom>
                    Thank you for your order.
                  </Typography>
                  <Typography variant="subtitle1">
                    Your order number is #2001539. We have emailed your order confirmation, and will
                    send you an update when your order has shipped.
                  </Typography>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  {currentForm ? (
                    currentForm.form
                  ) : null }
                  <div className={classes.buttons}>
                    { stepActions ? (
                      <React.Fragment>
                        {stepActions.map((action, key) => (
                          <Button
                            key={key}
                            variant="contained"
                            color="primary"
                            onClick={() => this.handleActionClick(currentForm.name, data, action.handleClick)}
                            className={classes.button}
                          >
                            {action.title}
                          </Button>
                        ))}
                        <div style={{flex: 1}}></div>
                      </React.Fragment>
                    ) : null}
                    {activeStep !== 0 && (
                      <Button onClick={this.handleBack} className={classes.button}>
                        Back
                      </Button>
                    )}
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={activeStep === steps.length - 1 ? this.handleOk(currentForm.name) : this.handleNext(currentForm.name)}
                      className={classes.button}
                    >
                      {activeStep === steps.length - 1 ? '保存' : 'Next'}
                    </Button>
                  </div>
                </React.Fragment>
              )}
            </div>
          </Paper>
        </div>
      );
    }
  }
}

StepForm.propTypes = {
  title: PropTypes.string.isRequired,
  steps: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    formProps: PropTypes.object.isRequired,
  })).isRequired,
  methodName: PropTypes.oneOf(['POST', 'PUT', 'DELETE']).isRequired,
  saveEndpoint: PropTypes.string.isRequired,
  saveCallback: PropTypes.func,
  deleteEndpoint: PropTypes.string,
  deleteCallback: PropTypes.func,
};

export default withStyles(styles)(StepForm);
