import React from 'react';
import PropTypes from "prop-types";
import {
  DataSource,
  withPermissions,
  Card,
  CardHeader,
  CardBody,
} from '../../components';
import { EnhancedTable } from '../../datatable';
import {
  getExpenseCategoryChoices,
  handleAddObject,
  handleEditObject,
  handleDeleteObject,
  expense_list_schema,
  expense_form_schema,
  expense_form_layout,
} from '../utils';
import { config, common } from '../../utils';

class ExpenseList extends React.Component {
  expense_list = common.clone(expense_list_schema);
  expense_form = common.clone(expense_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      expense_category_list: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    getExpenseCategoryChoices().then(expense_category_list => this.setState({expense_category_list}));
  };

  render() {
    const { content_type, object_id, title, project } = this.props;
    const { reload, expense_category_list } = this.state;
    if (!common.isEmpty(project)) {
      const colProject = common.getFromJsonList(this.expense_form, 'name', 'project');
      colProject["choices"] = [{ "value": parseInt(project.project_id), "display_name": project.project_name }];
      colProject['read_only'] = true;
    }
    common.getFromJsonList(this.expense_list, 'name', 'category')['choices'] = expense_category_list;
    common.getFromJsonList(this.expense_form, 'name', 'category')['choices'] = expense_category_list;

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {title}
          </CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={common.formatStr(config.api.attendance.expense_list_by_content_object_list, content_type, object_id)}
              render={(initData) => (
                <EnhancedTable
                  title='精算リスト'
                  tableHead={this.expense_list}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  selectable="single"
                  allowCsv={true}
                  addProps={{
                    title: '精算追加',
                    schema: this.expense_form,
                    layout: expense_form_layout,
                    handleOk: handleAddObject(
                      () => this.setState({reload: !reload}),
                      common.formatStr(config.api.attendance.expense_list_by_content_object_add, content_type, object_id),
                    ),
                    data: {
                      'content_type': content_type,
                      'object_id': object_id,
                      'project': project.project_id && parseInt(project.project_id),
                      'year': common.getCurrentYear(),
                    },
                    visible: common.hasPerm('attendance.add_expense'),
                  }}
                  editProps={{
                    title: '精算変更',
                    schema: this.expense_form,
                    layout: expense_form_layout,
                    handleOk: handleEditObject(
                      () => this.setState({reload: !reload}),
                      config.api.attendance.expense_list_by_content_object_detail,
                      content_type,
                      object_id,
                    ),
                    visible: common.hasPerm('attendance.change_expense'),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({reload: !reload}),
                      config.api.attendance.expense_list_by_content_object_detail,
                      content_type,
                      object_id,
                    ),
                    visible: common.hasPerm('attendance.delete_expense'),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

ExpenseList.propTypes = {
  title: PropTypes.string.isRequired,
  content_type: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
  object_id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
  project: PropTypes.shape({
    project_id: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
    ]),
    project_name: PropTypes.string,
  }),
};

export default withPermissions(ExpenseList, 'attendance.view_expense');