import React from 'react';
import { Route, Switch, Link } from 'react-router-dom';
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Container,
  Avatar,
  Typography,
  Tabs,
  Tab,
} from '@material-ui/core';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { HttpNotFound } from '../../components';
import WorkingStatusTab from './working_status_tab';
import UntreatedProjectRequestTab from './project_request_tab';
import { common } from '../../utils';

import defaultCover from "../../assets/img/cover.jpg";
import UntreatedPartnerOrderTab from './partner_order_tab';
import UntreatedPaymentNoticeTab from './payment_notice_tab';

const styles = (theme) => ({
  cover: {
    height: 180,
    position: 'relative',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundImage: `url(${defaultCover})`,
  },
  container: {
    maxWidth: '100%',
  },
  avatarWrapper: {
    position: 'relative',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  avatar: {
    width: 120,
    height: 120,
    position: 'absolute',
    top: -60,
  },
  avatarImage: {
    maxHeight: 120,
    maxWidth: 120,
    verticalAlign: 'middle',
  },
});

class MyPage extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      activeIndex: 0,
    };
  }

  componentDidUpdate = () => {
    const { user } = this.props;
    document.title = `${user.me.full_name || ''} | マイページ`;
  };

  componentDidMount = () => {
    const { pathname } = this.props.location;
    if (pathname.indexOf('working-status') > 0) {
      this.setState({ activeIndex: 0 });
    } else if (pathname.indexOf('project-request') > 0) {
      this.setState({ activeIndex: 1 });
    } else if (pathname.indexOf('partner-order') > 0) {
      this.setState({ activeIndex: 2 });
    } else if (pathname.indexOf('payment-notice') > 0) {
      this.setState({ activeIndex: 3 });
    }
  };

  handleChangeTab = (event, newIndex) => {
    this.setState({ activeIndex: newIndex });
  };

  render() {
    const { classes, user } = this.props;
    const { activeIndex } = this.state;

    return (
      <div>
        <div>
          <div className={classes.cover}>
          </div>
          <Container className={classes.container + ' ' + classes.avatarWrapper}>
            <Avatar aria-label="Recipe" className={classes.avatar}>
              {user.me.avatar ? (
                <img src={user.me.avatar} alt='Avatar' className={classes.avatarImage} />
              ) : <AccountCircle style={{fontSize: 145}} />}
            </Avatar>
            <div style={{marginLeft: 136}}>
              <Typography variant="caption" gutterBottom>
                {common.isEmpty(user.me.positions)
                  ? user.me.email
                  : user.me.positions.map(x => `${x['position']}(${x['org_name']})${x['is_part_time'] === true ? '(兼)' : ''}`).join()}
              </Typography>
              <Typography variant="subtitle1" gutterBottom>{user.me.full_name}</Typography>
            </div>
          </Container>
        </div>
        <Container className={classes.container}>
          <Tabs value={activeIndex} onChange={this.handleChangeTab}>
            <Tab label="稼働状況" component={Link} to={`/account/mypage/${user.me.member}/working-status`} />
            <Tab label="案件請求" component={Link} to={`/account/mypage/${user.me.member}/project-request`} />
            <Tab label="ＢＰ注文書" component={Link} to={`/account/mypage/${user.me.member}/partner-order`} />
            <Tab label="支払通知書" component={Link} to={`/account/mypage/${user.me.member}/payment-notice`} />
          </Tabs>
        </Container>
        <Switch>
          <Route exact path='/account/mypage/:member_id/working-status' component={WorkingStatusTab} />
          <Route exact path='/account/mypage/:member_id/project-request' component={UntreatedProjectRequestTab} />
          <Route exact path='/account/mypage/:member_id/partner-order' component={UntreatedPartnerOrderTab} />
          <Route exact path='/account/mypage/:member_id/payment-notice' component={UntreatedPaymentNoticeTab} />
          <Route component={HttpNotFound} />
        </Switch>
      </div>
    );
  }
}

export default withStyles(styles)(MyPage);
