import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import DataSource from '../../components/DataSource';
import {
  getPartnerCompany,
  getPartnerMember,
  getPartnerMemberContractTotal,
  getIndividualMember,
} from '../utils/api';
import { TableDetail, EnhancedTable } from '../../datatable';
import {
  partner_contract_detail_schema,
  member_monthly_request_list_schema,
  member_monthly_request_form_schema,
  member_monthly_request_form_layout,
} from './schema';
import { Button, Paper } from '@material-ui/core';
import { common, config, ajax, constant } from '../../utils';
import { withPermissions, HasPerm, Toast } from '../../components';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class PartnerContractDetail extends React.Component {

  constructor(props) {
    super(props);

    this.onEditMonthlyRequest = this.onEditMonthlyRequest.bind(this);
    this.state = {
      company: {},
      member: {},
      contract_total: {},
      reload: false,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { company_id, member_id, contract_id } = props.match.params;
    if (company_id) {
      getPartnerCompany(company_id).then(company => {
        this.setState({company});
      });
      getPartnerMember(company_id, member_id).then(member => {
        this.setState({member});
      });
    } else {
      getIndividualMember(member_id).then(member => {
        this.setState({member});
      });
    }
    getPartnerMemberContractTotal(member_id, contract_id).then(
      contract_total => this.setState({contract_total})
    ).catch(errors => {
      if (errors.status_code === 403) {
        this.props.forbidden(errors.detail);
      }
    });
  };

  componentDidUpdate = () => {
    const { company, member, contract_total } = this.state;
    console.log(contract_total)
    document.title = `${contract_total.contract_no} | ${member.full_name || ''} | ${company.name || '個人事業主'}`;
  };

  onEditMonthlyRequest = (data, handleClose) => {
    const { member_id, contract_id } = this.props.match.params;
    return ajax.fetchPut(
      common.formatStr(config.api.partner.member_monthly_request_detail, member_id, contract_id, data.id),
      data,
    ).then(() => {
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
      this.setState({reload: !this.state.reload});
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  handleRequestFormChanges = (name, data) => {
    const is_show_formula = data['is_show_formula'];
    const min_hours = data['min_hours'];
    const max_hours = data['max_hours'];
    const price = common.toNumComma(data['price']);
    const minus_per_hour = common.toNumComma(data['minus_per_hour']);
    const plus_per_hour = common.toNumComma(data['plus_per_hour']);
    if (name === 'minus_per_hour') {
      if (is_show_formula === true) {
        data['minus_per_hour_memo'] = `不足単価：￥${price}/${min_hours}h=￥${minus_per_hour}/h`;
      } else {
        data['minus_per_hour_memo'] = `不足単価：￥${minus_per_hour}/h`;
      }
    } else if (name === 'plus_per_hour') {
      if (is_show_formula === true) {
        data['plus_per_hour_memo'] = `超過単価：￥${price}/${max_hours}h=￥${plus_per_hour}/h`;
      } else {
        data['plus_per_hour_memo'] = `超過単価：￥${plus_per_hour}/h`;
      }
    } else if (name === 'is_show_formula') {
      if (is_show_formula === true) {
        data['minus_per_hour_memo'] = `不足単価：￥${price}/${min_hours}h=￥${minus_per_hour}/h`;
        data['plus_per_hour_memo'] = `超過単価：￥${price}/${max_hours}h=￥${plus_per_hour}/h`;
      } else {
        data['minus_per_hour_memo'] = `不足単価：￥${minus_per_hour}/h`;
        data['plus_per_hour_memo'] = `超過単価：￥${plus_per_hour}/h`;
      }
    }
  };

  render() {
    const { company_id, member_id, contract_id } = this.props.match.params;
    const { company, member, contract_total, reload } = this.state;
    const breadcrumbs = [];
    if (company_id) {
      breadcrumbs.push({ link: '/partner/companies?is_retired=false', name: '協力会社一覧' });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/`, name: company.name });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members`, name: '社員一覧' });
      breadcrumbs.push({ link: `/partner/companies/${company_id}/members/${member_id}`, name: member.full_name });
    } else {
      breadcrumbs.push({ link: '/partner/individual', name: '個人事業主一覧' });
      breadcrumbs.push({ link: `/partner/individual/${member_id}/`, name: member.full_name });
    }
    breadcrumbs.push({ name: contract_total.contract_no });

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={breadcrumbs}
        />
        <div style={{marginTop: 16}}>
          <TableDetail
            data={contract_total}
            schema={partner_contract_detail_schema}
            actions={[
              (
                <HasPerm permission='partner.change_partnermember' key='edit'>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => {
                      if (company_id) {
                        return common.redirect(`/partner/companies/${company_id}/members/${member_id}/contracts/${contract_id}/form`);
                      } else {
                        return common.redirect(`/partner/individual/${member_id}/contracts/${contract_id}/form`);
                      }
                    }} 
                  >変更</Button>
                </HasPerm>
              )
            ]}
          />
          <Paper style={paperSpacing}>
            <DataSource
              reload={reload}
              endpoint={common.formatStr(config.api.partner.member_monthly_request, member_id, contract_id)}
              render={(initData) => (
                <EnhancedTable
                  title='月別請求詳細'
                  showTitle={true}
                  tableHead={member_monthly_request_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  selectable="single"
                  editProps={{
                    title: '請求情報変更',
                    schema: member_monthly_request_form_schema,
                    layout: member_monthly_request_form_layout,
                    handleOk: this.onEditMonthlyRequest,
                    onChanges: [this.handleRequestFormChanges],
                    visible: common.hasPerm('partner.change_partnermember'),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </Paper>
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerContractDetail, 'partner.view_partnermember');
