import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import { list_company_schema } from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { withPermissions } from '../../components';

class ClientCompanyList extends React.Component {
  title = '取引先企業一覧';

  componentDidMount = () => {
    document.title = this.title;
  }

  render() {
    return (
      <div>
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={ config.api.client.list }
              render={ (initData) => (
                <EnhancedTable
                  storageKey='client.companies'
                  title={this.title}
                  tableHead={list_company_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '取引先企業を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect('/client/companies/add')),
                      'visible': common.hasPerm('client.add_clientcompany'),
                    }
                  ]}
                  allowCsv={true}
                  urlReflect={false}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 1000 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(ClientCompanyList, 'client.view_clientcompany');
