import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { withPermissions, ActionForm } from '../../components';
import { getProjectDetail, getProjectSubcontractorDetail, getProjectSubcontractorContractTotal } from '../utils';
import {
  blanket_contract_form_schema,
  blanket_contract_form_layout,
  contract_allowance_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
} from '../partner/schema';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { config, common } from '../../utils';

class SubcontractorContractForm extends React.Component {
  
  constructor(props) {
    super(props);

    this.state = {
      project: {},
      subcontractor: {},
      contract_data: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id, subcontractor_id, contract_id } = props.match.params;
    getProjectDetail(project_id).then(project => this.setState({project}));
    getProjectSubcontractorDetail(project_id, subcontractor_id).then(
      subcontractor => this.setState({subcontractor})
    );
    getProjectSubcontractorContractTotal(project_id, subcontractor_id, contract_id).then(
      contract_data => this.setState({contract_data})
    );
  };

  componentDidUpdate = () => {
    const { project, subcontractor, contract_data } = this.state;
    document.title = `${contract_data.contract_no} | 下請:${subcontractor.company_name} | 案件:${project.name}`;
  };

  render() {
    const { project_id, subcontractor_id, contract_id } = this.props.match.params;
    const { project, subcontractor, contract_data } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/project/projects', name: '案件一覧' },
            { link: `/project/projects/${project_id}`, name: project.name },
            { link: `/project/projects/${project_id}/subcontractors`, name: '下請業者一覧' },
            { name: subcontractor.company_name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={blanket_contract_form_schema}
                layout={blanket_contract_form_layout}
                inlines={[
                  {
                    name: 'allowances',
                    title: '精算条件',
                    schema: contract_allowance_schema,
                    allowAdd: false,
                    allowDelete: false,
                  },
                  {
                    name: "contract_items",
                    title: "契約条件",
                    schema: bp_contract_item_schema,
                    layout: bp_contract_item_layout,
                    allowAdd: false,
                    allowDelete: false,
                  },
                ]}
                data={contract_data}
                methodName={'POST'}
                saveEndpoint={common.formatStr(
                  config.api.project.subcontractor_contract_save, project_id, subcontractor_id, contract_id
                )}
                saveCallback={() => common.redirect(`/project/projects/${project_id}/subcontractors`)}
                deleteEndpoint={common.formatStr(
                  config.api.project.subcontractor_contract_detail, project_id, subcontractor_id, contract_id
                )}
                deleteCallback={() => common.redirect(`/project/projects/${project_id}/subcontractors`)}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(SubcontractorContractForm, 'project.change_projectsubcontractor');
