import React from 'react';
import { withRouter } from 'react-router-dom';
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types';
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Popper,
  Grow,
  Card,
  CardHeader,
  CardActions,
  Avatar,
  Tooltip,
  IconButton,
  CardContent,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Divider,
  Collapse,
} from '@material-ui/core';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AccountCircle from '@material-ui/icons/AccountCircle';
import PersonIcon from '@material-ui/icons/Person';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { common } from '../utils';

const styles = (theme) => ({
  popper: {
    width: 330,
  },
  cardContent: {
    padding: 0,
  },
  avatar: {
    border: 1,
    '& svg': {
      fontSize: '48px !important;',
    },
  },
  listRoot: {
    margin: 0,
    padding: 0,
    position: 'relative',
    listStyle: 'none',
  },
  listIcon: {
    backgroundImage: 'linear-gradient(180deg, #1976d2 0%, #0d47a1 100%)',
  },
  nested: {
    paddingLeft: theme.spacing(9),
  },
});

class AccountInfo extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      mypage_expanded: true,
      anchorEl: null,
    };
  }

  handleOpen = (anchorEl) => {
    this.setState({open: true, anchorEl});
  };

  handleClose = () => {
    this.setState({open: false});
  };

  handleExpandMyPage = () => {
    this.setState(state => {
      return { mypage_expanded: !state.mypage_expanded };
    });
  }

  render () {
    const { classes, user } = this.props;
    const { open, anchorEl, mypage_expanded } = this.state;

    if (anchorEl) {
      return (
        <Popper
          open={open}
          anchorEl={anchorEl}
          transition
          disablePortal
          placement='bottom-end'
          className={classes.popper}
        >
          {({ TransitionProps }) => (
            <Grow {...TransitionProps} timeout={350}>
              <Card className={classes.card} elevation={8}>
                <CardHeader
                  avatar={
                    <Avatar aria-label="Recipe" className={classes.avatar}>
                      {user.me.avatar ? (
                        <img src={user.me.avatar} alt='Avatar' width={40} />
                      ) : <AccountCircle />}
                    </Avatar>
                  }
                  title={user.me.full_name}
                  subheader={
                    common.isEmpty(user.me.positions)
                      ? user.me.email
                      : user.me.positions.map(x => `${x['position']}(${x['org_name']})${x['is_part_time'] === true ? '(兼)' : ''}`).join()
                  }
                />
                <CardContent className={classes.cardContent}>
                  <List className={classes.listRoot}>
                    <Divider component="li" />
                    { !common.isEmpty(user.me.member) ? (
                      <>
                        <ListItem button onClick={this.handleExpandMyPage}>
                          <ListItemAvatar>
                            <Avatar className={classes.listIcon}>
                              <PersonIcon/>
                            </Avatar>
                          </ListItemAvatar>
                          <ListItemText
                            primary='マイページ'
                          />
                          {mypage_expanded ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={mypage_expanded} timeout='auto' unmountOnExit>
                          <List disablePadding>
                            <ListItem
                              button
                              component={Link}
                              className={classes.nested}
                              to={`/account/mypage/${user.me.member}/working-status`}
                              onClick={this.handleClose}
                            >
                              <ListItemText primary='稼働状況' />
                            </ListItem>
                            <ListItem
                              button
                              component={Link}
                              className={classes.nested}
                              to={`/account/mypage/${user.me.member}/project-request`}
                              onClick={this.handleClose}
                            >
                              <ListItemText primary='案件請求' />
                            </ListItem>
                            <ListItem
                              button
                              component={Link}
                              className={classes.nested}
                              to={`/account/mypage/${user.me.member}/partner-order`}
                              onClick={this.handleClose}
                            >
                              <ListItemText primary='ＢＰ注文書' />
                            </ListItem>
                            <ListItem
                              button
                              component={Link}
                              className={classes.nested}
                              to={`/account/mypage/${user.me.member}/payment-notice`}
                              onClick={this.handleClose}
                            >
                              <ListItemText primary='支払通知書' />
                            </ListItem>
                          </List>
                        </Collapse>
                        <Divider component="li" />
                      </>
                    ) : null }
                  </List>
                </CardContent>
                <CardActions disableSpacing>
                  <div style={{flex: 1}} />
                  <Tooltip title='ログアウト' placement='bottom' enterDelay={300}>
                    <IconButton aria-label="logout" onClick={() => this.props.logout(this.props.location)}>
                      <ExitToAppIcon />
                    </IconButton>
                  </Tooltip>
                </CardActions>
              </Card>
            </Grow>
          )}
        </Popper>
      );
    } else {
      return null;
    }
  }
}

AccountInfo.propTypes = {
  classes: PropTypes.object.isRequired,
  user: PropTypes.object.isRequired,
}

export default withRouter(withStyles(styles)(AccountInfo));
