import React from 'react';
import { Paper } from '@material-ui/core';
import withStyles from "@material-ui/core/styles/withStyles";
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import { HierarchyTable } from '../../datatable/index';
import DataSource from '../../components/DataSource';
import { list_organization_schema } from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { withPermissions } from '../../components';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

const styles = (theme) => ({
  paper: {
    ...paperSpacing,
  },
});

class OrganizationList extends React.Component {
  title = '部署一覧';

  componentDidMount = () => {
    document.title = this.title;
  };

  render() {
    const { classes } = this.props;

    return (
      <Paper className={classes.paper}>
        <DataSource
          endpoint={ config.api.organization.portal_list }
          render={ (initData) => (
            <HierarchyTable
              title={this.title}
              tableHead={list_organization_schema}
              tableData={initData.results}
              rowsPerPage={config.rowsPerPage}
              pushpinTop={common.getHeaderHeight()}
              allowCsv={true}
              relatedName='parent_id'
              tableActions={[
                {
                  'tooltip': '部署追加',
                  'icon': <AddIcon color="secondary"/>,
                  'handleClick': () => (common.redirect('/organization/add/')),
                  'visible': common.hasPerm('org.add_organization'),
                },
              ]}
              rowActions={[
                {
                  'tooltip': '部署変更',
                  'icon': <EditIcon color="primary" />,
                  'handleClick': (row) => (common.redirect(`/organization/${row.id}/edit/`)),
                  'visible': common.hasPerm('org.change_organization'),
                },
              ]}
              tableStyles={{ minWidth: 400 }}
            />
          ) } 
        />
      </Paper>
    );
  }
}

export default withPermissions(withStyles(styles)(OrganizationList), 'org.view_organization');
