/**
 * メンバーにアサインした案件一覧を取得する。
 * @param {Integer} ct メンバー種類（社員または協力社員）
 * @param {Integer} pk メンバーID
 */
export function getMemberProjectTimeline(ct, pk) {
  return ajax.fetchGet(common.formatStr(config.api.member.member_projects, ct, pk)).then(data => {
    const timelines = [];
    data.map(row => (
      timelines.push({
        start: row.start_date,
        end: row.end_date,
        text: row.project_name,
        link: `/project/projects/${row.project_id}`,
      })
    ));
    return timelines;
  });
};

