/**
 * カスケード式の選択
 * @param {String} current_name 現在変更中の項目
 * @param {String} content_type_name カスケード選択の項目名
 * @param {Any} content_type_value 選択元項目の値
 * @param {Object} schema フォームのスキーマ
 */
export function getCascadeObjects(current_name, content_type_name, content_type_value, schema) {
  if (current_name === content_type_name) {
    return getContentObjects(content_type_value).then(object_list => {
      common.cascade_reflect(object_list, schema, content_type_name);
    });
  } else {
    return null;
  }
};

