/**
 * HttpApiを呼び出し、オブジェクトを追加する。
 * @param {function} callback コールバックメソッド
 * @param {String} url Http ApiのＵＲＬ
 */
export function handleAddObject(callback, url) {
  const params = Array.from(arguments).slice(2);
  return function(data, handleClose) {
    if (typeof url === 'function') {
      url = url(data);
    }
    return ajax.fetchPost(common.formatStr(url, ...params), data).then((added_data) => {
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
      if (callback) {
        callback(added_data);
      }
    }).catch(errors => {
      if (errors.detail) {
        Toast.error(errors.detail);
      } else {
        Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      }
      return Promise.reject(errors);
    });
  };
};

/**
 * HttpApiを呼び出し、オブジェクトを変更する。
 * @param {function} callback コールバックメソッド
 * @param {String} url Http ApiのＵＲＬ
 */
export function handleEditObject(callback, url) {
  const params = Array.from(arguments).slice(2);
  return function(data, handleClose) {
    if (typeof url === 'function') {
      url = url(data);
    }
    return ajax.fetchPut(common.formatStr(url, ...params, data.id), data).then(() => {
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
      if (callback) {
        callback(data);
      }
    }).catch(errors => {
      if (errors.detail) {
        Toast.error(errors.detail);
      } else {
        Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      }
      return Promise.reject(errors);
    });
  };
};

/**
 * HttpApiを呼び出し、オブジェクトを削除する。
 * @param {function} callback コールバックメソッド
 * @param {String} url Http ApiのＵＲＬ
 */
export function handleDeleteObject(callback, url) {
  const params = Array.from(arguments).slice(2);
  return async function(data, handleClose) {
    let success = false;
    if (typeof url === 'function') {
      url = url(data);
    }
    await ajax.fetchDelete(common.formatStr(url, ...params, (data && data.id)), data).then(() => {
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.DELETED);
      if (callback) {
        callback();
      }
      success = true;
    }).catch(errors => {
      if (errors.detail) {
        Toast.error(errors.detail);
      } else {
        Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      }
      success = false;
      return Promise.reject(errors);
    });
    return success;
  };
};

export function getDuplicatedProjectMemberSync(project_id, member_content_type, member_id, start_date, end_date) {
  const res = request('POST', common.formatStr(config.api.project.duplicated_project_members_for_add, project_id), {
    headers: authHeader(),
    json: {
      member_content_type_id: member_content_type,
      member_id,
      start_date,
      end_date,
    },
  });
  const data = JSON.parse(res.getBody('utf8'));
  if (res.statusCode === 200) {
    return data;
  } else {
    Toast.error(data.detail);
    return false;
  }
};

export function getAddressByPostcodeSync(postcode) {
  const res = request('GET', common.formatStr(config.api.third_part.get_address_by_postcode, postcode), {
    headers: authHeader(),
  });
  const data = JSON.parse(res.getBody('utf8'));
  if (res.statusCode === 200) {
    return data.length > 0 ? data[0] : null;
  } else {
    Toast.error(data.detail);
    return false;
  }
};

