import React from 'react';
import { Paper } from '@material-ui/core';
import {
  withPermissions,
  TopBreadcrumbs,
} from '../../components';
import PaymentNoticeByMonth from './payment_notice_by_month';
import {
  getOrganization,
  getIndividualMember,
} from '../utils';
import { common, config } from '../../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class IndividualPaymentNotice extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      member: {},
      organization: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { member_id, org_id } = props.match.params;
    getIndividualMember(member_id).then(member => {
      this.setState({member});
    });
    getOrganization(org_id).then(organization => {
      this.setState({organization});
    });
  };

  componentDidUpdate = () => {
    const { member } = this.state;
    document.title = `支払通知書・ＢＰ請求書 | ${member.full_name || ''} | 個人事業主`;
  };

  render() {
    const { member_id, year, month, org_id } = this.props.match.params;
    const { member } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/individual', name: '個人事業主一覧' },
            { link: `/partner/individual/${member_id}`, name: member.full_name },
            { name: `${year}年${month}月 ${member.full_name}の支払通知書・ＢＰ請求書` },
          ]}
        />
        <Paper style={paperSpacing}>
          {member.content_type ? (
            <PaymentNoticeByMonth
              title={`${year}年${month}月 ${member.full_name}の支払通知書・ＢＰ請求書`}
              content_type={member.content_type}
              object_id={member_id}
              member_id={member_id}
              org_id={org_id}
              year={year}
              month={month}
              endpoint={common.formatStr(config.api.partner.individual_payment_notice_by_month, member_id, year, month, org_id)}
            />
          ) : null}
        </Paper>
      </div>
    );
  }
}

export default withPermissions(IndividualPaymentNotice, 'partner.view_partnerrequest');
