import React from 'react';
import {
  Paper,
  FormControl,
  TextField,
  withStyles,
  Button,
  Avatar,
  CssBaseline,
  Typography,
} from '@material-ui/core';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import { styles } from '../../components/Login';
import { sendPasswordReset, initPasswordReset } from '../utils/api';
import { Toast, HttpStatus } from '../../components';
import { constant, common } from '../../utils';
import { Link } from 'react-router-dom';

class PasswordReset extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      password: '',
      password_confirm: '',
      security_code: '',
      timeout: false,
      timeout_msg: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.initialize(props);
  }

  initialize = (props) => {
    const { user, signature } = props.match.params;
    initPasswordReset(user, signature).catch(errors => (
      this.setState({ timeout: true, timeout_msg: errors.detail })
    ));
  };

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { user, signature } = this.props.match.params;
    const { password, password_confirm, security_code } = this.state;
    if (!password || !password_confirm || !security_code) {
      Toast.error(constant.ERROR.REQUIRE_PASSWORD_AND_SECURITY_CODE);
    } else if ( password === password_confirm ) {
      sendPasswordReset(password, user, signature, security_code).then(() => {
        common.redirect('/login');
        Toast.success(constant.SUCCESS.PASSWORD_CONFIRM);
      }).catch(errors => {
        Toast.error(errors.detail);
        if (errors.redirect) {
          common.redirect(errors.redirect);
        }
      });
    } else {
      Toast.error(constant.ERROR.PASSWORD_CONFIRM);
    }
  };
  
  render() {
    const { classes } = this.props;
    const { timeout, timeout_msg } = this.state;

    return (
      <React.Fragment>
        <CssBaseline />
        { timeout === false ? (
          <main className={classes.layout}>
            <Paper className={classes.paper}>
              <Avatar className={classes.avatar}>
                <VpnKeyIcon />
              </Avatar>
              <Typography variant="subtitle1">新しいパスワードを設定してください。</Typography>
              <form className={classes.form}>
                <FormControl margin="normal" required fullWidth>
                  <TextField name="password" label="パスワード" type="password" onChange={this.handleChange} />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                  <TextField name="password_confirm" label="パスワード確認" type="password" onChange={this.handleChange} />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                  <TextField name="security_code" label="認証コード" onChange={this.handleChange} type='number' 
                    placeholder='パスワード忘れ画面と同じ４桁数字を入力'
                    helperText="パスワード忘れ画面と同じ４桁数字を入力"
                    onInput={(e)=>{ 
                      if (e.target.value !== '' && !isNaN(parseInt(e.target.value))) {
                        e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,4)
                      }
                    }}
                  />
                </FormControl>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                  onClick={this.handleSubmit}
                >
                  送信
                </Button>
              </form>
            </Paper>
          </main>
        ) : (
          <div className={classes.timeout}>
            <HttpStatus statusCode={403} message={timeout_msg} />
            <Link to="/password/forget">パスワード忘れへ</Link>
            <Link to="/login">ログインへ</Link>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(PasswordReset);
