import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { company_form_schema, company_form_layout } from './schema';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { withPermissions } from '../../components';

class PartnerCompanyForm extends React.Component {
  title = '協力会社追加';

  componentDidMount = () => {
    document.title = `${this.title}`;
  };

  render() {
    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={company_form_schema}
                layout={company_form_layout}
                methodName={'POST'}
                saveEndpoint={config.api.partner.add}
                saveCallback={() => common.redirect('/partner/companies')}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerCompanyForm, 'partner.add_partnercompany');
