import { userConstants } from '../constants';

const initialState = {
  loading: false,
  error: false,
  me: {},
  perms: {},
  notifications: [],
};

export function user(state = initialState, action) {
  switch (action.type) {
    case userConstants.GET_ME_REQUEST:
      return {
        ...state,
        loading: true,
        error: false,
      };
    case userConstants.GET_ME_SUCCESS:
      return {
        ...state,
        loading: false,
        me: action.payload.me,
        perms: action.payload.perms,
        notifications: action.payload.notifications,
      };
    case userConstants.GET_ME_FAILURE:
      return {
        ...state,
        loading: false,
        error: true,
      };
    case userConstants.CLEAR_ME:
      return {
        ...state,
        me: {},
      };
    case userConstants.NOTIFICATION_ADD:
      state.notifications.push(action.notification)
      return {
        ...state,
        notifications: state.notifications.slice(),
      };
    case userConstants.NOTIFICATION_CHANGE:
      const items = state.notifications.filter(col => col['id'] === action.notification.id);
      if (items.length > 0) {
        Object.assign(items[0], action.notification);
      }
      return {
        ...state,
        notifications: state.notifications.slice(),
      };
    case userConstants.NOTIFICATION_RELOAD:
      return {
        ...state,
        notifications: action.notifications,
      }
    default:
      return state
  }
}
