import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import { Paper, withStyles } from '@material-ui/core';
import {
  TopBreadcrumbs,
  DataSource,
  withPermissions,
} from '../../components';
import {
  org_positionship_list_schema,
  positionship_form_schema,
} from './schema';
import {
  getOrganization,
  getMemberChoices,
  handleAddObject,
  handleEditObject,
  handleDeleteObject,
} from '../utils';
import { common, config } from '../../utils';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

const styles = (theme) => ({
  paper: {
    ...paperSpacing,
  }
})

class OrgPositionList extends React.Component {
  positionship_form_schema = common.clone(positionship_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      organization: {},
      members: [],
      reload: false,
    };
    this.initialize(props);
　}

  initialize = (props) => {
    const { org_id } = props.match.params;
    getOrganization(org_id).then(organization => {
      this.setState({organization});
    });
    getMemberChoices().then(members => this.setState({members}));
  };

  componentDidUpdate = () => {
    const { organization } = this.state;
    document.title = `${organization.name} の職位一覧`;
  };

  render() {
    const { org_id } = this.props.match.params;
    const { classes } = this.props;
    const { organization, reload, members } = this.state;
    common.getFromJsonList(this.positionship_form_schema, 'name', 'member')['choices'] = members;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/organization', name: '部署一覧' },
            { link: `/organization/${org_id}`, name: organization.name },
            { name: '職位一覧' },
          ]}
        />
        <Paper className={classes.paper}>
          <DataSource
            reload={reload}
            endpoint={common.formatStr(config.api.organization.position_list, org_id)}
            render={ (initData) => (
              <EnhancedTable
                title={`${organization.name} の職位一覧`}
                showTitle={true}
                tableHead={org_positionship_list_schema}
                tableData={initData.results}
                rowsPerPage={config.rowsPerPage}
                allowCsv={true}
                pushpinTop={common.getHeaderHeight()}
                selectable='single'
                addProps={{
                  title: '職位追加',
                  schema: this.positionship_form_schema,
                  handleOk: handleAddObject(
                    () => this.setState({reload: !reload}),
                    common.formatStr(config.api.organization.position_add, org_id),
                  ),
                  visible: common.hasPerm('org.add_positionship'),
                  data: {
                    organization: org_id,
                    end_date: '9999-12-31',
                  },
                }}
                editProps={{
                  title: '職位変更',
                  schema: this.positionship_form_schema,
                  handleOk: handleEditObject(
                    () => this.setState({reload: !reload}),
                    config.api.organization.position_detail,
                    org_id,
                  ),
                  visible: common.hasPerm('org.change_positionship'),
                }}
                deleteProps={{
                  handleDelete: handleDeleteObject(
                    () => this.setState({reload: !reload}),
                    config.api.organization.position_detail,
                    org_id,
                  ),
                  visible: common.hasPerm('org.delete_positionship'),
                }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(withStyles(styles)(OrgPositionList), 'org.view_positionship');
