import React from 'react';
import { Button } from '@material-ui/core';
import {
  withPermissions,
  HasPerm,
  TopBreadcrumbs,
  Card,
  CardHeader,
  CardBody,
  Toast,
} from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { TableDetail } from '../../datatable';
import { getPartnerCompany } from '../utils/api';
import {
  company_form_schema,
  company_form_layout,
} from './schema';
import { config } from '../../utils/config';
import { ajax, common } from '../../utils/common';
import { constant } from '../../utils/constants';
import OrderStatusList from './order_status_list';
import PaymentNoticeStatus from './payment_notice_status';

class PartnerCompanyDetail extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      company: {},
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.partner.detail,
      props.match.params.company_id
    );
  }

  initialize = (props) => {
    const { params } = props.match;
    getPartnerCompany(params.company_id).then(company => {
      this.setState({company});
    });
  };

  componentDidUpdate = () => {
    document.title = `${this.state.company.name || ''} | 協力会社`;
  };

  onEditCompany = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(company => {
      this.setState({company});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteCompany = (data) => {
    ajax.fetchDelete(this.detail_api).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect('/partner/companies')
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { company_id } = this.props.match.params;
    const { company } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { name: company.name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={company}
                schema={company_form_schema}
                editProps={{
                  title: '協力会社情報変更',
                  schema: company_form_schema,
                  layout: company_form_layout,
                  handleEdit: this.onEditCompany,
                  visible: common.hasPerm('partner.change_partnercompany'),
                }}
                deleteProps={{
                  handleDelete: this.onDeleteCompany,
                  visible: common.hasPerm('partner.delete_partnercompany'),
                }}
                actions={[
                  (
                    <HasPerm permission='master.view_bankaccount' key="bank-accounts">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => common.redirect(
                          `/partner/companies/${company_id}/bank-accounts`
                        )}
                      >
                        口座一覧
                      </Button>
                    </HasPerm>
                  ),
                  (
                    <HasPerm permission='partner.view_partnermember' key="list">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => common.redirect(
                          `/partner/companies/${company_id}/members`
                        )}
                      >
                        社員一覧
                      </Button>
                    </HasPerm>
                  ),
                  (
                    <HasPerm permission='partner.add_partnermember' key="add">
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => common.redirect(
                          `/partner/companies/${company_id}/members/add`
                        )}
                      >
                        社員追加
                      </Button>
                    </HasPerm>
                  ),
                ]}
              />
            </GridItem>
          </GridContainer>
          <HasPerm permission='partner.view_partnerorder'>
            <Card>
              <CardHeader color="info">注文書一覧</CardHeader>
              <CardBody>
                <OrderStatusList
                  endpoint={common.formatStr(config.api.partner.company_orders_status, company_id)}
                  filters={common.getNextMonthDict()}
                />
              </CardBody>
            </Card>
          </HasPerm>
          <HasPerm permission='partner.view_partnerrequest'>
            <Card>
              <CardHeader color="info">支払通知と請求一覧</CardHeader>
              <CardBody>
                {company.content_type ? (
                  <PaymentNoticeStatus
                    content_type={company.content_type}
                    object_id={company_id}
                    endpoint={common.formatStr(config.api.partner.company_payment_notice_status, company_id)}
                  />
                ) : null }
              </CardBody>
            </Card>
          </HasPerm>
        </div>
      </div>
    );
  }
}

export default withPermissions(
  PartnerCompanyDetail,
  'partner.view_partnercompany',
);
