import React from "react";
import moment from "moment";
import PropTypes from "prop-types";
import TimelineItem from "./TimelineItem";
import { useStyles } from './style';

/**
 * Converts array of events in to object having date as the key and list of
 * event for that date as the value
 *
 * @param {Array} items Array of events in the form of ts and text
 * @returns {Object} return object with key as date and values array in events for that date
 */
function getFormattedData(items, showEnd) {
  const activities = {};
  items.forEach(({ start, end, text, link }, index) => {
    const start_date = moment(start);
    let dateStr = start_date.format("YYYY-MM-DD");
    if (showEnd === true && end) {
      const end_date = moment(end);
      dateStr = `${dateStr}～${end_date.format("YYYY-MM-DD")}`
    }
    const list = activities[dateStr] || [];
    list.push({
      time: start_date.format("hh:mm"),
      text,
      link,
      key: index,
    });
    activities[dateStr] = list;
  });
  return activities;
}

function Timeline({ items, showEnd, ...rest }) {
  const classes = useStyles();
  const activities = getFormattedData(items, showEnd);
  const dates = Object.keys(activities);

  return (
    <div className={classes.timeLineCtnr}>
      {dates.map(d => (
        <React.Fragment key={d}>
          <div className='time-line-before' />
          <ul className="time-line">
            <li className="time-label">
              <span>{d}</span>
            </li>
            {activities[d].map(({ time, text, link, key }) => (
              <TimelineItem time={time} text={text} key={key} link={link} {...rest} />
            ))}
          </ul>
        </React.Fragment>
      ))}
    </div>
  );
}

Timeline.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      start: PropTypes.string.isRequired,
      end: PropTypes.string,
      text: PropTypes.string.isRequired,
    })
  ).isRequired,
  showTime: PropTypes.bool,
  showEnd: PropTypes.bool,
};

Timeline.defaultProps = {
  showTime: true,
  showEnd: false,
}

export default Timeline;
