import React from 'react';
import PropTypes from "prop-types";
import {
  Dialog,
  withStyles,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Radio,
  Typography,
} from '@material-ui/core';
import { Toast } from '../../components';
import { constant, common } from '../../utils';

const styles = theme => ({
  fullScreen: {
    [theme.breakpoints.down('xs')]: {
      width: '100%',
      height: '100%',
      margin: 0,
      maxWidth: '100%',
      maxHeight: 'none',
      borderRadius: 0,
    },
  },
  dialogContent: {
    paddingLeft: theme.spacing(0.5),
    paddingRight: theme.spacing(0.5),
  },
  table: {
    '& th, & td': {
      paddingLeft: 0,
      paddingRight: theme.spacing(0.5),
    },
  },
  description: {
    margin: '16px 16px 0',
  },
});

class DuplicatedMemberDialog extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      full_name: null,
      data: [],
      selected: null,
    };
  }

  handleOpen = (full_name, data) => (this.setState({open: true, full_name, data}));

  handleClose = () => (this.setState({open: false}));

  handleChange = (event) => this.setState({selected: event.target.value});

  handleSelect = (value) => this.setState({selected: value});

  handleSame = () => {
    const { handleSelected } = this.props;
    const { data, selected } = this.state;
    if (selected) {
      if (handleSelected) {
        const retVal = common.clone(common.getFromJsonList(data, 'id', selected));
        const duplication = {
          content_type_id: retVal.content_type_id,
          object_id: retVal.object_id,
        }
        delete retVal.id;
        delete retVal.code;
        delete retVal.object_id;
        delete retVal.content_type_id;
        delete retVal.company_id;
        delete retVal.company_name;
        delete retVal.project_id;
        delete retVal.project_name;
        delete retVal.organization_id;
        delete retVal.organization_name;
        delete retVal.member_type;
        retVal.duplication = duplication;
        handleSelected(retVal);
        this.handleClose();
      } else {
        Toast.error(constant.ERROR.NO_CALLBANK);
      }
    } else {
      Toast.error(constant.ERROR.REQUIRE_SELECTED);
    }
  };

  render() {
    const { classes } = this.props;
    const { open, full_name, data, selected } = this.state;

    return (
      <Dialog
        open={open}
        onClose={this.handleClose}
        PaperProps={{className: classes.fullScreen}}
      >
        <DialogTitle>
          {`${full_name} は既に存在します：`}
        </DialogTitle>
        <DialogContent dividers className={classes.dialogContent}>
          <Table size="small" className={classes.table}>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell>番号</TableCell>
                <TableCell>所属会社</TableCell>
                <TableCell>最近の案件</TableCell>
                <TableCell>部署</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.map((row, key) => (
                <TableRow hover key={key} onClick={() => this.handleSelect(row.id)}>
                  <TableCell padding="none">
                    <Radio
                      checked={selected === row.id}
                      value={row.id}
                      size="small"
                      // onChange={this.handleChange}
                    />
                  </TableCell>
                  <TableCell>{row.code}</TableCell>
                  <TableCell>{row.company_name}</TableCell>
                  <TableCell>{row.project_name}</TableCell>
                  <TableCell>{row.organization_name}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <Typography variant="body2" className={classes.description}>
            ※ 別人だったら「違う人」ボタンを選択してください。<br/>
            ※ 同じ人だったら「同じ人」ボタンを選択し、以前記入済みのデータをそのまま使われます。
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose} color='secondary'>違う人</Button>
          <Button onClick={this.handleSame} color='primary'>同じ人</Button>
        </DialogActions>
      </Dialog>
    );
  }
}

DuplicatedMemberDialog.propTypes = {
  classes: PropTypes.object.isRequired,
  handleSelected: PropTypes.func.isRequired,
};

export default withStyles(styles)(DuplicatedMemberDialog);
