import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import { list_salesperson_schema } from './schema';
import { common } from '../../utils';
import { config } from '../../utils/config';
import { withPermissions } from '../../components';

class SalespersonList extends React.Component {
  title = '営業員一覧';

  componentDidMount = () => {
    document.title = this.title;
  };

  render () {

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={ config.api.salesperson.salesperson_portal_list }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  tableHead={list_salesperson_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  toolbar={false}
                  pushpinTop={common.getHeaderHeight()}
                  allowCsv={true}
                  urlReflect={false}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(SalespersonList, 'member.view_salesperson');
