import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import BlockIcon from '@material-ui/icons/Block';
import { EnhancedTable, FormDialog } from '../../datatable/index';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import {
  project_list_schema,
  project_list_search_layout,
} from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import {
  getOrganizationChoices,
  getProjectBusinessTypeChoice,
  getSalespersonChoice,
  projectFinish,
} from '../utils/api';
import { withPermissions, Toast } from '../../components';
import { constant } from '../../utils';

class ProjectList extends React.Component {
  title = '案件一覧';

  constructor(props) {
    super(props);

    this.state = {
      organizations: [],
      business_types: [],
      salesperson_choice: [],
      reload: false,
    };
    this.initialize();
  }

  initialize = () => {
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    getProjectBusinessTypeChoice().then(business_types => {
      this.setState({business_types});
    });
    getSalespersonChoice().then(salesperson_choice => this.setState({salesperson_choice}));
  };

  componentDidMount = () => {
    document.title = this.title;
  }

  handleProjectFinish = (data) => {
    return projectFinish(data.id, data).then(() => {
      Toast.success(constant.SUCCESS.SAVED);
      this._closeFinshDialog && this._closeFinshDialog();
      this.setState(state => {
        return {reload: !state.reload};
      })
    }).then(errors => Toast.error(errors.detail));
  }

  render() {
    const { organizations, business_types, salesperson_choice, reload } = this.state;

    const list_schema = common.clone(project_list_schema);
    common.getFromJsonList(list_schema, 'name', 'business_type').choices = business_types;
    common.getFromJsonList(list_schema, 'name', 'organization').choices = organizations;
    common.getFromJsonList(list_schema, "name", "salesperson")["choices"] = salesperson_choice;

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={ config.api.project.list }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  storageKey='project.projects'
                  tableHead={list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '案件を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect('/project/projects/add')),
                      'visible': common.hasPerm('project.add_project'),
                    }
                  ]}
                  allowCsv={true}
                  urlReflect={false}
                  pushpinTop={common.getHeaderHeight()}
                  selectable='single'
                  tableStyles={{ minWidth: 1000 }}
                  rowActions={[
                    {
                      'tooltip': '案件を終了する',
                      'icon': <BlockIcon />,
                      'handleClick': (data) => {
                        if (this._openFinshDialog) {
                          this._openFinshDialog({'id': data.id, 'finish_date': data.end_date});
                        }
                      },
                      'visible': common.hasPerm('project.change_project'),
                    }
                  ]}
                  filterLayout={project_list_search_layout}
                />
              ) } 
            />
          </CardBody>
        </Card>
        <FormDialog
          title='案件を終了'
          schema={[{
            "name": "finish_date",
            "type": "date",
            "label": "終了日",
            "required": true,
          }]}
          handleOk={this.handleProjectFinish}
          innerRef={dlg => {
            this._openFinshDialog = dlg && dlg.handleOpen;
            this._closeFinshDialog = dlg && dlg.handleClose;
          }}
        />
      </div>
    );
  }
}

export default withPermissions(ProjectList, 'project.view_project');
