import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import Card from '../../components/Card/Card';
import CardHeader from '../../components/Card/CardHeader';
import CardBody from '../../components/Card/CardBody';
import DataSource from '../../components/DataSource';
import { common, config, ajax, constant } from '../../utils';
import {
  getPartnerCompany,
  getPartnerMember,
  getPartnerMemberChoice,
  getPartnerMemberProjectTimeline,
} from '../utils/api';
import { TableDetail, EnhancedTable } from '../../datatable';
import {
  member_form_basic_schema,
  member_form_basic_layout,
  bp_member_contract_list_schema,
  member_detail_schema,
} from './schema';
import OrderStatusList from './order_status_list';
import { withPermissions, HasPerm, Timeline, Toast } from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { onBlurPostcode } from '../utils';

class PartnerMemberDetail extends React.Component {
  member_form_basic_schema = common.clone(member_form_basic_schema);

  constructor(props) {
    super(props);

    this.state = {
      company: {},
      member: {},
      members_choice: [],
      member_projects: [],
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.partner.company_member,
      props.match.params.company_id,
      props.match.params.member_id,
    );
  }

  initialize = (props) => {
    const { company_id, member_id } = props.match.params;
    getPartnerCompany(company_id).then(company => {
      this.setState({company});
    });
    getPartnerMember(company_id, member_id).then(member => {
      this.setState({member});
    });
    getPartnerMemberChoice(company_id).then(members_choice => this.setState({members_choice}));
    getPartnerMemberProjectTimeline(member_id).then(member_projects => {
      this.setState({ member_projects });
    });
  };

  componentDidUpdate = () => {
    document.title = `${this.state.member.full_name || ''} | ${this.state.company.name || ''} | 協力会社`;
  };

  onEditCompany = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(member => {
      this.setState({member});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteCompany = () => {
    const { company_id } = this.props.match.params;
    ajax.fetchDelete(this.detail_api).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect(`/partner/companies/${company_id}/members`);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { company_id, member_id } = this.props.match.params;
    const { company, member, members_choice, member_projects } = this.state;

    // 社員番号は変更できないように
    common.getFromJsonList(this.member_form_basic_schema, 'name', 'code')['read_only'] = true;
    // 所属設定
    common.getFromJsonList(this.member_form_basic_schema, 'name', 'company')['choices'] = [{
      'value': parseInt(company_id),
      'display_name': company.name,
    }];
    common.getFromJsonList(this.member_form_basic_schema, 'name', 'company')['read_only'] = true;
    common.getFromJsonList(this.member_form_basic_schema, 'name', "middleman")['choices'] = members_choice;
    common.getFromJsonList(member_detail_schema, 'name', 'company')['choices'] = [{
      'value': parseInt(company_id),
      'display_name': company.name,
    }];

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { link: `/partner/companies/${company_id}/`, name: company.name },
            { link: `/partner/companies/${company_id}/members`, name: '社員一覧' },
            { name: member.full_name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={member}
                schema={member_detail_schema}
                editProps={{
                  title: '社員情報変更',
                  schema: this.member_form_basic_schema,
                  layout: member_form_basic_layout,
                  handleEdit: this.onEditCompany,
                  visible: common.hasPerm('partner.change_partnermember'),
                  onBlurs: [onBlurPostcode],
                }}
                deleteProps={{
                  handleDelete: this.onDeleteCompany,
                  visible: common.hasPerm('partner.delete_partnermember'),
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={3} lg={3}>
              <Card profile>
                <CardHeader>アサインした案件</CardHeader>
                <CardBody>
                  { !common.isEmpty(member_projects) ? (
                    <Timeline items={member_projects} showTime={false} />
                  ) : null }
                </CardBody>
              </Card>
            </GridItem>
          </GridContainer>
          <Card>
            <CardHeader color="info">契約一覧</CardHeader>
            <CardBody>
              <DataSource
                endpoint={common.formatStr(config.api.partner.member_contracts, company_id, member_id)}
                render={(initData) => (
                  <EnhancedTable
                    tableHead={bp_member_contract_list_schema}
                    tableData={initData}
                    rowsPerPage={config.rowsPerPage}
                    tableActions={[
                      {
                        'tooltip': 'ＢＰ契約を追加する',
                        'icon': <AddIcon color="secondary"/>,
                        'handleClick': () => (common.redirect(`/partner/companies/${company_id}/members/${member_id}/contracts/add`)),
                        'visible': common.hasPerm('partner.change_partnermember'),
                      },
                    ]}
                    rowActions={[
                      {
                        'tooltip': "ＢＰ契約を変更する",
                        "icon": <EditIcon />,
                        "handleClick": (data) => common.redirect(`/partner/companies/${company_id}/members/${member_id}/contracts/${data.id}/form`),
                        'visible': common.hasPerm('partner.change_partnermember'),
                      },
                    ]}
                    selectable='single'
                    pushpinTop={common.getHeaderHeight()}
                  />
                )}
              />
            </CardBody>
          </Card>
          <HasPerm permission='partner.view_partnerorder'>
            <Card>
              <CardHeader color="info">注文書一覧</CardHeader>
              <CardBody>
                <OrderStatusList
                  endpoint={common.formatStr(config.api.partner.member_orders_status, company_id, member_id)}
                />
              </CardBody>
            </Card>
          </HasPerm>
        </div>
      </div>
    );
  }
}

export default withPermissions(PartnerMemberDetail, 'partner.view_partnermember');
