import React from 'react';
import PropTypes from "prop-types";
import { withSnackbar } from 'notistack';
import {
  DataSource,
  Card,
  CardHeader,
  CardBody,
} from '../../components';
import { EnhancedTable } from '../../datatable';
import {
  getBankChoices,
  getBankBranchChoices,
  bank_account_list_schema,
  bank_account_form_schema,
  handleAddObject,
  handleEditObject,
  handleDeleteObject,
} from '../utils';
import { config, common } from '../../utils';

class BankAccountList extends React.Component {
  bank_account_form = bank_account_form_schema;

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      bank_list: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    getBankChoices().then(bank_list => this.setState({bank_list}));
  };

  resetBranchList = (name, data) => {
    if (name === 'bank') {
      return getBankBranchChoices(data[name]).then(object_list => {
        common.cascade_reflect(object_list, this.bank_account_form, 'bank');
      });
    }
  };

  handleBeforeShowEditDialog = (data) => {
    this.resetBranchList('bank', data);
  };

  render() {
    const { content_type, object_id, title } = this.props;
    const { reload, bank_list } = this.state;
    common.getFromJsonList(this.bank_account_form, 'name', 'bank')["choices"] = bank_list;

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {title}
          </CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={common.formatStr(config.api.master.bank_account_by_ct, content_type, object_id)}
              render={(initData) => (
                <EnhancedTable
                  tableHead={bank_account_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  selectable="single"
                  allowCsv={true}
                  addProps={{
                    title: '銀行口座追加',
                    schema: this.bank_account_form,
                    handleOk: handleAddObject(
                      () => this.setState({reload: !reload}),
                      config.api.master.bank_account_add,
                    ),
                    onChanges: [this.resetBranchList],
                    data: {
                      'content_type': content_type,
                      'object_id': object_id,
                    },
                    visible: common.hasPerm('master.add_bankaccount'),
                  }}
                  editProps={{
                    title: '銀行口座変更',
                    schema: this.bank_account_form,
                    handleBeforeShowup: this.handleBeforeShowEditDialog,
                    handleOk: handleEditObject(
                      () => this.setState({reload: !reload}),
                      config.api.master.bank_account_detail,
                    ),
                    onChanges: [this.resetBranchList],
                    visible: common.hasPerm('master.change_bankaccount'),
                  }}
                  deleteProps={{
                    handleDelete: handleDeleteObject(
                      () => this.setState({reload: !reload}),
                      config.api.master.bank_account_detail,
                    ),
                    visible: common.hasPerm('master.delete_bankaccount'),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

BankAccountList.propTypes = {
  title: PropTypes.string.isRequired,
  content_type: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  object_id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
};

export default withSnackbar(BankAccountList);