import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { getPartnerCompany } from '../utils/api';
import Card from '../../components/Card/Card';
import CardHeader from '../../components/Card/CardHeader';
import CardBody from '../../components/Card/CardBody';
import DataSource from '../../components/DataSource';
import { EnhancedTable } from '../../datatable';
import { common, config } from '../../utils';
import { company_member_list_schema } from './schema';
import { withPermissions } from '../../components';

class PartnerMemberList extends React.Component {
  title = '社員一覧';

  constructor(props) {
    super(props);

    this.state = {
      company: {},
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { params } = props.match;
    getPartnerCompany(params.company_id).then(company => {
      this.setState({company});
    });
  };

  componentDidUpdate = () => {
    document.title = `${this.title} | ${this.state.company.name || ''} | 協力会社`;
  };

  render() {
    const { company_id } = this.props.match.params;
    const { company } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/partner/companies?is_retired=false', name: '協力会社一覧' },
            { link: `/partner/companies/${company_id}/`, name: company.name },
            { name: this.title },
          ]}
        />
        <Card>
          <CardHeader color="info">
            社員一覧
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={common.formatStr(config.api.partner.company_members, company_id)}
              render={(initData) => (
                <EnhancedTable
                  tableHead={company_member_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': 'ＢＰ社員を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect(`/partner/companies/${company_id}/members/add`)),
                    },
                  ]}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(PartnerMemberList, 'partner.view_partnermember');
