import React from 'react';
import { Paper } from '@material-ui/core';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import { DataSource } from '../../components';
import { EnhancedTable } from '../../datatable';
import { config, common } from '../../utils';
import {
  commutation_list_schema,
  commutation_add_schema,
  commutation_edit_schema,
} from './schema';
import {
  handleAddObject,
  getMemberChoices,
  handleEditObject,
  handleDeleteObject,
} from '../utils/api';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class CommutationList extends React.Component {
  commutation_add_schema = common.clone(commutation_add_schema);

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      year: props.match.params.year || common.getCurrentYear(),
      month: props.match.params.month || common.getCurrentMonth(),
      members: [],
    };
    this.initialize();
　}

  initialize = () => {
    getMemberChoices().then(members => this.setState({ members }));
  };

  componentDidMount = () => {
    const { year, month } = this.state;
    document.title = `${year}年${month}月の交通費一覧 | 契約管理`;
  }

  componentDidUpdate = () => {
    const { year, month } = this.state;
    document.title = `${year}年${month}月の交通費一覧 | 契約管理`;
  }

  prevMonth = () => {
    const { year, month } = this.state;
    const date = common.getPrevMonthDictByYm(year, month)
    common.redirect(`/contract/commutations/year/${date.year}/month/${date.month}/`);
    this.setState(state => ({ ...date, reload: !state.reload }));
  };

  nextMonth = () => {
    const { year, month } = this.state;
    const date = common.getNextMonthDictByYm(year, month)
    common.redirect(`/contract/commutations/year/${date.year}/month/${date.month}/`);
    this.setState(state => ({ ...date, reload: !state.reload }));
  };

  render() {
    const { year, month, reload, members } = this.state;
    common.getFromJsonList(this.commutation_add_schema, 'name', 'id').choices = members;

    return (
      <div>
        <Paper style={paperSpacing}>
          <DataSource
            reload={reload}
            endpoint={ common.formatStr(config.api.contract.commutation_list, year, month) }
            render={ (initData) => (
              <EnhancedTable
                title={`${year}/${month}交通費`}
                showTitle={true}
                storageKey='member_commutation'
                tableHead={commutation_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                allowCsv={true}
                pushpinTop={common.getHeaderHeight()}
                tableStyles={{ minWidth: 800 }}
                selectable='single'
                tableActions={[
                  {
                    "tooltip": "先月",
                    "icon": <NavigateBeforeIcon />,
                    "handleClick": this.prevMonth,
                  },
                  {
                    "tooltip": "次月",
                    "icon": <NavigateNextIcon />,
                    "handleClick": this.nextMonth,
                  },
                ]}
                addProps={{
                  "title": '交通費追加',
                  "schema": this.commutation_add_schema,
                  "handleOk": handleAddObject(
                    () => this.setState({reload: !reload}),
                    (data) => common.formatStr(config.api.contract.commutation_add, data.id),
                  ),
                  "visible": (data) => {
                    if (common.hasPerm('member.add_membercommutation') && data.commutation_id) {
                      if (common.formatDate(new Date(data.start_date), 'YYYYMM') === `${year}${month}`) {
                        return true;
                      }
                    }
                    return false;
                  },
                }}
                editProps={{
                  "schema": commutation_edit_schema,
                  "handleOk": handleEditObject(
                    () => this.setState({reload: !reload}),
                    (data) => common.formatStr(config.api.contract.commutation_detail, data.id, data.commutation_id),
                  ),
                  "visible": (data) => {
                    if (common.hasPerm('member.edit_membercommutation') && data.commutation_id) {
                      if (common.formatDate(new Date(data.start_date), 'YYYYMM') === `${year}${month}`) {
                        return true;
                      }
                    }
                    return false;
                  },
                }}
                deleteProps={{
                  "handleDelete": handleDeleteObject(
                    () => this.setState({reload: !reload}),
                    (data) => common.formatStr(config.api.contract.commutation_detail, data.id, data.commutation_id),
                  ),
                  "visible": (data) => {
                    if (common.hasPerm('member.edit_membercommutation') && data.commutation_id) {
                      if (common.formatDate(new Date(data.start_date), 'YYYYMM') === `${year}${month}`) {
                        return true;
                      }
                    }
                    return false;
                  },
                }}
              />
            )} 
          />
        </Paper>
      </div>
    );
  }
}

export default CommutationList
