import React from 'react';
import {
  Paper,
  FormControl,
  TextField,
  withStyles,
  Button,
  Avatar,
  CssBaseline,
  Typography,
  CircularProgress,
} from '@material-ui/core';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import { styles } from '../../components/Login';
import { Toast } from '../../components';
import { sendPasswordForgetEmail } from '../utils/api';
import { constant } from '../../utils';

class PasswordForget extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      email: '',
      security_code: '',
      message: null,
      loading: false,
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { email, security_code } = this.state;
    if (!email || !security_code) {
      Toast.error(constant.ERROR.REQUIRE_MAIL_AND_SECURITY_CODE);
    } else if (security_code.length !== 4) {
      Toast.error(constant.ERROR.SECURITY_CODE_LENGTH);
    } else {
      this.setState({ loading: true });
      sendPasswordForgetEmail(email, security_code).then(() => (
        Toast.success(constant.SUCCESS.EMAIL_SENT)
      )).catch(errors => (
        this.setState({ message: errors.detail })
      )).finally(() => (
        this.setState({ loading: false })
      ));
    }
  };
  
  render() {
    const { classes } = this.props;
    const { message, loading } = this.state;

    return (
      <React.Fragment>
        <CssBaseline />
        <main className={classes.layout}>
          <Paper className={classes.paper}>
            <Avatar className={classes.avatar}>
              <VpnKeyIcon />
            </Avatar>
            <Typography variant="subtitle1">会社メールアドレスを入力してください。</Typography>
            {message ?
              <p className={classes.alert}>{message}</p>
              : ''
            }
            <form className={classes.form}>
              <FormControl margin="normal" required fullWidth>
                <TextField name="email" label="会社メールアドレス" onChange={this.handleChange} />
              </FormControl>
              <FormControl margin="normal" required fullWidth>
                <TextField
                  name="security_code"
                  label="認証コード"
                  onChange={this.handleChange}
                  type='number'
                  placeholder='任意の４桁数字を入力'
                  helperText="任意の４桁数字を入力"
                  onInput={(e)=>{ 
                    if (e.target.value !== '' && !isNaN(parseInt(e.target.value))) {
                      e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,4)
                    }
                  }}
                />
              </FormControl>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={this.handleSubmit}
                disabled={loading}
              >
                送信
                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
              </Button>
            </form>
          </Paper>
        </main>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(PasswordForget);
