import React from 'react';
import PropTypes from "prop-types";
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import SendIcon from '@material-ui/icons/Send';
import DoneIcon from '@material-ui/icons/Done';
import DataSource from '../../components/DataSource';
import { EnhancedTable, FormDialog, ConfirmDialog } from '../../datatable';
import {
  partner_orders_status_list_schema,
  create_partner_order_form_schema,
  create_partner_order_form_layout,
  partner_orders_status_filter_layout,
} from './schema';
import { config, common, constant, checkEndYearAndMonth } from '../../utils';
import {
  getPartnerOrderEmailData,
  sendContentObjectEmail,
  send_email_form_schema,
  send_email_form_layout,
  createPartnerOrder,
  receivePartnerOrderRequest,
  getSalespersonChoice,
  onDeletePartnerOrder,
} from '../utils';
import { HasPerm, Toast } from '../../components';

class OrderStatusList extends React.Component {
  partner_orders_status_list_schema = partner_orders_status_list_schema;

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      email_schema: send_email_form_schema,
      received_data: null,
      salesperson_choice: [],
    };
    this.initialize();
  }

  initialize = () => {
    getSalespersonChoice().then(salesperson_choice => this.setState({salesperson_choice}));
  };

  showOrderConfirm = (data, orderCreatedCallback) => {
    if (this._showOrderConfirm) {
      this._showOrderConfirm(data);
      this._orderCreatedCallback = orderCreatedCallback;
    }
  };

  createPartnerOrder = (data, handleClose) => {
    return createPartnerOrder(data.id, data).then(json => {
      handleClose();
      Toast.success(constant.SUCCESS.BP_ORDER_FILE_CREATED);
      if (this._orderCreatedCallback) {
        const newSelected = Object.assign(data, json);
        this._orderCreatedCallback(newSelected)
      }
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  handleOrderReload = (data) => {
    this.setState({reload: !this.state.reload});
  };

  showSendEmail = (data, sendEmailCallback) => {
    if (this._showSendEmail) {
      if (data.order_id) {
        getPartnerOrderEmailData(data.order_id).then(mail_data => {
          // 添付ファイルの選択肢を設定する
          const email_schema = common.clone(send_email_form_schema);
          common.getFromJsonList(email_schema, 'name', 'attachments')['choices'] = mail_data.attachment_choices;
          this.setState({email_schema});
          this._showSendEmail(mail_data);
          this._sendEmailCallback = sendEmailCallback;
        });
      } else {
        Toast.error(constant.ERROR.BP_ORDER_FILE_NOT_FOUND);
      }
    }
  };

  sendPartnerOrders = (data, handleClose) => {
    return sendContentObjectEmail(data.content_type_id, data.object_id, data).then(() => {
      handleClose();
      Toast.success(constant.SUCCESS.EMAIL_SENT);
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  showReceiveOrderRequest = (data) => {
    if(this._showReceiveOrderRequest) {
      this.setState({received_data: data});
      this._showReceiveOrderRequest(
        data.is_received
         ? constant.CONFIRM.RECEIVE_ORDER_REQUEST_UNDO
         : constant.CONFIRM.RECEIVE_ORDER_REQUEST
      );
    }
  };

  handleReceiveOrderRequest = () => {
    const { received_data } = this.state;
    if (received_data.order_id) {
      receivePartnerOrderRequest(received_data.order_id, !received_data.is_received).then(() => {
        Toast.success(constant.SUCCESS.SAVED);
        this.handleOrderReload(received_data);
        this._closeReceiveOrderRequest && this._closeReceiveOrderRequest();
      }).catch(errors => (
        Toast.error(errors.detail)
      ));
    } else {
      Toast.error(constant.ERROR.BP_ORDER_FILE_NOT_FOUND);
    }
  };

  handleDeleteOrder = (order_id) => {
    if (order_id) {
      return onDeletePartnerOrder(order_id).then(() => {
        Toast.success(constant.SUCCESS.DELETED);
        this.setState({reload: !this.state.reload});
      }).catch((errors) => {
        Toast.error(errors.detail);
      });
    } else {
      Toast.error(constant.ERROR.BP_ORDER_FILE_NOT_FOUND);
      return Promise.reject();
    }
  };

  render() {
    const { endpoint, filters, showMonth } = this.props;
    const { reload, email_schema, salesperson_choice } = this.state;
    common.getFromJsonList(
      this.partner_orders_status_list_schema, 'name', 'salesperson_id'
    ).choices = salesperson_choice;
    if (showMonth === false) {
      this.partner_orders_status_list_schema = this.partner_orders_status_list_schema.filter(
        item => item.name !== 'ym'
      );
    }
    
    return (
      <div>
        <DataSource
          reload={reload}
          endpoint={endpoint}
          render={(initData) => (
            <EnhancedTable
              storageKey='order.status'
              tableHead={this.partner_orders_status_list_schema}
              tableData={initData}
              rowsPerPage={config.rowsPerPage}
              rowActions={[
                {
                  'tooltip': '注文書／注文請書を作成',
                  'icon': <NoteAddIcon />,
                  'handleClick': this.showOrderConfirm,
                  'visible': common.hasPerm('partner.add_partnerorder'),
                },
                {
                  'tooltip': 'BP注文書を送付する',
                  'icon': <SendIcon />,
                  'handleClick': this.showSendEmail,
                  'visible': common.hasPerm('partner.send_partnerorder'),
                },
                {
                  'tooltip': '注文請書を回収',
                  'icon': <DoneIcon />,
                  'handleClick': this.showReceiveOrderRequest,
                  'visible': common.hasPerm('partner.change_partnerorder'),
                },
              ]}
              selectable="single"
              filters={filters}
              pushpinTop={common.getHeaderHeight()}
              tableStyles={{ minWidth: 1300 }}
              filterLayout={partner_orders_status_filter_layout}
              deleteProps={{
                handleDelete: (data) => this.handleDeleteOrder(data.order_id),
              }}
            />
          )}
        />
        <HasPerm permission='partner.add_partnerorder'>
          <FormDialog
            innerRef={(dlg) => { this._showOrderConfirm = dlg && dlg.handleOpen }}
            title={'注文書・注文請書を作成'}
            schema={create_partner_order_form_schema}
            layout={create_partner_order_form_layout}
            checkList={[checkEndYearAndMonth]}
            handleOk={this.createPartnerOrder}
            saveCallback={this.handleOrderReload}
          />
        </HasPerm>
        <HasPerm permission='partner.send_partnerorder'>
          <FormDialog
            innerRef={(dlg) => { this._showSendEmail = dlg && dlg.handleOpen }}
            title='注文書・注文請書を送付'
            schema={email_schema}
            layout={send_email_form_layout}
            handleOk={this.sendPartnerOrders}
            saveCallback={this.handleOrderReload}
          />
        </HasPerm>
        <ConfirmDialog
          title={'注文請書の回収'}
          innerRef={(dlg) => {
            this._showReceiveOrderRequest = dlg && dlg.handleOpen;
            this._closeReceiveOrderRequest = dlg && dlg.handleCancel;
          }}
          onOk={this.handleReceiveOrderRequest}
        />
      </div>
    )
  }
}

OrderStatusList.propTypes = {
  endpoint: PropTypes.string.isRequired,
  filters: PropTypes.object,
  showMonth: PropTypes.bool,
};

OrderStatusList.defaultProps = {
  showMonth: true,
}

export default OrderStatusList;
