import React from 'react';
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography, Paper, Breadcrumbs,
} from '@material-ui/core';
import { common } from '../utils/common';

const styles = theme => ({
  root: {
    justifyContent: 'center',
    flexWrap: 'wrap',
  },
  paper: {
    padding: `${theme.spacing(1)}px ${theme.spacing(1) * 2}px`,
  },
  breadcrumbs: {
    fontSize: '13px',
    '& p': {
      fontSize: '13px',
    },
  },
});

function TopBreadcrumbs(props) {
  const { classes, breadcrumbs } = props;

  if (common.isEmpty(breadcrumbs)) {
    return null;
  } else {
    return (
      <div className={classes.root}>
        <Paper className={classes.paper}>
          <Breadcrumbs separator="›" arial-label="Breadcrumb" className={classes.breadcrumbs}>
            { breadcrumbs.map((bread, key) => {
              return (
                bread.link ? (
                  <Link key={key} to={bread.link}>{bread.name}</Link>
                ) : (
                  <Typography key={key} color="textPrimary">{bread.name}</Typography>
                )
              )
            }) }
          </Breadcrumbs>
        </Paper>
      </div>
    );
  }
}

TopBreadcrumbs.propTypes = {
  classes: PropTypes.object.isRequired,
  breadcrumbs: PropTypes.arrayOf(PropTypes.shape({link: PropTypes.string, name: PropTypes.string})),
};

export default withStyles(styles)(TopBreadcrumbs);