import React from 'react';
import { withSnackbar } from 'notistack';
import clsx from 'clsx';
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  Avatar,
  withStyles,
  Typography,
  CardActions,
  Tooltip,
  IconButton,
  Collapse,
  Paper,
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import {
  TableDetail, FormDialog, EnhancedTable,
} from '../../datatable';
import {
  withPermissions,
  TopBreadcrumbs,
  HasPerm,
  Timeline,
  DataSource,
} from '../../components';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  getOrganizationChoices,
  getOrganization,
  handleEditObject,
  handleDeleteObject,
  handleAddObject,
  getMemberChoices,
  getOrganizationPositionTimelines,
} from '../utils/api';
import {
  organization_detail_schema,
  organization_form_schema,
  positionship_form_schema,
  turnover_client_schema_by_month,
} from './schema';
import { common, config } from '../../utils';
import { blue } from '@material-ui/core/colors';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

const styles = theme => ({
  avatar: {
    backgroundColor: blue[500],
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  card: {
    ...paperSpacing,
  },
});

class OrganizationDetail extends React.Component {
  organization_form_schema = common.clone(organization_form_schema);
  positionship_form_schema = common.clone(positionship_form_schema);

  constructor(props) {
    super(props);

    this.state = { 
      organization: {},
      organizations: [],
      members: [],
      chief: {},
      vice_chief: {},
      position_timelines: [],
      org_expanded: false,
      year_turnover_clients: common.getPrevMonthDict().year,
      month_turnover_clients: common.getPrevMonthDict().month,
      turnover_clients_reload: false,
    };
    this.initialize(props);
    this.detail_api = common.formatStr(
      config.api.organization.detail,
      props.match.params.org_id,
    );
　}

  initialize = (props) => {
    const { org_id } = props.match.params;
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    getMemberChoices().then(members => this.setState({members}));
    this.initializeOrganization(org_id);
  };

  initializeOrganization = (org_id) => {
    getOrganization(org_id).then(organization => {
      this.setState({
        organization,
        chief: organization.chief,
        vice_chief: organization.vice_chief,
      });
    });
    getOrganizationPositionTimelines(org_id).then(position_timelines => {
      this.setState({ position_timelines });
    });
  };

  componentDidUpdate = () => {
    const { organization } = this.state;
    document.title = `部署：${organization.name}`;
  };

  handleOrgExpandClick = () => {
    this.setState((state) => {
      return { org_expanded: !state.org_expanded };
    })
  };

  prevMonthTurnoverClients = () => {
    const { year_turnover_clients, month_turnover_clients } = this.state;
    const date = common.getPrevMonthDictByYm(year_turnover_clients, month_turnover_clients)
    this.setState(state => ({
      year_turnover_clients: date.year,
      month_turnover_clients: date.month,
      turnover_clients_reload: !state.turnover_clients_reload,
    }));
  };

  nextMonthTurnoverClients = () => {
    const { year_turnover_clients, month_turnover_clients } = this.state;
    const date = common.getNextMonthDictByYm(year_turnover_clients, month_turnover_clients)
    this.setState(state => ({
      year_turnover_clients: date.year,
      month_turnover_clients: date.month,
      turnover_clients_reload: !state.turnover_clients_reload,
    }));
  };

  render() {
    const { org_id } = this.props.match.params;
    const { classes } = this.props;
    const {
      organization, organizations, members, chief, vice_chief, position_timelines, org_expanded,
      year_turnover_clients, month_turnover_clients, turnover_clients_reload,
    } = this.state;
    common.getFromJsonList(this.organization_form_schema, 'name', 'parent')['choices'] = organizations;
    common.getFromJsonList(this.positionship_form_schema, 'name', 'member')['choices'] = members;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/organization', name: '部署一覧' },
            { name: organization.name },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer spacing={2}>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                data={organization}
                schema={organization_detail_schema}
                editProps={{
                  title: '部署変更',
                  schema: this.organization_form_schema,
                  handleEdit: handleEditObject(
                    (organization) => this.setState({organization}),
                    this.detail_api
                  ),
                  visible: common.hasPerm('org.change_organization'),
                }}
                deleteProps={{
                  handleDelete: handleDeleteObject(
                    () => common.redirect('/organization'),
                    this.detail_api,
                  ),
                  visible: common.hasPerm('org.delete_organization'),
                }}
                actions={[
                  <HasPerm key={1} permission='member.view_member'>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => common.redirect(`/organization/${org_id}/members/`, 'is_belong=true')}
                    >
                      メンバー一覧
                    </Button>
                  </HasPerm>,
                  <HasPerm key={2} permission='org.view_positionship'>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => common.redirect(`/organization/${org_id}/positions/`)}
                    >
                      職位一覧
                    </Button>
                  </HasPerm>,
                  <HasPerm key={3} permission='attendance.view_attendance'>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => common.redirect(`/organization/${org_id}/attendances/${common.getPrevMonthDict().year}/${common.getPrevMonthDict().month}`)}
                    >
                      勤怠情報
                    </Button>
                  </HasPerm>,
                ]}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={3} lg={3}>
              <Card style={{marginTop: 16}}>
                <CardHeader
                  avatar={
                    <Avatar className={classes.avatar}>
                      {organization.name ? organization.name.substring(0, 2) : ''}
                    </Avatar>
                  }
                  title={organization.name}
                />
                <CardContent>
                  <Typography gutterBottom>
                    {organization && !common.isEmpty(chief) ? (
                      `${chief.position_name}: ${chief.full_name}`
                    ) : '部門長が任命されていません。'}
                  </Typography>
                  <Typography gutterBottom>
                    {organization && !common.isEmpty(vice_chief) ? (
                      `${vice_chief.position_name}: ${vice_chief.full_name}`
                    ) : null}
                  </Typography>
                </CardContent>
                <CardActions disableSpacing>
                  <HasPerm permission='org.add_positionship'>
                    <Tooltip title="職位追加">
                      <IconButton
                        color="secondary"
                        aria-label="add"
                        onClick={() => {
                          if (this._showAddPositionShip) {
                            this._showAddPositionShip({ organization: org_id, end_date: '9999-12-31' });
                          }
                        }}
                        to='/contract/members/add'
                      >
                        <AddIcon />
                      </IconButton>
                    </Tooltip>
                  </HasPerm>
                  { !common.isEmpty(position_timelines) ? (
                    <IconButton
                      className={clsx(classes.expand, {
                        [classes.expandOpen]: org_expanded,
                      })}
                      onClick={this.handleOrgExpandClick}
                      aria-expanded={org_expanded}
                      aria-label="show more"
                    >
                      <ExpandMoreIcon />
                    </IconButton>
                  ) : null }
                </CardActions>
                { !common.isEmpty(position_timelines) ? (
                  <Collapse in={org_expanded} timeout='auto' unmountOnExit>
                    <CardContent>
                      <Timeline items={position_timelines} showTime={false} />
                    </CardContent>
                  </Collapse>
                ) : null }
              </Card>
            </GridItem>
          </GridContainer>
          <Paper className={classes.card}>
            <DataSource
              reload={turnover_clients_reload}
              endpoint={common.formatStr(
                config.api.organization.turnover_for_clients_by_month,
                org_id, year_turnover_clients, month_turnover_clients
              )}
              render={ (initData) => (
                <EnhancedTable
                  title={`${year_turnover_clients}年${month_turnover_clients}月取引先別売上一覧`}
                  showTitle={true}
                  tableHead={turnover_client_schema_by_month}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  allowCsv={true}
                  pushpinTop={common.getHeaderHeight()}
                  showAggregate={true}
                  tableActions={[
                    {
                      "tooltip": "先月",
                      "icon": <NavigateBeforeIcon />,
                      "handleClick": this.prevMonthTurnoverClients,
                    },
                    {
                      "tooltip": "次月",
                      "icon": <NavigateNextIcon />,
                      "handleClick": this.nextMonthTurnoverClients,
                    },
                  ]}
                />
              )}
            />
          </Paper>
        </div>
        <FormDialog
          title='職位追加'
          schema={this.positionship_form_schema}
          handleOk={handleAddObject(
            () => this.initializeOrganization(org_id),
            config.api.organization.position_add,
            org_id
          )}
          innerRef={(dlg) => { this._showAddPositionShip = dlg && dlg.handleOpen }}
        />
      </div>
    );
  }
}

export default withPermissions(
  withSnackbar(withStyles(styles)(OrganizationDetail)), 'org.view_organization'
);
