import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { StepForm, withPermissions } from '../../components';
import {
  proejct_member_form_schema,
  cost_percentage_form_schema,
} from './schema';
import {
  member_contract_form_schema,
  default_contract_data,
  contract_calculate_hours_schema,
  contract_allowance_schema,
  bp_contract_item_schema,
  bp_contract_item_layout,
} from '../partner/schema';
import {
  getProjectDetail,
  getMemberCategory,
  getCascadeObjects,
  getContentObjects,
  getDuplicatedProjectMemberSync
} from '../utils/api';
import { common } from '../../utils/common';
import { config } from '../../utils/config';
import { onChangeCalculateType, onChangeMinusAndPlus } from '../utils/common';

class ProjectMemberAdd extends React.Component {
  title = 'アサイン';
  project_member_schema = JSON.parse(JSON.stringify(proejct_member_form_schema));
  initial_member_list = [];

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      member_categories: [],
      initial_member_list: [],
      duplications: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id } = props.match.params;
    getProjectDetail(project_id).then(project => {
      this.setState({project});
    })
    getMemberCategory().then(member_categories => {
      this.setState({member_categories});
    });
    if (props.location.state && props.location.state.src_object) {
      getContentObjects(props.location.state.src_object.member_content_type).then(members => {
        this.setState({initial_member_list: members});
      });
    }
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name} | 案件情報`;
  };

  getSteps = () => {
    const { project_id } = this.props.match.params;
    const { project, duplications } = this.state;

    return [
      {
        name: '案件メンバー情報',
        formProps: {
          name: 'project_member',
          schema: this.getProjectMemberFormSchema(),
          data: {
            "project": project_id,
            "project_name": project.name,
          },
          onChanges:[
            (
              (name, data) => getCascadeObjects(
                name,
                'member_content_type',
                data[name],
                this.project_member_schema
              )
            ),
          ],
        },
      },
      {
        name: "精算条件",
        formProps: {
          name: 'contract_info',
          schema: member_contract_form_schema,
          data: default_contract_data,
          onChanges: [onChangeCalculateType, onChangeMinusAndPlus],
          inlines: [
            {
              name: "calculate_hours",
              title: "契約計算用時間",
              schema: contract_calculate_hours_schema,
              allowAdd: false,
              allowDelete: false,
            },
            {
              name: 'allowances',
              title: '精算条件',
              schema: contract_allowance_schema,
              allowAdd: false,
              allowDelete: false,
            },
            {
              name: "contract_items",
              title: "契約条件",
              schema: bp_contract_item_schema,
              layout: bp_contract_item_layout,
              allowAdd: false,
              allowDelete: false,
            },
          ],
          checkList: [this.checkProjectMemberDuplicated],
        },
      },
      {
        name: 'コスト分担',
        description: 'メンバーがこの案件のアサイン期間内で、既に別の案件にもアサインした場合、このステップで各案件のコスト分担割合を指定する。',
        formProps: {
          name: 'cost_percentage',
          inline: true,
          schema: cost_percentage_form_schema,
          data: duplications,
          allowAdd: false,
          allowDelete: false,
        }
      }
    ];
  };

  /**
   * 重複アサインした場合、コスト分担を行う。
   */
  checkProjectMemberDuplicated = (currData, stepsData) => {
    const { project_id } = this.props.match.params;
    const { project_member } = stepsData;
    const { project } = this.state;
    const duplications = getDuplicatedProjectMemberSync(
      project_id,
      project_member.member_content_type,
      project_member.member_object_id,
      currData.start_date,
      currData.end_date
    );
    if (duplications === false) {
      return false;
    } else {
      const arr_tmp_ym = [];
      const arr_ym = [];
      for (let item of duplications) {
        if (arr_tmp_ym.indexOf(item.ym) < 0) {
          arr_tmp_ym.push(item.ym);
          arr_ym.push({ 'year': item.year, 'month': item.month, 'ym': item.ym });
        }
      }
      for (let ym of arr_ym) {
        duplications.push({
          'project': project_id,
          'project_name': project.name,
          'project_member': null,
          'member_content_type': project_member.member_content_type,
          'member_object_id': project_member.member_object_id,
          'full_name': duplications[0].full_name,
          'start_date': duplications[0].start_date,
          'end_date': duplications[0].end_date,
          'year': ym.year,
          'month': ym.month,
          'ym': ym.ym,
          'organization': project.organization,
          'organization_name': duplications[0].organization_name,
          'cost_percentage_id': null,
          'percentage': null,
        });
      }
      duplications.sort(function(a, b) {
        let keyA = a.ym + a.project_name,
            keyB = b.ym + b.project_name;
        if (keyA < keyB) return -1;
        if (keyA > keyB) return 1;
        return 0;
      });
      this.setState({ duplications });
      return true;
    }
  };

  getProjectMemberFormSchema = () => {
    const { member_categories, initial_member_list } = this.state;
    common.getFromJsonList(
      this.project_member_schema, 'name', 'member_content_type'
    ).choices = member_categories;
    common.getFromJsonList(
      this.project_member_schema, 'name', 'member_object_id'
    ).choices = initial_member_list;
    return this.project_member_schema;
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project } = this.state;
    const steps = this.getSteps();

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: `/project/projects`, name: '案件一覧' },
            { link: `/project/projects/${project_id}/`, name: project.name },
            { link: `/project/projects/${project_id}/members`, name: 'メンバー一覧' },
            { name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <StepForm
                title={this.title}
                steps={steps}
                methodName='POST'
                saveEndpoint={common.formatStr(config.api.project.member_add, project_id)}
                saveCallback={(data) => common.redirect(`/project/projects/${project_id}/members/${data.id}`)}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(ProjectMemberAdd, 'project.add_projectmember');
