import React from 'react';
import PropTypes from 'prop-types';
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Typography,
} from '@material-ui/core';

const styles = (theme) => ({
  cardHeader: {
    backgroundColor: theme.palette.grey[200],
  },
  cardPricing: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'baseline',
    marginBottom: theme.spacing(2),
  },
});

class SimpleCard extends React.Component {

  render() {
    const { classes, title, subheader, data, secondaryList, actions } = this.props;

    return (
      <Card>
        <CardHeader
          title={title}
          subheader={subheader}
          titleTypographyProps={{ align: 'center' }}
          className={classes.cardHeader}
        />
        <CardContent>
          <div className={classes.cardPricing}>
            <Typography component="h2" variant="h3" color="textPrimary">
              {data.count}
            </Typography>
            <Typography variant="h6" color="textSecondary">
              {data.message}
            </Typography>
          </div>
          <ul>
            {secondaryList.map((item, key) => (
              <Typography key={key} component="li" variant="subtitle1" align="center">
                {item.count}{item.message}
              </Typography>
            ))}
          </ul>
        </CardContent>
        <CardActions disableSpacing>
          {actions}
        </CardActions>
      </Card>
    );
  }
}

SimpleCard.propTypes = {
  classes: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  subheader: PropTypes.string,
  data: PropTypes.shape({
    count: PropTypes.number.isRequired,
    message: PropTypes.string.isRequired,
  }).isRequired,
  secondaryList: PropTypes.array,
  actions: PropTypes.object,
};

SimpleCard.defaultProps = {
  secondaryList: [],
}

export default withStyles(styles)(SimpleCard);
