import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardBody from "../../components/Card/CardBody";
import DataSource from '../../components/DataSource';
import {
  list_member_schema,
  member_list_search_layout,
} from './schema';
import { config } from '../../utils/config';
import { withPermissions } from '../../components';
import { getSalespersonChoice } from '../utils/api';
import { common } from '../../utils';

class MemberList extends React.Component {
  title = 'メンバー一覧';
  my_list_member_schema = common.clone(list_member_schema);

  constructor(props) {
    super(props);

    this.state = {
      salesperson_choice: [],
      loaded: false,
    };
    
    this.initialize();
  }

  initialize = () => {
    getSalespersonChoice().then(salesperson_choice => {
      this.setState({salesperson_choice, loaded: true});
    });
  };

  componentDidMount = () => {
    document.title = this.title;
  };

  render () {
    const { salesperson_choice, loaded } = this.state;
    // 営業員選択肢の設定
    common.getFromJsonList(this.my_list_member_schema, 'name', 'salesperson_id')['choices'] = salesperson_choice;

    if (loaded) {
      return (
        <div>
          <Card>
            <CardHeader color="info">
              {this.title}
            </CardHeader>
            <CardBody>
              <DataSource
                endpoint={ config.api.member.portal_list }
                render={ (initData) => (
                  <EnhancedTable
                    title='作業メンバー一覧'
                    storageKey='member.members'
                    tableHead={this.my_list_member_schema}
                    tableData={initData.results}
                    rowsPerPage={config.rowsPerPage}
                    pushpinTop={common.getHeaderHeight()}
                    allowCsv={true}
                    urlReflect={false}
                    filterLayout={member_list_search_layout}
                    tableStyles={{ minWidth: 1050 }}
                  />
                ) }
              />
            </CardBody>
          </Card>
        </div>
      );
    } else {
      return null
    }
  }
}

export default withPermissions(MemberList, 'member.view_member');
