import React from 'react';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import { company_form_schema, company_form_layout } from '../partner/schema';
import ActionForm from '../../components/ActionForm';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { withPermissions } from '../../components';

class ClientCompanyForm extends React.Component {
  title = '取引先企業追加';

  componentDidMount = () => {
    document.title = `${this.title}`;
  };

  render() {
    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/client/companies', name: '取引先企業一覧' },
            { name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <ActionForm
                title={this.title}
                schema={company_form_schema}
                layout={company_form_layout}
                methodName={'POST'}
                saveEndpoint={config.api.client.add}
                saveCallback={() => common.redirect('/client/companies')}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(ClientCompanyForm, 'client.add_clientcompany')
