import React from 'react';
import { TableDetail } from '../../datatable';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import {
  getProjectDetail,
  getBankAccountList,
  getProjectOrderDetail,
  onDeleteObject,
  getProjectMemberChoices,
} from '../utils/api';
import { project_order_form_schema, project_order_form_layout } from './schema';
import { common, config, constant, ajax } from '../../utils';
import { withPermissions, Toast } from '../../components';

class ProjectOrderDetail extends React.Component {
  title = '注文書詳細';
  project_order_form = common.clone(project_order_form_schema);

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      order: {},
      bank_account_list: [],
      project_members_chioce: [],
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id, order_id } = props.match.params;
    this.detail_api = common.formatStr(
      config.api.project.project_order_detail,
      project_id, order_id
    );
    getProjectDetail(project_id).then(project => {
      this.setState({ project });
      return project;
    }).then(project => {
      getBankAccountList(project.my_company_content_type, project.my_company_id).then(bank_account_list => {
        this.setState({ bank_account_list });
      });
    });
    getProjectOrderDetail(project_id, order_id).then(order => this.setState({ order }));
    getProjectMemberChoices(project_id).then(project_members_chioce => {
      this.setState({ project_members_chioce });
    });
  };

  componentDidUpdate = () => {
    const { project } = this.state;
    document.title = `${this.title} | ${project.name || ''} | 案件一覧`;
  };

  onEditProjectOrder = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(order => {
      this.setState({ order });
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  render() {
    const { project_id } = this.props.match.params;
    const { project, bank_account_list, order, project_members_chioce } = this.state;
    common.getFromJsonList(this.project_order_form, 'name', 'bank_account')['choices'] = bank_account_list;
    common.getFromJsonList(this.project_order_form, 'name', 'members')['choices'] = project_members_chioce;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: `/project/projects`, name: '案件一覧' },
            { link: `/project/projects/${project_id}/`, name: project.name },
            { name: this.title },
          ]}
        />
        <div style={{ marginTop: 16 }}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9} lg={9}>
              <TableDetail
                title={`註文書：${order.name}` || ''}
                data={Object.assign(order, { 'company_name': project.company_name })}
                schema={this.project_order_form}
                editProps={{
                  title: '註文書情報変更',
                  schema: this.project_order_form,
                  layout: project_order_form_layout,
                  handleEdit: this.onEditProjectOrder,
                  visible: common.hasPerm('project.change_projectorder'),
                }}
                deleteProps={{
                  handleDelete: () => (onDeleteObject(
                    this.detail_api,
                    `/project/projects/${project_id}`,
                  )),
                  visible: common.hasPerm('project.delete_projectorder'),
                }}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
};

export default withPermissions(ProjectOrderDetail, 'project.view_projectorder');
