import React from 'react';
import { EnhancedTable } from '../../datatable/index';
import DataSource from '../../components/DataSource';
import { expense_month_list_schema } from './schema';
import { common } from '../../utils';
import { config } from '../../utils/config';
import { withPermissions } from '../../components';
import { Paper } from '@material-ui/core';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ExpenseMonths extends React.Component {
  title = '立替金一覧';

  componentDidMount = () => {
    document.title = this.title;
  };

  render () {

    return (
      <div>
        <Paper style={paperSpacing}>
          <DataSource
            endpoint={ config.api.attendance.expense_months }
            render={ (initData) => (
              <EnhancedTable
                title={this.title}
                showTitle={true}
                tableHead={expense_month_list_schema}
                tableData={initData}
                rowsPerPage={config.rowsPerPage}
                pushpinTop={common.getHeaderHeight()}
                allowCsv={true}
                urlReflect={false}
                tableStyles={{ minWidth: 250 }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default withPermissions(ExpenseMonths, 'attendance.view_expense');
