import { common } from "../../utils";
import {
  getAddressByPostcodeSync,
} from "./api";

export function calcMinusOrPlusPerHour(amount, hours) {
  if (hours === 0) {
    return 0;
  } else {
    return parseInt(amount / hours);
  }
};

export function onChangeMinHours(name, data) {
  if (name === 'min_hours') {
    return {minus_per_hour: calcMinusOrPlusPerHour(data['price'], data['min_hours'])};
  } else {
    return null;
  }
};

export function onChangeMaxHours(name, data) {
  if (name === 'max_hours') {
    return {plus_per_hour: calcMinusOrPlusPerHour(data['price'], data['max_hours'])};
  } else {
    return null;
  }
};

export function onChangePrice(name, data) {
  if (name === 'price') {
    return {
      plus_per_hour: calcMinusOrPlusPerHour(data['price'], data['min_hours']),
      minus_per_hour: calcMinusOrPlusPerHour(data['price'], data['max_hours'])
    };
  } else {
    return null;
  }
};

export function onChangeCalculateType(name, data) {
  if (name === 'member_calculate_type') {
    const value = data[name];
    const item_min_hours = common.getFromJsonList(data.calculate_hours, 'code', '0000');
    switch (value) {
      case "01":  // 固定１６０時間
        item_min_hours['hours'] = 160;
        break;
      case "02":  // 営業日数 × ８
        item_min_hours['hours'] = 160;
        break;
      case "03":  // 営業日数 × ７.９
        item_min_hours['hours'] = 160;
        break;
      case "04":  // 営業日数 × ７.７５
        item_min_hours['hours'] = 160;
        break;
      case "10":
      case "11":
        item_min_hours['hours'] = 0;
        break;
      default:
        break;
    }
  }
};

export function onChangeAutoUpdatePeriod(name, data) {
};

/**
 * 契約基本情報変更時（正社員または契約社員登録・変更時）
 * @param {String} name 変更する項目ＩＤ
 * @param {*} data フォームのデータ
 */
export function onChangeContractBasicInfo(name, data) {
  if (name === 'contract_type') {
    if (data[name] === '0001') {
      // 正社員
      data['end_date'] = '9999-12-31';
      data['is_auto_update'] = false;
      data['auto_update_period'] = null;
    } else if (data[name] === '0010' && data['start_date']) {
      // 契約社員
      const end_date = common.getOneYearEndDate(data['start_date']);
      data['end_date'] = common.formatDate(end_date, 'YYYY-MM-DD');
      data['is_auto_update'] = true;
    } else {
      data['end_date'] = null;
      data['is_auto_update'] = false;
      data['auto_update_period'] = null;
    }
  } else if (name === 'start_date') {
    if (data['contract_type'] === '0001') {
      data['end_date'] = '9999-12-31';
    } else if (data['contract_type'] === '0010') {
      const end_date = common.getOneYearEndDate(data['start_date']);
      data['end_date'] = common.formatDate(end_date, 'YYYY-MM-DD');
    }
  }
  // 契約自動更新時の更新期間を自動計算する（契約開始日と終了日の期間とする）
  if (['start_date', 'end_date', 'contract_type'].indexOf(name) >= 0 && data['contract_type'] !== '0001') {
    // 契約開始日または契約終了日が変更され、且つ契約形態が正社員でない場合
    const start_date = data['start_date'];
    const end_date = data['end_date'];
    const cnt = common.getMonthDiff(start_date, end_date);
    data['auto_update_period'] = cnt ? cnt + 1 : null;
  }
};

export function onBlurPostcode(name, data) {
  if (name === 'post_code') {
    const json = getAddressByPostcodeSync(data[name]);
    if (json) {
      data['address1'] = json.address
    }
  }
  return data;
};

export function onChangeProjectBlanketContract(name, data, prefix, inlineIndex) {
  if (prefix === 'allowances') {
    const value = data[prefix][inlineIndex][name];
    if (name === 'unit' && ['0001', '0002'].indexOf(data[prefix][inlineIndex].code) >= 0) {
      common.getFromJsonList(data[prefix], 'code', '0001').unit = value;
      common.getFromJsonList(data[prefix], 'code', '0002').unit = value;
    } else if (name === 'amount' && data[prefix][inlineIndex].code === '0001') {
      // 基本金額が変更した場合、税金も変更する
      common.getFromJsonList(data[prefix], 'code', '0002').amount = Math.round(value * 0.1);
    }
  }
  return data;
};

export function onChangeProjectBlanketContractInStep(name, data, steps, prefix, inlineIndex) {
  if (prefix === 'allowances') {
    const value = data[prefix][inlineIndex][name];
    if (name === 'unit' && ['0001', '0002'].indexOf(data[prefix][inlineIndex].code) >= 0) {
      common.getFromJsonList(data[prefix], 'code', '0001').unit = value;
      common.getFromJsonList(data[prefix], 'code', '0002').unit = value;
    } else if (name === 'amount' && data[prefix][inlineIndex].code === '0001') {
      // 基本金額が変更した場合、税金も変更する
      common.getFromJsonList(data[prefix], 'code', '0002').amount = Math.round(value * 0.1);
    }
  }
  return data;
};

export function onChangeMinusAndPlus(name, data, steps, prefix, inlineIndex) {
  if (name === 'amount' && prefix === 'allowances') {
    const value = data[prefix][inlineIndex][name];
    const member_calculate_type = data['member_calculate_type'];
    const min_hours = getMinHours(data['calculate_hours']);
    const item_minus_per_hours = common.getFromJsonList(data[prefix], 'code', '1007');
    const max_hours = getMaxHours(data['calculate_hours']);
    const item_plus_per_hours = common.getFromJsonList(data[prefix], 'code', '1008');
    if (data[prefix][inlineIndex].code === '0001' && ['10', '11'].indexOf(member_calculate_type) < 0) {
      if (min_hours && item_minus_per_hours) {
        item_minus_per_hours['amount'] = calcMinusOrPlusPerHour(value, min_hours);
      }
      if (max_hours && item_plus_per_hours) {
        item_plus_per_hours['amount'] = calcMinusOrPlusPerHour(value, max_hours);
      }
    }
  }
  return data;
};

function getMinHours(dataList) {
  const item_min_hours = common.getFromJsonList(dataList, 'code', '0000');
  const item_calc_min_hours = common.getFromJsonList(dataList, 'code', '0010');
  if (item_calc_min_hours && item_calc_min_hours.hours > 0) {
    return item_calc_min_hours.hours;
  } else {
    return item_min_hours.hours;
  }
}

function getMaxHours(dataList) {
  const item_max_hours = common.getFromJsonList(dataList, 'code', '0001');
  const item_calc_max_hours = common.getFromJsonList(dataList, 'code', '0011');
  if (item_calc_max_hours && item_calc_max_hours.hours > 0) {
    return item_calc_max_hours.hours;
  } else {
    return item_max_hours.hours;
  }
}

/**
 * 契約手当に金額が記入した場合のコメントを表示する
 * @param {String} name 項目名称
 * @param {Object} data フォームのデータ（InlineFormの場合はArray）
 * @param {Array} steps ステップフォームデータのArray
 * @param {String} prefix InlineFormの場合はnull
 * @param {Integer} inlineIndex フォームデータArrayのインデックス
 * @param {Object} default_data フォームの既定値
 */
export function onChangeContractAllowance(name, data, steps, prefix, inlineIndex, default_data) {
  if (name === 'amount') {
    const value = data[inlineIndex][name];
    const code = data[inlineIndex]['code'];
    if (value > 0) {
      const default_value = common.getFromJsonList(default_data, 'code', code);
      data[inlineIndex]['comment'] = default_value.comment;
    } else {
      data[inlineIndex]['comment'] = null;
    }
  }
  return data;
};
