import React from 'react';
import { common } from '../utils/common';
import HttpStatus from './HttpStatus';
import { constant } from '../utils/constants';

export default function withPermissions(WrappedComponent, permission) {
  return class extends React.Component {

    constructor(props) {
      super(props);

      this.state = {
        error: false,
        error_message: constant.ERROR.HTTP_NOT_ALLOWED,
      };
    }

    forbidden = (message) => {
      this.setState({ error: true, error_message: message });
    };

    render() {
      const { error, error_message } = this.state;

      let hasPerm = false;
      if (error === true) {
        hasPerm = false;
      } else if (Array.isArray(permission)) {
        // 複数の権限がある場合、1つでも権限内ならＯＫ
        hasPerm = (permission.filter(perm => common.hasPerm(perm) === true).length > 0);
      } else {
        hasPerm = common.hasPerm(permission);
      }
      if (hasPerm) {
        return <WrappedComponent {...this.props} forbidden={this.forbidden} />;
      } else {
        return (
          <HttpStatus
            statusCode={403}
            message={error_message}
          />
        );
      }
    }
  }
}

export function HasPerm(props) {
  const { permission, children } = props;
  if (Array.isArray(permission)) {
    const hasPerm = (permission.filter(perm => common.hasPerm(perm) === true).length > 0);
    return hasPerm ? (
      <React.Fragment>
        {children}
      </React.Fragment>
    ) : null;
  } else if (common.hasPerm(permission)) {
    return (
      <React.Fragment>
        {children}
      </React.Fragment>
    );
  } else {
    return null;
  }
};
