import React from 'react';
import { Paper } from '@material-ui/core';
import {
  untreated_payment_notices_schema,
} from './schema';
import { EnhancedTable } from '../../datatable';
import { DataSource } from '../../components';
import { config, common } from '../../utils';
import { useStyles } from './style';

function UntreatedPaymentNoticeTab(props) {
  const { member_id } = props.match.params;
  const classes = useStyles();

  return (
    <div className={classes.tabWrapper}>
      <Paper className={classes.tabPaper}>
        <DataSource
          endpoint={ common.formatStr(config.api.member.untreated_payment_notices, member_id) }
          render={ (initData) => (
            <EnhancedTable
              title='未送信の支払通知書・ＢＰ請求書一覧'
              tableHead={untreated_payment_notices_schema}
              tableData={initData.results}
              rowsPerPage={config.rowsPerPage}
              pushpinTop={common.getHeaderHeight()}
              allowCsv={true}
              showTitle={true}
            />
          ) }
        />
      </Paper>
    </div>
  );
};

export default UntreatedPaymentNoticeTab;
