import { common } from "../../utils"

export const company_monthly_turnover_option = {
  chart: {
    type: 'bar',
  },
  plotOptions: {
    bar: {
      horizontal: false,
      columnWidth: '55%',
      endingShape: 'rounded'
    },
  },
  dataLabels: {
    enabled: false
  },
  stroke: {
    show: true,
    width: 2,
    colors: ['transparent']
  },
  xaxis: {
    categories: null,
  },
  yaxis: {
    title: {
      text: '￥(百万円)'
    },
    labels: {
      formatter: (val) => Math.round(val / 1000000)
    }
  },
  fill: {
    opacity: 1
  },
  tooltip: {
    y: {
      formatter: function (val) {
        return `￥${common.toNumComma(val)} 円`
      }
    }
  },
};

