import React from 'react';
import { EnhancedTable } from '../../datatable';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import DataSource from '../../components/DataSource';
import Card from '../../components/Card/Card';
import CardHeader from '../../components/Card/CardHeader';
import CardBody from '../../components/Card/CardBody';
import { getProjectDetail, onSaveProjectAttendance } from '../utils';
import { common, config, constant } from '../../utils';
import {
  project_attendance_by_month_schema,
  project_attendance_edit_schema,
  project_attendance_edit_layout,
} from './schema';
import { withPermissions, Toast } from '../../components';

class ProjectAttendance extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      project: {},
      reload: true,
    };
    this.initialize(props);
  }

  initialize = (props) => {
    const { project_id } = props.match.params;
    getProjectDetail(project_id).then(project => this.setState({project}));
  };

  componentDidUpdate = () => {
    const { year, month } = this.props.match.params;
    const { project } = this.state;
    document.title = `${year}年${month}月出勤情報 | ${project.name || ''} | 案件一覧`;
  };

  onAttendanceModified = (data, handleClose) => {
    const { project_id } = this.props.match.params;
    return onSaveProjectAttendance(project_id, data.attendance_id, data).then(() => {
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
      this.setState({reload: !this.state.reload});
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  render() {
    const { project_id, year, month } = this.props.match.params;
    const { project, reload } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: `/project/projects`, name: '案件一覧' },
            { link: `/project/projects/${project_id}/`, name: project.name },
            { name: `${year}年${month}月出勤情報` },
          ]}
        />
        <Card>
          <CardHeader color="info">{`${project.name}：${year}年${month}月出勤情報`}</CardHeader>
          <CardBody>
            <DataSource
              reload={reload}
              endpoint={common.formatStr(config.api.project.project_attendance_by_month, project_id, year, month)}
              render={(initData) => (
                <EnhancedTable
                  title={`${year}年${month}月出勤情報`}
                  tableHead={project_attendance_by_month_schema}
                  tableData={initData}
                  rowsPerPage={config.rowsPerPage}
                  selectable="single"
                  editProps={{
                    "schema": project_attendance_edit_schema,
                    "layout": project_attendance_edit_layout,
                    "handleOk": this.onAttendanceModified,
                    "visible": common.hasPerm('attendance.change_attendance'),
                  }}
                  pushpinTop={common.getHeaderHeight()}
                />
              )}
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(ProjectAttendance, 'attendance.view_attendance');
