import React from 'react';
import { Link } from 'react-router-dom';
import {
  Tooltip, Button, MenuItem, Paper,
} from '@material-ui/core';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import {
  getMemberInfo,
  getContract,
  getOrganizationChoices,
  handleAddObject,
  handleEditObject,
  getContractCommentsForAdd,
  handleDeleteObject,
} from '../utils/api';
import GridContainer from '../../components/Grid/GridContainer';
import GridItem from '../../components/Grid/GridItem';
import DataSource from '../../components/DataSource';
import { TableDetail, EnhancedTable } from '../../datatable';
import { 
  contract_form_basic_schema, 
  contract_form_basic_layout, 
  contract_item_list_schema, 
  contract_allowance_list_schema,
  contract_allowance_schema,
  contract_item_schema,
  contract_detail_schema
} from './schema';
import { ajax, common } from '../../utils/common';
import { config, constant } from '../../utils';
import { withPermissions, HasPerm, Toast } from '../../components';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class ContractDetail extends React.Component {
  basic_info_edit_schema = common.clone(contract_form_basic_schema);
  add_contract_item_schema = common.clone(contract_item_schema);

  constructor(props) {
    super(props);

    this.state = {
      member: {},
      contract: {},
      organizations: [],
      allowance_reload: false,
      comment_reload: false,
      comments_for_add: [],
    };
    this.initialize(props.match.params);
    this.detail_api = common.formatStr(
      config.api.contract.detail,
      props.match.params.contract_id,
    );
  }

  initialize = (params) => {
    const { member_id, contract_id } = params;
    getMemberInfo(member_id).then(member => {
      this.setState({member});
    });
    getContract(contract_id).then(contract => {
      this.setState({contract});
    });
    getOrganizationChoices().then(organizations => {
      this.setState({organizations});
    });
    this.initCommentsForAdd(contract_id);
  };

  initCommentsForAdd = (contract_id) => {
    getContractCommentsForAdd(contract_id).then(data => {
      const comments = [];
      data.map(row => (
        comments.push({value: row.name, display_name: row.name})
      ));
      common.getFromJsonList(this.add_contract_item_schema, 'name', 'name').type = 'choice';
      common.getFromJsonList(this.add_contract_item_schema, 'name', 'name').choices = comments;
      this.setState({comments_for_add: data});
    });
  };

  componentDidUpdate = () => {
    const { member } = this.state;
    document.title = `契約情報 | ${member.full_name} | 契約管理`;
  };

  onEditContract = (data, handleClose) => {
    return ajax.fetchPut(this.detail_api, data).then(contract => {
      this.setState({contract});
      if (handleClose) {
        handleClose();
      }
      Toast.success(constant.SUCCESS.SAVED);
    }).catch(errors => {
      Toast.error(constant.ERROR.FORM_CHECK_ERROR);
      return Promise.reject(errors);
    });
  };

  onDeleteContract = () => {
    const { params } = this.props.match;
    ajax.fetchDelete(this.detail_api).then(() => {
      Toast.success(constant.SUCCESS.DELETED);
      common.redirect(`/contract/members/${params.member_id}`);
    }).catch((errors) => {
      const msg = errors.detail || constant.ERROR.DELETED_FAILURE;
      Toast.error(msg);
    });
  };

  onContractCommentChange = (name, data) => {
    const { comments_for_add } = this.state;
    if (name === 'name') {
      const item = common.getFromJsonList(comments_for_add, 'name', data[name]);
      if (item) {
        data.code = item.code;
        data.content = item.content;
      }
      return data;
    }
  };

  render() {
    const { member_id, contract_id } = this.props.match.params;
    const { member, contract, organizations, allowance_reload, comment_reload } = this.state;
    // 「契約形態」、「契約開始日」、「契約終了日」は変更できないように
    this.basic_info_edit_schema[4].choices = organizations;
    this.basic_info_edit_schema[1].read_only = true;
    this.basic_info_edit_schema[2].read_only = true;
    this.basic_info_edit_schema[3].read_only = true;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/contract', name: '契約管理'},
            {link: '/contract/members', name: '社員一覧'},
            {link: `/contract/members/${member_id}`, name: member.full_name },
            {name: contract.contract_no },
          ]}
        />
        <div style={{marginTop: 16}}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={12} lg={12}>
              <TableDetail
                title='契約詳細'
                data={contract}
                schema={contract_detail_schema}
                editProps={{
                  title: '契約情報変更',
                  schema: this.basic_info_edit_schema,
                  layout: contract_form_basic_layout,
                  handleEdit: this.onEditContract,
                  visible: common.hasPerm('contract.change_contract'),
                }}
                deleteProps={{
                  handleDelete: this.onDeleteContract,
                  visible: common.hasPerm('contract.delete_contract'),
                }}
                actions={[
                  <HasPerm key={1} permission='contract.add_contract'>
                    <Tooltip title="契約条件などは直接変更できない、変更したい場合はコピーして再作成しましょう">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => common.redirect(
                          `/contract/members/${member_id}/contracts/add`,
                          `?contract_id=${contract_id}`,
                        )}
                      >
                        コピーして再作成
                      </Button>
                    </Tooltip>
                  </HasPerm>
                ]}
                cardMenuItems={[
                    <MenuItem
                      key='1'
                      component={Link}
                      to={`/contract/members/${member_id}/contracts/${contract_id}/employment`}
                    >
                      雇用契約書
                    </MenuItem>,
                    <MenuItem
                      key='2'
                      component={Link}
                      to={`/contract/members/${member_id}/contracts/${contract_id}/incumbency`}
                    >
                      在職証明書
                    </MenuItem>,
                    <MenuItem
                      key='3'
                      component={Link}
                      to={`/contract/members/${member_id}/contracts/${contract_id}/income`}
                    >
                      所得証明書
                    </MenuItem>,
                ]}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={12} lg={12}>
              <Paper style={paperSpacing}>
                <DataSource
                  reload={allowance_reload}
                  endpoint={common.formatStr(config.api.contract.contract_allowances, contract_id)}
                  render={(initData) => (
                    <EnhancedTable
                      title="基本給・諸手当"
                      showTitle={true}
                      tableHead={contract_allowance_list_schema}
                      tableData={initData.results}
                      rowsPerPage={config.rowsPerPage}
                      pushpinTop={common.getHeaderHeight()}
                      selectable='single'
                      editProps={{
                        "schema": contract_allowance_schema,
                        "handleOk": handleEditObject(
                          () => this.setState({allowance_reload: !allowance_reload}),
                          config.api.contract.contract_allowances_detail,
                          contract_id
                        ),
                        "visible": common.hasPerm('contract.change_contract'),
                      }}
                    />
                  )}
                />
              </Paper>
            </GridItem>
            <GridItem xs={12} sm={12} md={12} lg={12}>
              <Paper style={paperSpacing}>
                <DataSource
                  reload={comment_reload}
                  endpoint={common.formatStr(config.api.contract.contract_items, contract_id)}
                  render={(initData) => (
                    <EnhancedTable
                      title="契約条件"
                      showTitle={true}
                      tableHead={contract_item_list_schema}
                      tableData={initData.results}
                      rowsPerPage={config.rowsPerPage}
                      pushpinTop={common.getHeaderHeight()}
                      selectable='single'
                      addProps={{
                        "schema": this.add_contract_item_schema,
                        "data": { contract: contract_id },
                        "handleOk": handleAddObject(
                          () => {
                            this.setState({comment_reload: !comment_reload});
                            this.initCommentsForAdd(contract_id);
                          },
                          config.api.contract.contract_comment_list,
                          contract_id
                        ),
                        "onChanges": [this.onContractCommentChange],
                        "visible": common.hasPerm('contract.add_contract'),
                      }}
                      editProps={{
                        "schema": contract_item_schema,
                        "handleOk": handleEditObject(
                          () => this.setState({comment_reload: !comment_reload}),
                          config.api.contract.contract_comment_detail,
                          contract_id
                        ),
                        "visible": common.hasPerm('contract.change_contract'),
                      }}
                      deleteProps={{
                        handleDelete: handleDeleteObject(
                          () => {
                            this.setState({comment_reload: !comment_reload});
                            this.initCommentsForAdd(contract_id);
                          },
                          config.api.contract.contract_comment_detail,
                          contract_id
                        ),
                      }}
                    />
                  )}
                />
              </Paper>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withPermissions(ContractDetail, 'contract.view_contract');
