import React from 'react';
import { Paper } from '@material-ui/core';
import {
  DataSource,
  TopBreadcrumbs,
} from '../../components';
import { EnhancedTable } from '../../datatable';
import { config, common } from '../../utils';
import { member_turnover_by_month_list_schema } from './schema';
import { paperSpacing } from '../../assets/jss/material-dashboard-react';

class MembersTurnoverByFiscalYear extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      reload: false,
      year: props.match.params.year,
    };
　}

  componentDidMount = () => {
    const { year } = this.state;
    document.title = `${year}年度の要員売上`;
  };

  render() {
    const { year, reload } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            { link: '/turnover/members/monthly', name: '要員売上' },
            { name: `${year}年度` },
          ]}
        />
        <Paper style={paperSpacing}>
          <DataSource
            reload={reload}
            endpoint={ common.formatStr(config.api.turnover.members_by_fiscal_year, year) }
            render={ (initData) => (
              <EnhancedTable
                title={`${year}年度の要員売上`}
                showTitle={true}
                tableHead={member_turnover_by_month_list_schema}
                tableData={initData}
                showAggregate={true}
                pushpinTop={common.getHeaderHeight()}
                allowCsv={true}
                tableStyles={{ minWidth: 750 }}
              />
            )}
          />
        </Paper>
      </div>
    );
  }
}

export default MembersTurnoverByFiscalYear;
