import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import { EnhancedTable } from '../../datatable/index';
import {
  Card,
  CardHeader,
  CardBody,
  withPermissions,
  DataSource,
} from "../../components";
import { individual_member_list_schema } from './schema';
import { config } from '../../utils/config';
import { common } from '../../utils/common';
import { getSalespersonChoice } from '../utils';

class IndividualMembers extends React.Component {
  title = '個人事業主一覧';
  individual_member_list_schema = common.clone(individual_member_list_schema);

  constructor(props) {
    super(props);

    this.state = {
      salesperson_choice: [],
    };
    
    this.initialize();
  }

  initialize = () => {
    getSalespersonChoice().then(salesperson_choice => {
      this.setState({salesperson_choice});
    });
  };

  componentDidMount = () => {
    document.title = this.title;
  }

  render () {
    const filters = {};
    const { salesperson_choice } = this.state;
    // 営業員選択肢の設定
    common.getFromJsonList(this.individual_member_list_schema, 'name', 'salesperson_id')['choices'] = salesperson_choice;

    return (
      <div>
        <Card>
          <CardHeader color="info">
            {this.title}
          </CardHeader>
          <CardBody>
            <DataSource
              endpoint={ config.api.partner.individual_members }
              render={ (initData) => (
                <EnhancedTable
                  title={this.title}
                  storageKey='individual.members'
                  tableHead={this.individual_member_list_schema}
                  tableData={initData.results}
                  rowsPerPage={config.rowsPerPage}
                  tableActions={[
                    {
                      'tooltip': '個人事業主を追加する',
                      'icon': <AddIcon color="secondary"/>,
                      'handleClick': () => (common.redirect('/partner/individual/add')),
                      'visible': common.hasPerm('partner.add_partnermember'),
                    }
                  ]}
                  filters={filters}
                  allowCsv={true}
                  urlReflect={false}
                  pushpinTop={common.getHeaderHeight()}
                  tableStyles={{ minWidth: 900 }}
                />
              ) } 
            />
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default withPermissions(IndividualMembers, 'partner.view_partnermember');
