import { makeStyles } from '@material-ui/core/styles';

export const useStyles = makeStyles({
  timeLineCtnr: {
    textAlign: 'left',
    position: 'relative',
    '& .time-line-before': {
      position: 'absolute',
      top: 0,
      bottom: '-10px',
      width: '2px',
      background: '#3498db',
      left: '32px',
      borderRadius: '2px',
    },
    '& .time-line': {
      margin: '0 0 0px 0',
      padding: 0,
      listStyle: 'none',
      '& > .time-label': {
        '& > span': {
          fontWeight: 600,
          padding: '2px 10px',
          display: 'inline-block',
          backgroundImage: 'linear-gradient(180deg, #1976d2 0%, #0d47a1 100%)',
          color: '#fff',
          borderRadius: '2px',
          fontSize: '14px',
        },
      },
      '& > li': {
        position: 'relative',
        marginBottom: '10px',
        marginRight: 0,
        zIndex: 1,
        '& > .time-line-item': {
          '-webkit-box-shadow': '0 1px 1px rgba(0, 0, 0, 0.1)',
          boxShadow: '0 1px 1px rgba(0, 0, 0, 0.1)',
          borderRadius: '3px',
          background: '#fff',
          color: '#444',
          marginRight: '15px',
          position: 'relative',
          marginLeft: '50px',
          '& > .time-line-header': {
            fontSize: '14px',
            padding: '6px 10px',
          },
          // '& .time': {
          //   fontSize: '14px',
          //   padding: '6px 10px',
          // },
          '& .time': {
            fontSize: '12px',
            padding: '6px 10px',
            color: '#999',
            float: 'right',
          },
          '& .time-line-header': {
            margin: 0,
            color: '#555',
            borderBottom: '1px solid #f4f4f4',
            lineHeight: '1.1',
          }
        },
        '& > .fa': {
          position: 'absolute',
          borderRadius: '50%',
          width: '10px',
          height: '10px',
          color: '#666',
          background: '#ecf0f1',
          left: '28px',
          top: '9px',
          border: '2px solid #3498db',
          display: 'inline-block',
          boxSizing: 'border-box',
        }
      }
    }
  },
});