import React from 'react';
import { Card, CardHeader, CardContent, IconButton } from '@material-ui/core';
import PrintIcon from '@material-ui/icons/Print';
import TopBreadcrumbs from '../../components/TopBreadcrumbs';
import { getMemberInfo, getContract, getReportContractEmployment } from '../utils/api';
import { common } from '../../utils/common';
import { withPermissions } from '../../components';

class ContractEmployment extends React.Component {
  title = '雇用契約書';
  reportId = 'printContractEmployment';

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      member: {},
      contract: {},
      employment_html: null,
    };
    this.initialize(props.match.params);
  }

  initialize = (params) => {
    getMemberInfo(params.member_id).then(member => {
      this.setState({member});
    });
    getContract(params.contract_id).then(contract => {
      this.setState({contract});
    });
    getReportContractEmployment(params.contract_id).then(json => {
      this.setState({employment_html: json.html});
    })
  };

  componentDidUpdate = () => {
    const { member } = this.state;
    document.title = `${this.title} | ${member.full_name} | 契約管理`;
  };

  render() {
    const { member_id, contract_id } = this.props.match.params;
    const { member, contract, employment_html } = this.state;

    return (
      <div>
        <TopBreadcrumbs
          breadcrumbs={[
            {link: '/contract', name: '契約管理'},
            {link: '/contract/members', name: '社員一覧'},
            {link: `/contract/members/${member_id}`, name: member.full_name },
            {link: `/contract/members/${member_id}/contracts/${contract_id}`, name: contract.contract_no },
            {name: this.title },
          ]}
        />
        <div style={{marginTop: 16}}>
          <Card>
            <CardHeader
              title={this.title}
              action={
                <IconButton
                  aria-haspopup="true"
                  onClick={() => {common.printIframeContent(this.reportId)}}
                >
                  <PrintIcon />
                </IconButton>
              }
            />
            <CardContent>
              <iframe
                title={this.title}
                id={this.reportId}
                srcDoc={employment_html}
                frameBorder='0'
                width='100%'
                height='1100px'
              >
              </iframe>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }
}

export default withPermissions(ContractEmployment, 'contract.view_contract');
