import React from 'react';
import PropTypes from "prop-types";
import {
  Tooltip,
  Button,
} from '@material-ui/core';
import { FormDialog } from '../../datatable';
import { partnerMemberRetire } from '../utils/api';
import { Toast } from '../../components';
import { constant } from '../../utils';

class MemberRetire extends React.Component {

  handleOpenDialog = () => {
    if (this._openDialog) {
      this._openDialog();
    }
  };

  handleRetire = (data) => {
    const { member_id, callback } = this.props;
    return partnerMemberRetire(member_id, data).then(() => {
      Toast.success(constant.SUCCESS.RETIRED);
      if(this._closeDialog) {
        this._closeDialog();
      }
      if (callback) {
        callback();
      }
    }).catch(errors => {
      Toast.error(errors.detail);
    });
  };

  render() {
    const { full_name } = this.props;

    return (
      <div>
        <Tooltip
          title='協力社員に対して、これから退職とみなし使わなくなる。'
          placement='bottom'
          enterDelay={300}
        >
          <Button
            variant="contained"
            color="secondary"
            onClick={this.handleOpenDialog}
          >
            退場
          </Button>
        </Tooltip>
        <FormDialog
          title={`${full_name}を退職`}
          description={'退職に設定したら、下記の処理を行います。\n１、契約終了日を退職日に設定。\n２、部署の所属終了日を退職日に設定。\n３、営業対象から外します。'}
          schema={[{
            "name": "retire_date",
            "type": "date",
            "label": "退職日",
            "required": true,
          }]}
          handleOk={this.handleRetire}
          innerRef={dlg => {
            this._openDialog = dlg && dlg.handleOpen;
            this._closeDialog = dlg && dlg.handleClose;
          }}
        />
      </div>
    );
  }
}

MemberRetire.propTypes = {
  full_name: PropTypes.string.isRequired,
  member_id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
  callback: PropTypes.func,
};

export default MemberRetire;
